import $ from 'jquery';
import consts from '../../consts/consts';
import PopupInterface from './PopupInterface';

const UI_NAME = consts.UI_NAME;
/**
 * 풍선팝업 클래스입니다.
 * PopupInterface 클래스를 상속받습니다.
 * @extends {PopupInterface}
 */
class BalloonPopup extends PopupInterface{

    /**
     * @param {String} editorId 에디터를 식별할 수 있는 아이디
     * @param {String} uiName Popup 이름
     * @param {Array} uiComponents 표시할 컴포넌트 목록
     */
    constructor(editorId, uiName, uiComponents) {
        super(editorId);

        this.editorId = editorId;
        this.uiName = uiName;

        this.$element = this.__createDom__(uiComponents);
    }

    /**
     * DOM을 생성합니다.
     * @param {Array} uiComponents 풍선 팝업에 추가할 버튼의 instance 목록입니다
     * @returns {jQuery}
     */
    __createDom__(uiComponents) {
        const $popupContainer = $('#' + this.editorId).find('.se-popup-container');
        const $element = $(`<div class="se-popup se-balloon" name="${this.uiName}"></div>`);
        
        const $arrow = $('<div class="se-popup-arrow"></div>');
        const $body = $('<div class="se-popup-body"></div>');

        let $elRow = $('<div class="se-toolbar-row"></div>');
        $body.append($elRow);

        uiComponents.forEach(uiComponent => {
            const uiComponentName = uiComponent.name;

            if(uiComponentName === UI_NAME.HORIZONTAL_SEPARATOR) {
                $elRow = $('<div class="se-toolbar-row"></div>');
                $body.append($elRow);
            } else {
                $elRow.append(uiComponent.getElement());
            }
        });

        $element.append($arrow);
        $element.append($body);

        $popupContainer.append($element);

        return $element;
    }

    /**
     * Popup Element를 보여줍니다.
     * @param {HTMLElement} elTarget
     */
    show(elTarget) {
        const $editor = $('#' + this.editorId);
        const $popupContainer = $editor.find('.se-popup-container');
        const $main = $editor.find('.se-main');
        const $toolbar = $editor.find('.se-toolbar');
        const $arrow = this.$element.find('.se-popup-arrow');

        const overflowX = elTarget.offsetLeft + elTarget.offsetWidth + this.$element.get(0).offsetWidth - $main.get(0).offsetWidth;
        const popupOffsetTop = elTarget.offsetTop + elTarget.offsetHeight + $toolbar.get(0).offsetHeight - $main.get(0).scrollTop - $popupContainer.get(0).offsetTop;
        const popupOffsetLeft = (overflowX > 0 ? elTarget.offsetLeft + elTarget.offsetWidth - overflowX : elTarget.offsetLeft + elTarget.offsetWidth) - 20 - $popupContainer.get(0).offsetLeft;
        const arrowOffsetLeft = elTarget.offsetLeft + elTarget.offsetWidth - ($arrow.get(0).offsetWidth / 2) - popupOffsetLeft - $popupContainer.get(0).offsetLeft;

        $arrow.css('left', arrowOffsetLeft);

        super.setPosition({top: popupOffsetTop, left: popupOffsetLeft});
        this.$element.addClass('show');

        $main.css('overflow-y', 'hidden');
    }

    /**
     * Popup Element를 숨깁니다.
     */
    hide() {
        const $main = $('#' + this.editorId).find('.se-main');
        $main.css('overflow-y', 'auto');

        super.hide();
    }
}

export default BalloonPopup;


