import ACTION from '../consts/actionMap';
import ControllerInterface from './ControllerInterface';

/**
 * 클립보드 이벤트를 수행하는 class 입니다.<br>
 * 복사/붙여넣기/잘라내기 등을 수행합니다.
 */
class ClipboardController extends ControllerInterface {
    constructor(editor) {
        super();
        this.SEClipboard = editor.getSEClipboard();
        this.selectionManager = editor.getSelectionManager();
        this.selectionRenderer = editor.getSelectionRenderer();
        this.selection = this.selectionManager.selection;
        this.modelHandler = editor.getModelHandler();
        this.renderer = editor.getRenderer();
        this.controller = editor.getController();
        this.SEModelManager = editor.getSEModelManager();
    }

    preExecute() {
    }

    postExecute() {
        this.renderer.renderAll();
        this.selectionRenderer.renderSelection(this.selection);
    }
}

/**
 * Controller Action Map에 등록될 Object 액션입니다.
 */
const clipboardController = {
    [ACTION.COPY]: class extends ClipboardController {
        execute(event) {
            this.SEClipboard.copy(event.originalEvent);
        }
    },

    [ACTION.CUT]: class extends ClipboardController {
        execute() {
        }
    },

    [ACTION.PASTE]: class extends ClipboardController {
        preExecute() {
            if(!this.selection.isCollapsed()) {
                this.modelHandler.deleteContent(this.selection);
            }
        }
        execute(event) {
            this.SEClipboard.paste(event.originalEvent);
        }
    }
};

export default clipboardController;



