import $ from 'jquery';
import util from '../util/util';
import UIInterface from './UIInterface';
import Button from './Button';
import icon from './icon';

/**
 * 드롭다운 아이템입니다.
 * @extends UIInterface
 */
class DropdownItem extends UIInterface {
    /**
     * 드롭다운 아이템 옵션으로 사용할 수 있는 속성 :<br/>
     * - label: (String) 드롭다운 아이템에 보여질 레이블입니다.
     * - cssText: (String) 레이블을 나타낼 css 스타일 텍스트입니다.
     * - onClickFunc: (Function) 클릭했을 때 수행할 동작입니다.
     * @param {Dropdown} parent 부모 드롭다운 인스턴스
     * @param {Object} dropdownItemOption 드롭다운아이템 옵션
     */
    constructor(parent, dropdownItemOption) {
        super(dropdownItemOption);

        this.parent = parent; // 부모 Dropdown 
        this.label = dropdownItemOption.label || ''; // 버튼에 나타날 레이블

        if (typeof this.label === 'string') {
            this.label = this.label.replace(/\n/gi, '<br>').replace(/\t/gi, '&nbsp;&nbsp;&nbsp;&nbsp;');
        }

        this.cssText = dropdownItemOption.cssText; // 버튼 레이블 스타일 텍스트
        this.onClickFunc = dropdownItemOption.onClickFunc; // 클릭시 수행할 기능
        this.useIcon = dropdownItemOption.useIcon;
        this.name = dropdownItemOption.name;
        this.$element = this.__createElement__();

        this.isDisable = false;
    }

    /**
     * 드롭다운 메뉴의 jQuery Element를 생성합니다.
     * @private
     * @returns {jQuery} 생성된 드롭다운 메뉴의 jQuery Element
     */
    __createElement__() {
        const value = this.useIcon ? `<img src="${icon[this.name]}">` : this.label;
        const $dropdownItem = $(`<li><a href="#">${value}</a></li>`);

        // show style setting
        if (!!this.cssText) {
            $dropdownItem.get(0).style.cssText = this.cssText;
        }

        // click event setting
        if (!!this.onClickFunc) {
            $dropdownItem.on('click', (event) => {
                if (this.isDisable) {
                    this.parent.$element.focus();
                } else {
                    const $menu = this.parent.$element.find('.se-dropdown-menu');
                    $menu.removeClass('show');

                    this.onClickFunc(event);
                }

                return false;
            });
        }

        return $dropdownItem;
    }

    /**
     * DropDownItem의 jQuery Element를 가져옵니다.
     * @returns {jQuery} UI의 jQuery Element
     */
    getElement() {
        return this.$element;
    }

    /**
     * 엘리먼트를 활성화합니다.
     */
    enable() {
        this.isDisable = false;
        this.$element.removeClass('disable');
    }

    /**
     * 엘리먼트를 비활성화합니다.
     */
    disable() {
        this.isDisable = true;
        this.$element.addClass('disable');
    }
}

/**
 * 드롭다운 입니다.
 * @extends Button
 * @example
 * var dropdownItems = [
 *     {label: 'Apple', onClickFunc: function() {}},
 *     {label: 'Banana', onClickFunc: function() {}},
 *     {label: 'Lemon', onClickFunc: function() {}}
 * ];
 * var dropdownOption = {
 *     label: 'Fruits',
 *     tooltipMsg: 'Select Fruits',
 *     dropdownItems: dropdownItems
 * };
 * var dropdown = new Dropdown(dropdownOption);
 */
class Dropdown extends UIInterface {
    /**
     * 드롭다운 옵션으로 사용할 수 있는 속성 :<br/>
     * - isSingle: (Boolean) 싱글버튼으로 할 것인지, 스플릿버튼(버튼과 드롭다운이 나뉜 형태)으로 할 것인지 여부입니다. (default: true)
     * - label: (String) 버튼에 보여질 레이블입니다.
     * - onClickFunc: (Function) 클릭했을 때 수행할 동작입니다.
     * - dropdownItems: Array[Object] 드롭다운을 클릭했을 때 보여지는 아이템입니다.
     * @param {String} dropdownName 드롭다운 이름
     * @param {Object} dropdownOption 드롭다운 옵션
     */
    constructor(dropdownName, dropdownOption) {
        super();
        this.name = dropdownName;
        this.label = dropdownOption.label || ''; // 버튼에 보여질 레이블
        this.onClickFunc = dropdownOption.onClickFunc; // 클릭했을 때 수행할 동작

        this.isSingle = util.get(dropdownOption, ['isSingle'], true);
        this.useIcon = util.get(dropdownOption, ['useIcon'], true);
        this.dropdownItems = []; // 드롭다운을 클릭시 보여지는 아이템들
        this.__setDropdownItems__(dropdownOption.dropdownItems);

        this.$element = this.__createElement__();
    }

    /**
     * 드롭다운의 드롭다운 아이템을 설정합니다.
     * @private
     * @param {Array} dropdownItems 세팅할 드롭다운 아이템 목록
     * @example
     * dropdownItems 형식 :
     * [
     *     {
     *         label: '레이블',
     *         style: 'color: red;',
     *         onClickFunc: function() {}
     *     },
     *     ....
     * ];
     */
    __setDropdownItems__(dropdownItems) {
        dropdownItems.forEach((item) => {
            this.dropdownItems.push(new DropdownItem(this, item));
        });
    }

    /**
     * 엘리먼트에 클릭 이벤트를 등록합니다.
     * @private
     * @param {jQuery} $el
     * @param {function} clickFunc
     */
    __attachClickEvent__($el, clickFunc) {
        if (!clickFunc) {
            clickFunc = () => {
                if (this.$element) {
                    this.$element.focus();

                    const $dropdownMenu = this.$element.find(`.se-dropdown-menu`);

                    if ($dropdownMenu.hasClass('show')) {
                        $dropdownMenu.removeClass('show');
                    } else {
                        $dropdownMenu.addClass('show');
                        $dropdownMenu.trigger('openDropdown', [this.name]);
                    }
                }
            };
        }

        $el.click(clickFunc);
    }

    /**
     * 드롭다운의 jQuery Element를 생성합니다.
     * @private
     * @returns {jQuery} 생성된 드롭다운의 jQuery Element
     */
    __createElement__() {
        const $btnGroup = $(`<div class="se-btn-group" tabindex="-1" name="${this.name}">`);
        const $button = this.__createButtonElement__();
        const $menu = this.__createMenuElement__();

        $btnGroup.append($button).append($menu);

        $btnGroup.on('blur', () => {
            if (!$menu.hasClass('mouseover')){
                const $dropdownMenu = $btnGroup.find(`.se-dropdown-menu`);
                $dropdownMenu.removeClass('show');
            }
        });

        return $btnGroup;
    }

    /**
     * 드롭다운 버튼의 jQuery Element를 생성합니다.
     * @private
     * @returns {jQuery}
     */
    __createButtonElement__() {
        let $dropdownBtn;

        if (this.useIcon && this.isSingle) {
            $dropdownBtn = this.__createSingleIconElement__();
        } else if (this.useIcon && !this.isSingle) {
            $dropdownBtn = this.__createDualIconElement__();
        } else if (!this.useIcon && this.isSingle) {
            $dropdownBtn = this.__createSingleTextElement__();
        } else {
            $dropdownBtn = this.__createDualTextElement__();
        }

        return $dropdownBtn;
    }

    /**
     * 아이콘이 포함된 싱글 버튼 엘리먼트를 반환합니다.
     * @private
     * @returns {jQuery}
     */
    __createSingleIconElement__() {
        const $dropdownBtn = $(`<div class="se-dropdown-btn-wrapper se-single-btn" value="icon"></div>`);
        const $iconBtn = $(`<div class="se-dropdown-btn" value="main"><img src="${icon[this.name]}" class="se-menu-btn"></div>`);
        const $arrowBtn = $('<div class="se-dropdown-btn" value="sub"><span class="se-arrow"></span></div>');

        this.__attachClickEvent__($dropdownBtn, null);

        $dropdownBtn.append($iconBtn);
        $dropdownBtn.append($arrowBtn);

        return $dropdownBtn;
    }

    /**
     * 텍스트가 포함된 싱글 버튼 엘리먼트를 반환합니다.
     * @private
     * @returns {jQuery}
     */
    __createSingleTextElement__() {
        const $dropdownBtn = $(`<div class="se-dropdown-btn-wrapper se-single-btn" value="text"></div>`);
        const $textBtn = $(`<div class="se-dropdown-btn" value="main"><span class="se-label">${this.label}</span></div>`);
        const $arrowBtn = $('<div class="se-dropdown-btn" value="sub"><span class="se-arrow"></span></div>');

        this.__attachClickEvent__($dropdownBtn, null);

        $dropdownBtn.append($textBtn);
        $dropdownBtn.append($arrowBtn);

        return $dropdownBtn;
    }

    /**
     * 아이콘이 포함된 듀얼 버튼 엘리먼트를 반환합니다.
     * @private
     * @returns {jQuery}
     */
    __createDualIconElement__() {
        const $dropdownBtn = $(`<div class="se-dropdown-btn-wrapper se-dual-btn" value="icon"></div>`);
        const $iconBtn = $(`<div class="se-dropdown-btn" value="main"><img src="${icon[this.name]}" class="se-menu-btn"></div>`);
        const $arrowBtn = $('<div class="se-dropdown-btn" value="sub"><span class="se-arrow"></span></div>');

        if (!!this.onClickFunc) {
            this.__attachClickEvent__($iconBtn, this.onClickFunc);
        }

        this.__attachClickEvent__($arrowBtn, null);

        $dropdownBtn.append($iconBtn);
        $dropdownBtn.append($arrowBtn);

        return $dropdownBtn;
    }

    /**
     * 텍스트가 포함된 듀얼 버튼 엘리먼트를 반환합니다.
     * @private
     * @returns {jQuery}
     */
    __createDualTextElement__() {
        const $dropdownBtn = $(`<div class="se-dropdown-btn-wrapper se-dual-btn" value="text"></div>`);
        const $textBtn = $(`<div class="se-dropdown-btn" value="main"><span class="se-label">${this.label}</span></div>`);
        const $arrowBtn = $('<div class="se-dropdown-btn" value="sub"><span class="se-arrow"></span></div>');

        if (!!this.onClickFunc) {
            this.__attachClickEvent__($textBtn, this.onClickFunc);
        }

        this.__attachClickEvent__($arrowBtn, null);

        $dropdownBtn.append($textBtn);
        $dropdownBtn.append($arrowBtn);

        return $dropdownBtn;
    }

    /**
     * 드롭다운 메뉴의 jQuery Element를 생성합니다.
     * @private
     * @returns {jQuery}
     */
    __createMenuElement__() {
        const $dropdownMenu = $(`<ul class="se-dropdown-menu">`);

        this.dropdownItems.forEach((item) => {
            $dropdownMenu.append(item.getElement());
        });

        $dropdownMenu.on('mouseenter', () => {
            $dropdownMenu.addClass('mouseover');
        });

        $dropdownMenu.on('mouseleave', () => {
            $dropdownMenu.removeClass('mouseover');
        });

        return $dropdownMenu;
    }

    /**
     * 드롭다운의 jQuery Element를 가져옵니다.
     * @returns {jQuery} UI의 jQuery Element
     */
    getElement() {
        return this.$element;
    }
}

export default Dropdown;


