import $ from 'jquery';
import UIInterface from './UIInterface';
import consts from '../consts/consts';

const UI_NAME = consts.UI_NAME;
/**
 * 세퍼레이터입니다.
 * @extends UIInterface
 */
class Separator extends UIInterface {
    /**
     * @param {String} separatorName
     */
    constructor(separatorName) {
        super();

        this.name = separatorName;
        this.isHorizontal = separatorName === UI_NAME.HORIZONTAL_SEPARATOR;
        this.$element = this.__createElement__();
    }

    /**
     * 세퍼레이터의 jQuery Element를 생성합니다.
     * @private
     * @returns {jQueryElement} 생성된 세퍼레이터의 jQuery Element
     */
    __createElement__() {
        const className = this.isHorizontal ? 'se-separator-horz' : 'se-separator-vert';
        const separatorHTML = `<div class="${className}">`;

        return $(separatorHTML);
    }

    /**
     * Separator의 jQuery Element를 가져옵니다.
     * @returns {jQueryElement} UI의 jQuery Element
     */
    getElement() {
        return this.$element;
    }
}

export default Separator;


