import $ from 'jquery';
import consts from '../consts/consts';
import TableHandler from '../handler/TableHandler';
import PictureHandler from '../handler/PictureHandler';

const UI_NAME = consts.UI_NAME;

/**
 * Selection을 렌더링하는 Class입니다.<br/>
 * Caret 및 selection range의 위치를 변경하고, 보여주는 API를 제공합니다.
 */
class SelectionRenderer {
    constructor(editorId, selectionManager, SEModelManager, ime, uiManager) {
        this.editorId = editorId;
        this.$elMain = $(`#${this.editorId} .se-main`);
        this.$elCaret = $(`#${this.editorId} .se-caret`);
        this.$elSelections = $(`#${this.editorId} .se-selections`);
        this.visibility = false;
        this.intervalId = 0;
        this.pictureHandler = new PictureHandler(editorId);
        this.tableHandler = new TableHandler(editorId, SEModelManager);
        this.selectionManager = selectionManager;
        this.SEModelManager = SEModelManager;
        this.ime = ime;
        this.uiManager = uiManager;
        this.init();
        this.stop();
    }

    /**
     * Caret을 초기화 합니다.
     */
    init() {
        const $elContents = $(`#${this.editorId} .se-contents`);
        if (!this.$elCaret.length) {
            this.$elCaret = $('<div class="se-caret"></div>');
            $elContents.after(this.$elCaret);
        }

        if (!this.$elSelections.length) {
            this.$elSelections = $('<div class="se-selections"></div>');
            $elContents.after(this.$elSelections);
        }
    }

    /**
     * Caret에 깜빡이게 렌더링 합니다.
     */
    blink() {
        this.stop();
        this.show();
        this.intervalId = setInterval(() => {
            if (this.visibility) {
                this.hide();
            } else {
                this.show();
            }
            this.visibility = !this.visibility;
        }, 500);
    }

    /**
     * Caret의 숨기고 visibility 값을 true로 변경합니다.
     */
    hide() {
        this.$elCaret.removeClass('show');
        this.visibility = true;
    }

    /**
     * Caret의 보이게하고 visibility 값을 false로 변경합니다.
     */
    show() {
        this.$elCaret.addClass('show');
        this.visibility = false;
    }

    /**
     * interval로 실행중인 Caret 깜빡이는 동작을 멈춥니다. <br/>
     * visibility를 false, $elCaret HTML Element에서 show class 제거, intervalId 0으로 초기화 합니다.
     */
    stop() {
        clearInterval(this.intervalId);

        this.visibility = false;
        this.$elCaret.removeClass('show');
        this.intervalId = 0;
    }

    /**
     * Selection을 비웁니다.
     */
    empty() {
        this.pictureHandler.hide();
        this.$elSelections.empty();
        this.stop();
    }

    /**
     * Position 정보에 맞게 Caret 위치(left, top)와 높이(height)를 잡아줍니다.
     * @param {Position} position
     */
    movePosition(position) {
        let elMain = this.$elMain.get(0);
        let seMainRect = elMain.getBoundingClientRect();
        let rect = position.getRect(this.editorId);
        let left = rect.left - seMainRect.left;
        let top = rect.top - seMainRect.top + elMain.scrollTop;
        let height = rect.bottom - rect.top;
        // Caret 위치 보정
        this.$elCaret.css({
            left: left,
            top: top,
            height: height
        });

        this.ime.moveIme(left, top + height);
    }

    /**
     * 셀랙션 상태에 따라서 캐럿 또는 확장된 셀랙션을 렌더링 합니다.
     * @param {Selection} selection
     */
    renderSelection(selection) {
        if(selection.isCellSelection() || selection.isInCell()) {
            const tableId = selection.anchor.tableId || selection.getTable().getId();
            const cellIndex = selection.cellIndex || selection.getCells()[0].getCellIndex();
            this.tableHandler.show(tableId, cellIndex);
        } else {
            this.tableHandler.hide();
        }

        if (selection.isCollapsed()) {
            this.setCaret(selection.focus);
        } else {
            this.stop();
            if (selection.isPictureSelection()) {
                this.setPictureSelection(selection);
            } else if (selection.isCellSelection()) {
                this.extendCellSelection(selection);
            } else {
                this.extendSelection(selection);
            }
        }
    }

    /**
     * focus가 화면에 들어오도록 스크롤을 보정합니다.
     * @param {Position} position 
     */
    reviseScroll(position) {
        let elMain = this.$elMain.get(0);
        let mainRect = elMain.getBoundingClientRect();
        let targetRect = position.getRect(this.editorId);
        let [top, bottom] = [targetRect.top - mainRect.top, targetRect.bottom - mainRect.top];

        if (top < 0) { // focus가 보이는 영역 위에 있을 경우
            elMain.scrollTop += top;
        } else if (bottom > mainRect.height) { // focus가 보이는 영역보다 클경우
            elMain.scrollTop += (bottom - mainRect.height);
        }
    }

    /**
     * 정해진 위치에 Caret을 호출합니다.(movePosition과 blink를 호출합니다.)
     * @param {Position} position
     */
    setCaret(position) {
        this.empty();
        this.movePosition(position);
        this.blink();

        this.uiManager.hideAllPopup();
    }

    /**
     * 셀렉션에 해당하는 Picture 셀렉션을 랜더링 합니다.
     * @param {Selection} selection 
     */
    setPictureSelection(selection) {
        const anchor = selection.anchor;
        const elPicture = document.querySelector(`#${this.editorId} .se-contents img[pic-id='${anchor.pictureId}']`);

        this.empty();
        this.pictureHandler.show(elPicture);

        this.uiManager.hideAllPopup();
        this.uiManager.showBalloonPopup(this.pictureHandler.$elPictureSelection[0], UI_NAME.PICTURE_BALLOON);
    }

    /**
     * 셀랙션을 확장하여 렌더링합니다.(line단위로 셀랙션 합니다.)
     * @param {Selection} selection
     */
    extendSelection(selection) {
        let elMain = this.$elMain.get(0);
        let startPosition = selection.getStartPosition();
        let endPosition = selection.getEndPosition();
        let mainRect = elMain.getBoundingClientRect();
        let startRect = startPosition.getRect(this.editorId);
        let endRect = endPosition.getRect(this.editorId);
        let [startMiddleY, endMiddleY] = [(startRect.top + (startRect.height/2)), (endRect.top + (endRect.height/2))];
        let lineInfos = this.selectionManager.getLineInfosFromSelection();

        let $selections = [];
        let endLineInfo = null;

        for(let lineInfo of lineInfos) {
            let left = lineInfo.left;
            if (startMiddleY > lineInfo.bottom) {
                continue;
            }

            if (endMiddleY < lineInfo.top) {
                break;
            }

            if(!endLineInfo) { // 첫줄 처리
                left = startRect.left;
            }
            endLineInfo = lineInfo;

            let $sel = $('<div class="se-selection"></div>').css({
                top : lineInfo.top - mainRect.top + elMain.scrollTop,
                left : left - mainRect.left,
                height : lineInfo.bottom - lineInfo.top,
                width : lineInfo.right - left
            });
            $selections.push($sel);
        }

        // 마지막 셀랙션 보정
        let lastLeft = $selections.length === 1 ? startRect.left : endLineInfo.left;
        $selections[$selections.length - 1].css({
            width : endRect.right - lastLeft
        });

        this.empty();
        this.$elSelections.append(...$selections);
    }

    /**
     * 셀랙션을 확장하여 렌더링합니다.
     * @param {Selection} selection
     */
    extendCellSelection(selection) {
        let elMain = this.$elMain.get(0);
        let elTableBody = elMain.querySelector(`table[table-id='${selection.anchor.tableId}'] tbody`);
        let mainRect = elMain.getBoundingClientRect();
        let leftTopcell = selection.getLeftTopCell();
        let rightBottomCell = selection.getRightBottomCell();
        let focusCell = selection.getFocusCell();
        let [ltRect, rbRect, focusRect] = [leftTopcell, rightBottomCell, focusCell].map(cell => {
            let elTr = elTableBody.children[cell.getRowIndex()];
            let elTd = elTr.children[cell.getColIndex(true)];
            return elTd.getBoundingClientRect();
        });

        let $cellSelection = $(`<div class='se-selection cell'></div>`).css({
            top : ltRect.top - mainRect.top + elMain.scrollTop,
            left : ltRect.left - mainRect.left,
            height : rbRect.bottom - ltRect.top,
            width : rbRect.right - ltRect.left
        });
        let $focusCellSelection = $(`<div class='se-selection cell focus'></div>`).css({
            top : focusRect.top - mainRect.top + elMain.scrollTop,
            left : focusRect.left - mainRect.left,
            height : focusRect.bottom - focusRect.top,
            width : focusRect.right - focusRect.left
        });

        this.empty();
        this.$elSelections.append($cellSelection);
        this.$elSelections.append($focusCellSelection);
    }
}

export default SelectionRenderer;



