import $ from 'jquery';

/**
 * 모든 Popup 클래스의 부모 클래스입니다.
 */
class Popup {
    /**
     * Creates an instance of Popup.
     * @param {any} editorId - 에디터를 식별할 수 있는 아이디 
     * @param {any} name - Popup 이름
     * @param {any} width - Popup 너비
     * @param {any} height - Popup 높이
     */
    constructor(editorId, name, width, height) {
        this.editorId = editorId;
        this.width = width;
        this.height = height;
        this.name = name;

        this.target = undefined; // 팝업을 오픈시킨 버튼에 대한 정보를 가지고 있습니다. (서로 다른 버튼에서 같은 동작 실행시 재팝업 하기 위한 용도)
        this.$element = undefined; // popup jQuey Element입니다.
        this.fnHideCallback = undefined; // hide() 수행시 추가적으로 수행할 콜백 함수입니다. (1회용)

        this.bUseConfirmBtn = true; //확인 버튼 사용 옵션 flag, Popup를 상속 받아 생성된 인스턴스에서 초기화 해줄 수 있음.
        this.bUseCloseBtn = true; //닫기 버튼 사용 옵션 flag, Dialog를 상속 받아 생성된 인스턴스에서 초기화 해줄 수 있음.
        this.bUseHeader = true; //Header 사용 옵션 flag, Dialog를 상속 받아 생성된 인스턴스에서 초기화 해줄 수 있음.
        this.bUseFooter = true; //Footer 사용 옵션 flag, Dialog를 상속 받아 생성된 인스턴스에서 초기화 해줄 수 있음.
    }

    /**
     * 팝업의 너비를 반환합니다.
     * @returns {Number} - 너비
     */
    getWidth() {
        return this.width;
    }

    /**
     * 팝업의 높이를 반환합니다.
     * @returns {Number} - 높이
     */
    getHeight() {
        return this.height;
    }

    /**
     * 팝업의 이름을 반환합니다.
     * @returns {String} - 이름
     */
    getName() {
        return this.name;
    }

    /**
     * 팝업의 position을 반환합니다.
     * @returns {Object} position
     * @example 
     * // position 형식
     * {
     *      top: Number,
     *      left: Number
     * };
     */
    getPosition() {
        const element = this.getElement().get(0);

        return {
            top: element.offsetTop,
            left: element.offsetLeft
        };
    }

    /**
     * 팝업 HTML Element의 width, Height를 반환합니다.
     * @returns {Object} Size
     * @example
     * // size 형식
     * {
     *      width: Number,
     *      height: Number
     * }
     */
    getSize() {
        const element = this.getElement().get(0);

        return {
            width: element.offsetWidth,
            height: element.offsetHeight
        };
    }

    /**
     * 팝업의 jQuery Element를 반환합니다.
     * @returns {jQueryElement}
     */
    getElement() {
        return this.$element;
    }

    /**
     * Popup Dom을 생성하여 Popup Container에 추가합니다.
     */
    createDom() {
        const popupHTML = `
            <div class='se-popup' style='min-width: ${this.getWidth()}px; min-height: ${this.getHeight()}px;'>
                <div class='se-popup-arrow'></div>
            </div>
        `;
        const $popup = $(popupHTML);

        //Header 붙이기
        const headerElement = this.getHeaderElement();
        if (this.bUseHeader) {
            headerElement.addClass('show');
        }
        $popup.append(headerElement);

        //Body 붙이기
        $popup.append(this.getBodyElement());

        //Footer 붙이기
        if (this.bUseFooter) {
            $popup.append(this.getFooterElement());
        }

        //Container에 Popup 붙이기
        let $popupContainer = $('#' + this.editorId).find('.se-popup-container');
        $popupContainer.append($popup);

        this.$element = $popup;
    }

    /**
     * 팝업의 Header 영역에 들어갈 jQueryElement를 반환합니다.
     * @returns {jQueryElement}
     */
    getHeaderElement() {
        const closeHTML = this.bUseCloseBtn ? `<span class='se-popup-close'>X</span>` : '';
        const backHTML = `<span class='se-popup-back'>≪</span>`;

        return $(`<div class='se-popup-header'>${backHTML}${closeHTML}</div>`);
    }

    /**
     * 팝업의 body 영역에 들어갈 jQueryElement를 반환합니다.
     * Popup을 상속받은 클래스는 getBodyElement()를 오버라이드 합니다.
     * @returns {jQueryElement}
     */
    getBodyElement() {
        // do override this
        return $(`<div class='se-popup-body'>`);
    }

    /**
     * 팝업의 Footer 영역에 들어갈 jQueryElement를 반환합니다.
     * @returns {jQueryElement}
     */
    getFooterElement() {
        const confirmHTML = this.bUseConfirmBtn ? `<span class='se-popup-confirm se-popup-btn'>확인</span>` : '';
        return $(`<div class='se-popup-footer'>${confirmHTML}</div>`);
    }

    /**
     * 팝업의 위치를 설정합니다. 
     * @param {Object} [position] 팝업을 보여줄 위치 
     * @example 
     * // position 형식
     * {
     *      top: Number,
     *      left: Number
     * };
     */
    setPosition(position) {
        this.getElement().css(position);
    }

    /**
     * 팝업을 토글합니다.
     * @param {HTMLElement} [position] 팝업을 보여줄 위치
     * @param {Boolean} [isBalloonAction=false] 풍선 도구에서 발생한 액션인지 여부
     */
    toggle(position, isBalloonAction = false) {
        const $popup = this.getElement();

        if ($popup.hasClass('show')) {
            this.hide();
        } else {
            this.show(position, isBalloonAction);
        }
    }

    /**
     * 팝업을 토글합니다.
     * @param {HTMLElement} target 이벤트가 발생한 대상 버튼
     * @param {HTMLElement} [position] 팝업을 보여줄 위치
     * @param {Function} [fnHideCallback] 팝업을 닫고나서 수행될 함수
     */
    toggleWithTarget(target, position, fnHideCallback) {
        const isBalloonAction = !!$(target).parents('.se-popup').get(0);

        this.setHideCallback(fnHideCallback);

        if (this.target !== target) {
            this.target = target;
            this.show(position, isBalloonAction);
        } else {
            this.toggle(position, isBalloonAction);
        }
    }

    /**
     * 팝업을 보여줍니다.
     * @param {Object} [position] 팝업을 보여줄 위치
     * @param {Boolean} [isBalloonAction=false] 풍선 도구에서 발생한 액션인지 여부
     * @example
     * // position
     * var position = {
     *      top: 10,
     *      left: 10
     * };
     * 
     * popup.show(position);
     */
    show(position, isBalloonAction = false) {
        if (!!position) {
            this.setPosition(position);
        }
        this.reconfigureHeader(isBalloonAction);

        this.getElement().addClass('show');
    }

    /**
     * 팝업을 숨깁니다.
     */
    hide() {
        this.getElement().removeClass('show');

        if (!!this.fnHideCallback) {
            this.fnHideCallback.call();
            this.fnHideCallback = undefined;
        }
    }

    /**
     * 팝업의 확인 버튼을 누를 때 호출됩니다.
     */
    confirm() {
        this.hide();
    }

    /**
     * 다이얼로그의 취소 버튼을 누를 때 호출됩니다.
     */
    cancel() {
        this.hide();
    }

    /**
     * 다이얼로그의 뒤로 버튼을 누를 때 호출됩니다.
     */
    back() {
        this.hide();
    }

    /**
     * hide()가 수행될 때 수행할 콜백 함수를 설정합니다.
     * @param {Function} fn 콜백 함수
     */
    setHideCallback(fn) {
        this.fnHideCallback = fn;
    }

    /**
     * 팝업의 헤더를 다시 구성합니다. <br/>
     * (뒤로 가기 버튼이 보여야 하는 경우 헤더를 보이도록 설정합니다)
     * @param {Boolean} isBalloonAction 풍선 도구에서 발생한 액션인지 여부
     */
    reconfigureHeader(isBalloonAction) {
        const $element = this.getElement();

        if (isBalloonAction) {
            // 뒤로가기 버튼이 보이도록 함
            $element.find('.se-popup-header, .se-popup-back').addClass('show');
        } else {
            // 헤더를 보이게/안보이게 && 뒤로가기버튼이 안보이도록 함
            if (!this.bUseHeader) {
                $element.find('.se-popup-header').removeClass('show');
            }
            $element.find('.se-popup-back').removeClass('show');
        }
    }

    /**
     * 이벤트를 등록합니다.
     */
    attachEvent() {
        const $popup = this.getElement();

        $popup.on('click', '.se-popup-confirm', this.confirm.bind(this));
        $popup.on('click', '.se-popup-close', this.cancel.bind(this));
        $popup.on('click', '.se-popup-back', this.back.bind(this));
    }

    /**
     * 이벤트를 해지합니다.
     */
    detachEvent() {
        const $popup = this.getElement();

        $popup.off('click', '.se-popup-confirm');
        $popup.off('click', '.se-popup-close');
        $popup.off('click', '.se-popup-back');
    }
}

export default Popup;


