import $ from 'jquery';
import UIInterface from './UIInterface';
import icon from './icon';

/**
 * 버튼 입니다.
 * @extends UIInterface
 * @example
 * var buttonOption = {
 *      label: '버튼레이블',
 *      tooltipMsg: '툴팁메세지',
 *      onClickFunc: function () {console.log('클릭시 수행할 기능');}
 * }
 *
 * var btn = new Button(buttonOption);
 */
class Button extends UIInterface {
    /**
     * 버튼 옵션으로 사용할 수 있는 속성 :<br/>
     * - label: (String) 버튼에 보여질 레이블입니다.
     * - tooltipMsg: (String) 툴팁에 보여질 메세지. 기본값은 label로 주어진 값입니다.
     * - onClickFunc: (Function) 클릭했을 때 수행할 동작입니다.
     * @param {String} buttonName 버튼 이름
     * @param {Object} buttonOption 버튼 옵션
     */
    constructor(buttonName, buttonOption) {
        super();
        this.name = buttonName;
        this.label = buttonOption.label || ''; // 버튼에 보여질 레이블
        this.tooltipMsg = buttonOption.tooltipMsg || this.label; // 마우스 오버시 툴팁으로 보여줄 메세지. 기본값은 버튼 레이블
        this.onClickFunc = buttonOption.onClickFunc; // 클릭했을 때 수행할 동작
        this.$element = this.__createElement__();
        this.$tooltipElement = this.__createTooltip__(); // 툴팁 jQuery Element
    }

    /**
     * 버튼의 jQuery Element를 가져옵니다.
     * @returns {jQueryElement} Button의 jQuery Element
     */
    getElement() {
        return this.$element;
    }

    /**
     * 버튼의 jQuery Element를 생성합니다.
     * @private
     * @returns {jQueryElement} 생성된 버튼의 HTML Element
     */
    __createElement__() {
        // 버튼 만들기
        const $elButtonWrap = $(`<div class="se-menu-btn-wrapper" name="${this.name}"></div>`);
        const src = icon[this.name];
        const $button = $(`<img src="${src}" class="se-menu-btn" value=${this.name}>`);
        const $label = $(`<span>${this.label}</span>`);

        $elButtonWrap.append($button);
        $button.append($label);

        // 버튼에 클릭 이벤트 붙이기
        if (!!this.onClickFunc) {
            $elButtonWrap.on('click', (event) => {
                this.onClickFunc(event);
            });
        }

        return $elButtonWrap;
    }

    /**
     * 버튼에 툴팁을 생성하고, 생성된 툴팁을 가져옵니다.
     * @private
     * @returns {jQueryElement} 생성된 툴팁의 jQuery Element
     */
    __createTooltip__() {
        const $button = this.$element;
        const className = 'se-tooltip-show';
        const $tooltip = $(`<span class="se-tooltip">${this.tooltipMsg}</span>`);

        $button.append($tooltip);

        // 버튼에 툴팁이벤트 붙이기
        $button.on({
            mouseover: () => {
                $tooltip.addClass(className);

                const x = ($button.width() - $tooltip.width()) / 2;
                $tooltip.css({
                    opacity: 0,
                    transform: `translate(${x}px, 10px)`
                });

                $tooltip.stop().animate({
                    opacity: 1
                }, 500);
            },
            mouseleave: () => {
                $tooltip.removeClass(className);
            }
        });

        return $tooltip;
    }
}

export default Button;


