import util from '../util/util';
import Position from '../model/selection/Position';
import CellPosition from '../model/selection/CellPosition';
import PicturePosition from '../model/selection/PicturePosition';
import CellIndex from '../model/CellIndex';

/**
 * 포지션 정보를 찾아서 반환해주는 역할을 합니다.
 */
class PositionManager {
    constructor(SEModelManager) {
        this.SEModelManager = SEModelManager;
    }
    /**
     * 편집모델의 맨 앞 Position을 반환합니다.
     * @returns {Position} Document start position
     */
    documentStart() {
        const firstParagraph = this.SEModelManager.getFirstParagraph();
        return new Position(firstParagraph.getId());
    }

    /**
     * 편집모델의 맨 뒤 Position을 반환합니다.
     * @returns {Position} Document end position
     */
    documentEnd() {
        const lastParagraph = this.SEModelManager.getLastParagraph();
        return new Position(lastParagraph.getId(), lastParagraph.length());
    }

    /**
     * 인자로 받은 position기준으로 왼쪽 position을 반환합니다.
     * @param {Position} position
     * @returns {Position} left Position
     */
    getLeftPosition(position) {
        const {id, offset} = position;
        if (offset === 0) {
            return this.getPreviousParagraphEndPosition(position);
        } else {
            return new Position(id, offset - 1);
        }
    }

    /**
     * 인자로 받은 position기준으로 오른쪽 position을 반환합니다.
     * @param {Position} position
     * @returns {Position} right Position
     */
    getRightPosition(position) {
        const {id, offset} = position;
        const paragraph = this.getElement(position);
        if (offset === paragraph.length()) {
            return this.getNextParagraphStartPosition(position);
        } else {
            return new Position(id, offset + 1);
        }
    }

    /**
     * 인자로 받은 cellPosition기준으로 왼쪽 position을 반환합니다.
     * @param {CellPosition} cellPosition 
     * @returns {Position}
     */
    getLeftPositionFromCellPosition(cellPosition) {
        const {id, offset, tableId, rowIndex, colIndex} = cellPosition;
        const table = this.getElement(new Position(tableId));

        let paragraph;
        if (rowIndex === 0 && colIndex === 0) {
            paragraph = this.SEModelManager.previousParagraphOf(table.parent());
        } else {
            let cellIndex;
            if (colIndex === 0) {
                cellIndex = new CellIndex(rowIndex - 1, colIndex);
            } else {
                cellIndex = new CellIndex(rowIndex, colIndex - 1);
            }
            paragraph = table.getCell(cellIndex).lastChild();
        }

        return !paragraph ? new Position(id, offset) : new Position(paragraph.getId(), paragraph.length());
    }

    /**
     * 인자로 받은 cellPosition기준으로 오른쪽 position을 반환합니다.
     * @param {CellPosition} cellPosition 
     * @returns {Position}
     */
    getRigthPositionFromCellPosition(cellPosition) {
        const {id, offset, tableId, rowIndex, colIndex} = cellPosition;
        const table = this.getElement(new Position(tableId));
        const cell = table.getCell(new CellIndex(rowIndex, colIndex));
        const nextCellRowIndex = rowIndex + cell.getRowSpan();
        const nextCellColIndex = colIndex + cell.getColSpan();
        const isLastRow = nextCellRowIndex === table.getCountOfRow();
        const isLastCol = nextCellColIndex === table.getCountOfColumn();

        let paragraph;
        if (isLastRow && isLastCol) {
            paragraph = this.SEModelManager.nextParagraphOf(table.parent());
        } else {
            let cellIndex;
            if (isLastCol) {
                cellIndex = new CellIndex(nextCellRowIndex, colIndex);
            } else {
                cellIndex = new CellIndex(rowIndex, nextCellColIndex);
            }
            paragraph = table.getCell(cellIndex).firstChild();
        }

        return !paragraph ? new Position(id, offset) : new Position(paragraph.getId());
    }

    /**
     * 인자로 받은 cellPosition기준으로 위쪽 position을 반환합니다.
     * @param {CellPosition} cellPosition
     * @returns {Position}
     */
    getUpPositionFromCellPosition(cellPosition) {
        const {id, offset, tableId, rowIndex, colIndex} = cellPosition;
        const table = this.getElement(new Position(tableId));
        let paragraph; 
        if (rowIndex === 0) {
            paragraph = this.SEModelManager.previousParagraphOf(table.parent());
        } else {
            paragraph = table.getCell(new CellIndex(rowIndex - 1, colIndex)).lastChild();
        }

        return !paragraph ? new Position(id, offset) : new Position(paragraph.getId(), paragraph.length());
    }

    /**
     * 인자로 받은 cellPosition기준으로 아래쪽 position을 반환합니다.
     * @param {CellPosition} cellPosition
     * @returns {Position}
     */
    getDownPositionFromCellPosition(cellPosition) {
        const {id, offset, tableId, rowIndex, colIndex} = cellPosition;
        const table = this.getElement(new Position(tableId));
        const cell = table.getCell(new CellIndex(rowIndex, colIndex));
        const nextCellRowIndex = rowIndex + cell.getRowSpan();
        const isLastRow = nextCellRowIndex === table.getCountOfRow();

        let paragraph; 
        if (isLastRow) {
            paragraph = this.SEModelManager.nextParagraphOf(table.parent());
        } else {
            paragraph = table.getCell(new CellIndex(nextCellRowIndex, colIndex)).firstChild();
        }

        return !paragraph ? new Position(id, offset) : new Position(paragraph.getId());
    }

    /**
     * previous paragraph start position을 반환합니다.
     * @param {Position} position
     * @returns {Position} Previous Paragraph Start Position
     */
    getPreviousParagraphStartPosition(position) {
        let element = this.getElement(position);
        let prevElement = position.offset === 0 ? this.SEModelManager.previousParagraphOf(element) : element;
        return prevElement ? new Position(prevElement.getId()) : position;
    }

    /**
     * previous paragraph end position을 반환합니다.
     * @param {Position} position
     * @returns {Position} Previous Paragraph End Position
     */
    getPreviousParagraphEndPosition(position) {
        let element = this.getElement(position);
        let prevElement = this.SEModelManager.previousParagraphOf(element);
        return prevElement ? new Position(prevElement.getId(), prevElement.length()) : position;
    }

    /**
     * next paragraph start position을 반환합니다.
     * @param {Position} position
     * @returns {Position} Next Paragraph Start Position
     */
    getNextParagraphStartPosition(position) {
        let element = this.getElement(position);
        let nextElement = this.SEModelManager.nextParagraphOf(element);
        return nextElement ? new Position(nextElement.getId()) : position;
    }

    /**
     * next paragraph end position을 반환합니다.
     * @param {Position} position
     * @returns {Position} Next Paragraph End Position
     */
    getNextParagraphEndPosition(position) {
        let element = this.getElement(position);
        let nextElement = position.offset === element.length() ? this.SEModelManager.nextParagraphOf(element) : element;
        return nextElement ? new Position(nextElement.getId(), nextElement.length()) : position;
    }

    /**
     * Previous Word Position을 반환합니다.
     * @param {Position} position 
     * @returns {Position} Previous Word Position
     */
    getPreviousWordPosition(position) {
        let element = this.getElement(position);
        if (!util.isParagraph(element)) {
            return null;
        }

        if (position.offset === 0) {
            return this.getPreviousParagraphEndPosition(position);
        } else {
            let offset = getWordStartOffset(element.getText(), position.offset, false);
            return new Position(position.id, offset);
        }
    }

    /**
     * Next Word Position을 반환합니다.
     * @param {Position} position 
     * @returns {Position} Next Word Position
     */
    getNextWordPosition(position) {
        let element = this.getElement(position);
        if (!util.isParagraph(element)) {
            return null;
        }

        if (position.offset === element.length()) {
            return this.getNextParagraphStartPosition(position);
        } else {
            let offset = getWordStartOffset(element.getText(), position.offset, true);
            return new Position(position.id, offset);
        }
    }

    /**
     * Position(type:text)을 CellPosition(type:table)으로 변환하여 반환합니다.</br>
     * 인자로 받은 commonTable의 자식레벨(tableCell)로 position을 맞춰줍니다. 
     * @param {Position} position 
     * @param {Table} commonTable 
     * @returns {CellPosition} CellPosition
     */
    toCellPosition(position, commonTable) {
        let cell = this.getElement(position).closest(el => {
            return util.isTableCell(el) && el.getTable() === commonTable;
        });
        return new CellPosition(position.id, position.offset, commonTable.getId(), cell.getRowIndex(), cell.getColIndex());
    }

    /**
     * 인자로 받은 position과 pictureId에 맞는 position을 반환합니다.
     * @param {Position} position 
     * @param {Number} pictureId 
     */
    toPicturePosition(position, pictureId) {
        return new PicturePosition(position.id, position.offset, pictureId);
    }

    /**
     * CellPosition(type:table)을 Position(type:text)으로 변환하여 반환합니다.</br>
     * 인자로 받은 commonRoot의 자식레벨(paragraph)로 position을 맞춰줍니다.
     * @param {Position} position
     * @param {Element} commonRoot
     * @param {Boolean} isPrecedes 인자로 받은 position이 앞서고있는지 여부
     * @returns {Position} position
     */
    toPosition(position, commonRoot, isPrecedes) {
        let paragraph = this.getElement(position).closest(el => {
            return util.isParagraph(el) && el.parent() === commonRoot;
        });
        if(paragraph.isTable()) {
            return new Position(paragraph.getId(), isPrecedes ? 0 : 1);
        } else {
            return new Position(position.id, position.offset);
        }
    }

    /**
     * Position의 정보에 해당하는 편집모델 Element를 반환합니다.
     * @param {Position} position
     * @returns {Element} Element
     */
    getElement(position) {
        let id = position.id;
        let bodyModel = this.SEModelManager.getBody();
        return bodyModel.findById(id);
    }

    /**
     * Position의 정보에 해당하는 편집모델 Element Run을 반환합니다.
     * @param {Position} position 
     * @returns {TextRun} textRun 편집모델
     */
    getRun(position) {
        let element = this.getElement(position);
        if (util.isParagraph(element)) {
            let endOffset = 0;
            return element.getChildren().find(run => {
                endOffset += run.length();
                return position.offset <= endOffset;
            });
        }

        return null;
    }

    /**
     * Position에 해당하는 Length가 0인 Run들을 반환합니다.
     * @param {Position} position
     * @return {Array} runs
     */
    getSameOffsetRuns(position) {
        let run = this.getRun(position);
        let runs = [];

        runs.push(run);

        let nextRun = run.nextSibling();
        while (nextRun && nextRun.getRunStartOffset() === position.offset) {
            runs.push(nextRun);
            nextRun = nextRun.nextSibling();
        }

        return runs;
    }

    /**
     * 인자로 받은 Run SEModel의 Start Position을 반환합니다.
     * @param {TextRun} run
     * @return {Position} Run SEModel Start Position
     */
    getRunStartPosition(run) {
        return new Position(run.parent().getId(), run.getRunStartOffset());
    }

    /**
     * 인자로 받은 Run SEModel의 End Position을 반환합니다.
     * @param {TextRun} run
     * @return {Position} Run SEModel End Position
     */
    getRunEndPosition(run) {
        return new Position(run.parent().getId(), run.getRunEndOffset());
    }

    
    /**
     * 두 Position 정보를 비교하여 앞인지 뒤인지 검사합니다.<br>
     * 현재 Position이 앞쪽이라면 -1을 반환, 뒤에 있으면 1을 반환합니다. (같으면 0을 반환)
     * @param {Position} mainPosition
     * @param {Position} subPosition
     * @returns {Number} this position이 앞: -1, 뒤: 1, 같으면: 0
     */
    compare(mainPosition, subPosition) {
        if (mainPosition.isEqual(subPosition)) {
            return 0;
        } else if (this.getElement(mainPosition) === this.getElement(subPosition)) {
            return (mainPosition.offset - subPosition.offset) / Math.abs(mainPosition.offset - subPosition.offset);
        } else {
            var thisElem = this.getElement(mainPosition);
            var posElem = this.getElement(subPosition);
            if (!thisElem || !posElem) {
                throw new Error('Position is not set');
            }
            if (thisElem.contains(posElem)) {
                let containElemOffset = this.getSameLevelOffset(thisElem, posElem);
                let offset = mainPosition.offset - containElemOffset;
                offset = offset === 0 ? -1 : offset;
                return (offset) / Math.abs(offset);
            } else if (posElem.contains(thisElem)) {
                let containElemOffset = this.getSameLevelOffset(posElem, thisElem);
                let offset = containElemOffset - subPosition.offset;
                offset = offset === 0 ? 1 : offset;
                return (offset) / Math.abs(offset);
            }
            return thisElem.compare(posElem);
        }
    }

    /**
     * parent와 child Element를 받아 child Element의 StartOffset을 반환합니다.
     * @param {Element} parentElem
     * @param {Element} childElem
     * @return {Number} child Start Offset
     */
    getSameLevelOffset(parentElem, childElem) {
        while (parentElem !== childElem.parent()) {
            childElem = childElem.parent();
        }
        return childElem.getRunStartOffset();
    }

    /**
     * 현재 mainPosiiton이 subPosition보다 앞인지 검사합니다.
     * @param {Position} mainPosition
     * @param {Position} subPosition
     * @return {Boolean}
     */
    precedes(mainPosiiton, subPosition) {
        return this.compare(mainPosiiton, subPosition) < 0;
    }

    /**
     * 현재 mainPosiiton이 subPosition보다 뒤인지 검사합니다.
     * @param {Position} mainPosition
     * @param {Position} subPosition
     * @return {Boolean}
     */
    follows(mainPosiiton, subPosition) {
        return this.compare(mainPosiiton, subPosition) > 0;
    }
}

/**
 * 이전 또는 다음 단어의 시작 offset을 구합니다.
 * @private
 * @param {String} text 문단 전체 문자열
 * @param {Number} offset 현재 문단에서의 offset
 * @param {Boolean} isNext 다음 offset을 비교할지 여부
 * @returns {Number} word start offset
 */
function getWordStartOffset(text, offset, isNext) {
    let wordStartOffset = offset;
    while(isNext ? (wordStartOffset < text.length) : (wordStartOffset > 0)) {
        wordStartOffset = wordStartOffset + (isNext ? 1 : -1);
        let subString = text.slice(wordStartOffset-1, wordStartOffset+1);
        if (subString.charAt(0) === ' ' && subString.charAt(1) !== ' ') {
            break;
        }
    }
    return wordStartOffset;
}

export default PositionManager;


