import $ from "jquery";
import Dialog from "./Dialog";
import cpDataHandler from './characterLoader';
import consts from '../../consts/consts';
import util from '../../util/util';

const UI_NAME = consts.UI_NAME;
const ROW_NUM = 10;
const COL_NUM = 16;
const DEFAULT_CATEGORY = 'default';
const TAB_MENU = {
    SPECIAL_CHARACTER: 'specialCharacter',
    EMOTICON: 'emoticon',
    EMOJI: 'emoji'
};

/**
 * 특수기호, 이모티콘, 이모지 삽입을 위한 CharacterPicker 클래스입니다.
 * Dialog를 상속 받습니다.
 * @extends {Dialog}
 */
class CharacterPicker extends Dialog {
    /**
     * CharacterPicker 생성자입니다.
     * @param {String} editorId - 에디터를 식별할 수 있는 아이디
     * @param {Controller} controller - 액션 편집을 위한 컨트롤러 인스턴스
     */
    constructor(editorId, controller) {
        super(editorId);

        this.editorId = editorId;
        this.controller = controller;

        this.selectedTabMenu = TAB_MENU.SPECIAL_CHARACTER;
        this.selectedCategory = DEFAULT_CATEGORY;

        this.$element = this.__createDom__();
        this.__attachEvent__();
    }

    /**
     * 이벤트 등록을 합니다.
     * @private
     */
    __attachEvent__() {
        const $cancel = this.$element.find('.se-cancel');
        const $tabs = this.$element.find('.se-tab');

        $cancel.on('click', this.hide.bind(this));
        $tabs.on('click', this.__selectTabMenu__.bind(this));

        this.$element.find('.se-character-picker-categories').on('click', 'li', this.__selectCategory__.bind(this));
        this.$element.find('.se-character-picker-values').on('click', 'td', this.__selectValue__.bind(this));
    }

    /**
     * Dialog Container 안에 CharacterPicker Dom을 생성하여 추가합니다.
     * @private
     * @returns {jQuery}
     */
    __createDom__() {
        const tabMenu = this.selectedTabMenu;
        const category = this.selectedCategory;

        const $dialogContainer = $('#' + this.editorId).find('.se-dialog-container');
        const $element = $(`
            <div class="se-dialog" name="${UI_NAME.CHARACTER_PICKER}">
                <div class='se-dialog-header'>
                    <div class='se-tab' value='specialCharacter'><span>특수기호</span></div>
                    <div class='se-tab' value='emoticon'><span>이모티콘</span></div>
                    <div class='se-tab' value='emoji'><span>이모지</span></div>
                </div>
                <div class='se-dialog-body'>
                    <div class='se-character-picker-categories'></div>
                    <div class="se-character-picker-values"></div>
                </div>
                <div class="se-dialog-footer">
                    <div class='se-cancel se-btn'><span>닫기</span></div>
                </div>
            </div>
        `);

        const $categories = $element.find('.se-character-picker-categories');
        const $values = $element.find('.se-character-picker-values');

        $categories.append(this.__getCategory__(tabMenu));
        $values.append(this.__getTable__(tabMenu, category));

        $dialogContainer.append($element);

        return $element;
    }

    /**
     * CharacterPicker Element를 보여줍니다.
     * @param {String} actionName -
     */
    show(actionName) {
        super.show();
        this.actionName = actionName;
    }

    /**
     * CharacterPicker Element를 숨깁니다.
     */
    hide() {
        super.hide();
        this.actionName = null;
    }

    /**
     * tab menu에 해당하는 카테고리를 렌더링합니다.
     * @private
     * @param {String} tabMenu
     */
    __renderCategory__(tabMenu) {
        const $categories = this.$element.find('.se-character-picker-categories').find('ul');
        $categories.replaceWith(this.__getCategory__(tabMenu));
    }

    /**
     * tab menu에 해당하는 카테고리 DOM을 반환합니다.
     * @private
     * @param {String} tabMenu
     * @return {jQuery}
     */
    __getCategory__(tabMenu) {
        const $ul = $('<ul></ul>');

        cpDataHandler.getItems(tabMenu).forEach(item => {
            const category = util.get(item, ['category']);
            const label = util.get(item, ['label']);
            const $li = $(`<li value='${category}'>${label}</li>`);

            $ul.append($li);
        });

        return $ul;
    }

    /**
     * 특수기호 테이블을 렌더링합니다.
     * @private
     * @param {String} tabMenu
     * @param {String} category
     */
    __renderTable__(tabMenu, category) {
        const $table = this.$element.find('.se-character-picker-values').find('table');
        $table.replaceWith(this.__getTable__(tabMenu, category));
    }

    /**
     * 특수기호 테이블 DOM을 반환합니다.
     * @private
     * @param {String} tabMenu
     * @param {String} category
     * @return {jQuery}
     */
    __getTable__(tabMenu, category) {
        const values = cpDataHandler.getCategoryValues(tabMenu, category);
        const $table = $('<table></table>');
        let $tr;

        for (let i = 0, max = Math.max(COL_NUM * ROW_NUM, values.length); i < max; i++) {
            const $td = $(`<td></td>`);

            if (i % COL_NUM === 0) {
                $tr = $('<tr></tr>');
                $table.append($tr);
            }

            if (values[i]) {
                $td.text(values[i]);
                $td.attr('value', values[i]);
                $td.addClass('has-value');
            }

            $tr.append($td);
        }

        return $table;
    }

    /**
     * 탭을 선택합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __selectTabMenu__(e) {
        const currentTarget = e.currentTarget;
        const tabMenu = $(currentTarget).attr('value');

        if (this.selectedTabMenu !== tabMenu) {
            this.$element.find('.se-tab').removeClass('selected');
            $(currentTarget).addClass('selected');

            this.selectedTabMenu = tabMenu;
            this.selectedCategory = DEFAULT_CATEGORY;

            this.__renderCategory__(this.selectedTabMenu);
            this.__renderTable__(this.selectedTabMenu, this.selectedCategory)
        }
    }

    /**
     * 카테고리를 선택합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __selectCategory__(e) {
        const currentTarget = e.currentTarget;
        const category = $(currentTarget).attr('value');

        if (this.selectedCategory !== category) {
            this.selectedCategory = category;

            this.__renderTable__(this.selectedTabMenu, this.selectedCategory);
        }
    }

    /**
     * 값을 선택합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __selectValue__(e) {
        const currentTarget = e.currentTarget;
        const value = $(currentTarget).attr('value');

        if (value) {
            this.controller.execute(this.actionName, value);
        }
    }
}

export default CharacterPicker;


