import ACTION from '../consts/actionMap';
import ControllerInterface from './ControllerInterface';

/**
 * Undo/Redo 편집동작을 수행하는 class입니다.
 */
class UndoRedoController extends ControllerInterface {
    constructor(editor, name) {
        super();
        this.editorId = editor.id;
        this.name = name;
        this.undoRedo = editor.getUndoRedo();
        this.modelHandler = editor.getModelHandler();
        this.renderer = editor.getRenderer();
        this.selectionRenderer = editor.getSelectionRenderer();
        this.selectionManager = editor.getSelectionManager();
        this.selection = this.selectionManager.selection;
        this.ime = editor.getIme();

        this.renderTargetParagraphs = [];
    }

    /**
     * Undo/Redo 동작후 랜더링 및 셀렉션 보정을 합니다.
     * @param {Boolean} [bForce=false]
     */
    postExecute({bForce=false} = {}) {
        if (bForce || checkGenerateListsCss(this.selection)) {
            this.renderer.listsRenderer.generateListsCss();
        }
        this.renderer.renderParagraphs(this.renderTargetParagraphs);
        this.selectionRenderer.renderSelection(this.selection);
        this.bScroll && this.selectionRenderer.reviseScroll(this.selection.focus);
        this.bSavePositionX && this.selectionManager.setStandardLeft();
        this.ime.focus();
    }
}

/**
 * 글머리를 표현하기 위한 CSS를 생성해야 할지 확인합니다.
 * @private
 * @param {Selection} selection
 * @returns {boolean}
 */
function checkGenerateListsCss(selection) {
    return !!selection
        && (selection.anchor.id !== selection.focus.id
        || selection.getStartPosition().offset === 0);
}

/**
 * undo/redo 관련 동작을 수행하는 undoRedoController 객체입니다.
 * 각 동작 메소드는 UndoRedoController를 상속 받습니다.
 */
const undoRedoController = {
    [ACTION.UNDO]: class extends UndoRedoController {
        execute() {
            const item = this.undoRedo.undo();
            if (!item) {
                return;
            }
            const selection = item.undo.selection;
            this.selection.set(selection.anchor, selection.focus);
            this.renderTargetParagraphs = this.selection.getParagraphs();

            item.undo.func();
        }
    },
    [ACTION.REDO]: class extends UndoRedoController {
        execute() {
            const item = this.undoRedo.redo();
            if (!item) {
                return;
            }
            const selection = item.redo.selection;
            this.selection.set(selection.anchor, selection.focus);
            this.renderTargetParagraphs = this.selection.getParagraphs();

            item.redo.func();
        }
    }
};

export default undoRedoController;


