import $ from 'jquery';
import util from '../util/util';

/**
 * 편집모델의 하위 모델인 Paragraph를 렌더링하는 Class입니다.<br/>
 * ParagraphRenderer에서 runRenderer, pictureRenderer를 호출합니다.
 */
class ParagraphRenderer {
    constructor(renderer) {
        const editor = renderer.editor;
        this.editorId = editor.id;
        this.listsManager = editor.getListsManager();

        this.runRenderer = renderer.getRunRenderer();
        this.hyperlinkRenderer = renderer.getHyperLinkRenderer();
        this.tableRenderer = renderer.getTableRenderer();
        this.pictureRenderer = renderer.getPictureRenderer();
        this.horizontalLineRenderer = renderer.getHorizontalLineRenderer();
        this.$elHyperlink = null;
    }

    /**
     * 하이퍼링크 JQuery Element를 설정합니다.
     * @param {String} HyperlinkHTMLString
     */
    setHyperlinkElement(HyperlinkHTMLString) {
        this.$elHyperlink = HyperlinkHTMLString ? $(HyperlinkHTMLString) : null;
    }

    /**
     * paragraph를 렌더링하기 위해 Paragraph Element를 인자로 받아 HTML을 반환합니다.
     * @param {Paragraph} paragraph Paragraph Element
     * @returns {String} HTML형태의 문자열
     * @example 
     * // return example
     * <p id='1' style='line-height: 200%;'>
     *  <span>동해물과</span>
     *  <span></span>
     *  <span>백두산이</span>
     * </p>
     */
    getHTML(paragraph) {
        const paragraphId = paragraph.getId();
        const childrenHTML = this.getChildrenHTML(paragraph);
        const tagName = util.get(paragraph, ['prop', 'para_style'], 'p');
        
        let $para = $(`<${tagName} id='${this.editorId}_${paragraphId}' para-id=${paragraphId}>${childrenHTML}</${tagName}>`);
        let cssText = this.getCssText(paragraph.prop);
        
        if (cssText) {
            $para.attr('style', cssText);
        }

        return $para.prop('outerHTML');
    }

    /**
     * 표를 렌더링하기 위해 Paragraph Element를 인자로 받아 HTML을 반환합니다. <br/>
     * 표는 하나의 문단으로 간주합니다.
     * @param {Paragraph} paragraph 
     * @returns {String} - HTML 형태의 문자열
     */
    getTableHTML(paragraph) {
        const paragraphId = paragraph.getId();
        const $table = $(this.tableRenderer.getHTML(paragraph.firstChild()));
        $table.attr('id', `${this.editorId}_${paragraphId}`);
        $table.attr('para-id', paragraphId);

        return $table.prop('outerHTML');
    }

    /**
     * 가로줄을 렌더링하기 위해 Paragraph Element를 인자로 받아 HTML을 반환합니다.('<hr>') <br/>
     * 가로줄은 하나의 문단으로 간주합니다.
     * @param {Paragraph} paragraph
     * @return {String} - HTML 형태의 문자열
     */
    getHorizonTalLineHTML(paragraph) {
        const paragraphId = paragraph.getId();
        const $horizonTalLine = $(this.horizontalLineRenderer.getHTML(paragraph.firstChild()));
        $horizonTalLine.attr('id', `${this.editorId}_${paragraphId}`);
        $horizonTalLine.attr('para-id', paragraphId);

        return $horizonTalLine.prop('outerHTML');
    }

    /**
     * 글머리목록을 렌더링하기 위해 Paragraph Element를 인자로 받아 HTML을 반환합니다.<br/>
     * 각 목록은 하나의 문단으로 간주합니다.
     * @param {Paragraph} paragraph Paragraph Element 
     * @returns {String} HTML형태의 문자열
     * @example
     * //return exmaple
     * <li id='1' class='lists1-0' style='text-indent: 10px;'>
     *  <span>동해물과</span>
     *  <span></span>
     *  <span>백두산이</span>
     * </li>
     */
    getListsHTML(paragraph) {
        let childrenHTML = this.getChildrenHTML(paragraph);
        let paragraphId = paragraph.getId();
        let tagName = paragraph.prop.para_style;
        let listsId = paragraph.getListsId();
        let listsLevel = paragraph.getListsLevel();
        let listsLevelItem = this.listsManager.getLevelItem(listsId, listsLevel);
        let align = this.listsManager.getListsAlign(listsId);
        let startValue = this.listsManager.getStartValue(listsId, listsLevel);
        let levelText = this.listsManager.getLevelText(listsId, listsLevel);
        let format = this.listsManager.getListsFormat(listsId, listsLevel);

        const paraProp = paragraph.getProp();
        const listsParaProp = util.get(listsLevelItem, ['para_prop']);
        const cssText = this.getCssText(Object.assign({}, paraProp, listsParaProp));
        
        if (tagName) {
            childrenHTML = `<${tagName}> ${childrenHTML} </${tagName}>`;
        }

        let $para = $(`<li>${childrenHTML}</li>`);

        $para.attr('id', `${this.editorId}_${paragraphId}`);
        $para.addClass(align.toLowerCase());
        $para.addClass(`level_${listsLevel}`);
        $para.attr('para-id', paragraphId);
        $para.attr('startval', startValue);
        $para.attr('text', levelText);
        $para.attr('format', format);
        
        if (cssText) {
            $para.attr('style', cssText);
        }

        return $para.prop('outerHTML');
    }

    /**
     * paragraph의 자식 엘리먼트(run, picture ...)를 렌더링하기 위한 HTML을 반환합니다.
     * @private
     * @param {Paragraph} paragraph Paragraph Element
     * @returns {String} HTML 형태의 문자열
     * @example
     * //return example
     *  <span>동해물과</span>
     *  <span></span>
     *  <span>백두산이</span>
     */
    getChildrenHTML(paragraph) {
        let $elTemp = $('<div></div>');
        let runs = paragraph.getChildren();

        if (runs.length > 0) {
            runs.forEach(run => {
                switch (run.getType()) {
                    case 'TextRun':
                        if (this.$elHyperlink) {
                            this.$elHyperlink.append($(this.runRenderer.getHTML(run)));
                        } else {
                            $elTemp.append($(this.runRenderer.getHTML(run)));
                        }
                        break;
                    case 'Picture':
                        const $elPicture = $(this.pictureRenderer.getHTML(run));

                        if (this.$elHyperlink) {
                            this.$elHyperlink.append($elPicture);
                        } else {
                            const hyperlink = util.get(run, ['layout_prop', 'word_layout', 'inline', 'non_visual_drawing_prop', 'hyperlink_on_click']);
                            if (hyperlink) {
                                const $elHyperlink = $(`<a se-data-href='${hyperlink.target}' target='_blank'></a>`);

                                $elHyperlink.append($elPicture);
                                $elTemp.append($elHyperlink);
                            } else {
                                $elTemp.append($elPicture);
                            }
                        }
                        break;
                    case 'HyperlinkStart':
                        this.$elHyperlink = $(this.hyperlinkRenderer.getHTML(run));
                        break;
                    case 'HyperlinkEnd':
                        $elTemp.append(this.$elHyperlink);
                        this.$elHyperlink = null;
                        break;
                }
            });

            if (this.$elHyperlink) { //하이퍼링크가 문단에서 끝나지 않았을 경우. 현재 문단에 append 하고 hyperlinkStart를 초기화한다.
                $elTemp.append(this.$elHyperlink);
            }

        } else {
            $elTemp.append($('<br>'));
        }

        let html = $elTemp.html();
        if (this.$elHyperlink) {
            this.$elHyperlink.empty();
        }
        return html;
    }

    /**
     * paragraph의 가로 정렬 속성 값을 반환합니다.
     * 값이 없는 경우 undefined를 리턴합니다.
     * @private
     * @param {Object} prop paragraph properties
     * @returns {String | undefined} 
     */
    getAlign(prop) {
        let align = util.get(prop, ['align']);
        return util.toLower(align) || undefined;
    }

    /**
     * paragraph의 줄간격 속성 값을 반환합니다.
     * spacing prop에 'line_in_percent' || 'line_in_sunit' 값을 이용하여 '%' || 'pt'단위의 줄간격을 반환합니다.
     * 값이 없는 경우 undefined를 리턴합니다.
     * @private
     * @param {Object} prop paragraph properties
     * @returns {String | undefined}
     */
    getLineHeight(prop) {
        let spacing = util.get(prop, ['spacing']);
        let value;
        if (spacing) {
            let lineInPercent = Number.parseFloat(util.get(spacing, ['line_in_percent']));
            let lineInSunit = Number.parseFloat(util.get(spacing, ['line_in_sunit']));
            if (lineInPercent) {
                value = `${lineInPercent / 100000}`;
            } else if (lineInSunit) {
                value = `${util.sunit2pt(lineInSunit)}pt`;
            }
        }
        return value;
    }

    /**
     * paragraph의 문단 들여쓰기와 첫 줄 들여쓰기 속성 정보를 반환합니다.
     * @private
     * @param {Object} prop paragraph properties
     * @returns {Object} first: 첫 줄 들여쓰기, left: 문단 들여쓰기
     */
    getIndentInfo(prop) {
        let oIndent = util.get(prop, ['indent']);
        let firstInSunit = util.get(oIndent, ['first_in_sunit'], 0);
        let leftInSunit = util.get(oIndent, ['left_in_sunit'], 0);

        return {
            first: util.sunit2px(firstInSunit),
            left: util.sunit2px(leftInSunit)
        };
    }

    /**
     * paragraph의 자간 속성 정보를 반환합니다.
     * @private
     * @param {Object} prop paragraph properties
     * @returns {Number} px단위의 값 
     */
    getLetterSpacing(prop) {
        let endRunProp = util.get(prop, ['end_run_prop']);
        let spacingInSunit = util.get(endRunProp, ['spacing_in_sunit']);

        return spacingInSunit ? util.sunit2px(spacingInSunit) : undefined;
    }

    /**
     * paragraph의 스타일을 적용하기 위해 paragraph properties를 받아 cssText를 반환합니다.
     * @private
     * @param {Object} prop paragraph properties
     * @returns {String}
     * @example
     * //return example
     * 'text-align: left;margin-left: 10px;'
     */
    getCssText(prop) {
        let cssText = '';

        let align = this.getAlign(prop);
        let lineHeight = this.getLineHeight(prop);
        let indentInfo = this.getIndentInfo(prop);
        let indent = util.get(indentInfo, ['left']);
        let textIndent = util.get(indentInfo, ['first']);
        let letterSpacing = this.getLetterSpacing(prop);

        if (align) {
            cssText += `text-align: ${align};`;
        }

        if (lineHeight) {
            cssText += `line-height: ${lineHeight};`;
        }

        if (indent) {
            cssText += `padding-left: ${indent}px;`;
        }

        if (textIndent) {
            cssText += `text-indent: ${textIndent}px;`;
        }

        if (letterSpacing) {
            cssText += `letter-spacing: ${letterSpacing}px;`;
        }

        return cssText;
    }
}

export default ParagraphRenderer;


