import util from '../util/util';
import Selection from '../model/selection/Selection';
import Position from '../model/selection/Position';
import PicturePosition from '../model/selection/PicturePosition';

/**
 * [Selection]{@link Selection}을 관리하는 Class입니다.</br>
 * [Selection]{@link Selection} 객체를 초기화 하고, 편집모델의 시작부분에 caret을 위치시킵니다.
 * @example
 * import SelectionManager from 'path';
 * var SelectionManager = SelectionManager.getInstnace();
 */
class SelectionManager {
    /**
     * SelectionManager.getInstnace()와 같이 사용합니다. <br/>
     * new SelectionManager()와 같은형식으로 사용을 권장하지 않습니다.
    */
    constructor(editor) {
        this.editorId = editor.id;
        this.positionManager = editor.getPositionManager();
        this.SEModelManager = editor.getSEModelManager();

        this.selection = new Selection(this.positionManager);
        this.elSEMain = document.querySelector(`#${this.editorId} .se-main`);
        this.elSEContents = document.querySelector(`#${this.editorId} .se-contents`);
        this.standardLeft = 0;
    }

    /**
     * 셀렉션을 초기화 합니다.
     */
    refresh() {
        this.documentStart();
    }


    /**
     * 확장 여부에따라 셀랙션의 achor와 focus를 수정합니다.
     * @param {Position} position 
     * @param {Boolean} isExtend
     */
    setSelectionPosition(position, isExtend) {
        if (!isExtend) {
            this.selection.setCaret(position);
        } else {
            this.selection.extend(position);
        }
    }

    /**
     * Mouse Left Down 이벤트발생시 이벤트 처리 함수 입니다.<br>
     * Event가 발생한 Element를 검사하여 Caret 및 Selection을 위치 시킵니다.
     * @param {Number} x x좌표
     * @param {Number} y y좌표
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    setSelection(x, y, isExtend = false) {
        let targetHTMLElement = document.elementFromPoint(x, y);
        let pictureId;
        if (util.isPictureHTMLElement(targetHTMLElement)) {
            pictureId = parseInt(targetHTMLElement.getAttribute('pic-id'));
        }

        let position = this.getPositionFromPoint(x, y);
        if (!position) {
            return;
        }

        if (pictureId) {
            position = new PicturePosition(position.id, position.offset, pictureId);
        }

        this.setSelectionPosition(position, isExtend);
    }

    /**
     * pictureId에 해당하는 picture Selection을 셋팅합니다.
     * @param {String} pictureId 
     */
    setPictureSelection(pictureId) {
        const position = this.positionManager.toPicturePosition(this.selection.getStartPosition(), pictureId);
        this.setSelectionPosition(position);
    }

    /**
     * x, y좌표에 해당하는 지점의 포지션을 반환합니다.
     * @param {Number} x 
     * @param {Number} y 
     * @returns {Position}
     */
    getPositionFromPoint(x, y) {
        let targetHTMLElement = document.elementFromPoint(x, y);
        if (!util.isInSEContents(targetHTMLElement, this.editorId)) {
            targetHTMLElement = this.elSEContents;
        }
        let paragraphHTMLElement = util.getParagraphHTMLElementFromPoint(targetHTMLElement, x, y);
        return this.getPositionFromParagraphElement(paragraphHTMLElement, x, y);
    }

    /**
     * x, y좌표에 해당하는 지점에 캐럿을 찍은후 셀렉션을 반환합니다.
     * @param {Number} x 
     * @param {Number} y 
     * @returns {Selection}
     */
    getSelectionFromPoint(x, y) {
        let selection = new Selection(this.positionManager);
        let position;
        if (position = this.getPositionFromPoint(x, y)) {
            selection.setCaret(position);
            return selection;
        }
        return null;
    }

    /**
     * 위/아래 이동시 x좌표값을 기억하고 있습니다.
     */
    setStandardLeft() {
        let focusRect = this.selection.focus.getRect(this.editorId);
        this.standardLeft = focusRect.left;
    }

    /**
     * left key event이벤트 발생시 이전 Paragraph 혹은 이전 position으로 position 변경 및 Caret을 이동 시킵니다.
     * @param {Boolean} [isExtend = false] 확장 selection인지 여부
     */
    left(isExtend = false) {
        let position;
        let focus = this.selection.focus;
        if (!this.selection.isCollapsed() && !isExtend) {
            position = this.selection.getStartPosition();
        } else if (this.selection.isCellSelection()) {
            position = this.positionManager.getLeftPositionFromCellPosition(focus);
        } else {
            position = this.positionManager.getLeftPosition(focus);
        }
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * right key event이벤트 발생시 다음 Paragraph 혹은 다음 position으로 position 변경 및 Caret을 이동 시킵니다.
     * @param {Boolean} [isExtend = false] 확장 selection인지 여부
     */
    right(isExtend = false) {
        let position;
        let focus = this.selection.focus;
        if (!this.selection.isCollapsed() && !isExtend) {
            position = this.selection.getEndPosition();
        } else if (this.selection.isCellSelection()) {
            position = this.positionManager.getRigthPositionFromCellPosition(focus);
        } else {
            position = this.positionManager.getRightPosition(focus);
        }

        this.setSelectionPosition(position, isExtend);
    }

    /**
     * Mouse Event가 발생한 좌표에 해당하는 Position을 반환합니다.
     * @param {HTMLElement} HTMLElement 
     * @param {Number} x x좌표
     * @param {Number} y y좌표
     * @returns {Position}
     */
    getPositionFromParagraphElement(paragraphHTMLElement, x, y) {
        if (!paragraphHTMLElement) {
            return;
        } else {
            let point = util.getClosestHTMLElementPoint(paragraphHTMLElement, x, y);
            x = point.x;
            y = point.y;
        }

        let paraId = util.getHTMLElementId(paragraphHTMLElement);
        let offset = 0;
        if (util.isTableHTMLElement(paragraphHTMLElement) || util.isHrHTMLElement(paragraphHTMLElement)) {
            let {left, width} = paragraphHTMLElement.getBoundingClientRect();
            let middleX = left + width / 2;
            if (middleX < x) {
                offset = 1;
            }
        } else {
            let {span, rect} = this.getHTMLElementInfoFromPoint(paragraphHTMLElement, x, y);
            let textNode = span.firstChild;
            let range = document.createRange();

            if (util.isTextNode(textNode) && !util.isEmptyTextNode(textNode)) {
                let prevMiddleX = 0;
                for (; offset < textNode.length; offset++) {
                    range.setStart(textNode, offset);
                    range.setEnd(textNode, offset+1);
                    let {top, bottom, left, right, width, height} = range.getBoundingClientRect();
                    let middleX = left + width / 2;
                    let middleY = top + height / 2;
        
                    if (rect.top < middleY && middleY <= rect.bottom) { // top 비교
                        if (prevMiddleX < x && x <= middleX) {
                            break;
                        }
                        prevMiddleX = middleX;
                    } else if (rect.bottom < middleY) { // top을 넘어갈 경우
                        offset--;
                        break;
                    }
                }
            } else if (util.isPictureHTMLElement(span)) {
                const {left, right} = rect;
                const width = right - left;
                const middleX = left + width / 2;
                offset += middleX < x ? util.getHTMLSpanLength(span) : 0;
            }
            offset += util.getHTMLSpanOffset(span);
        }

        return new Position(paraId, offset);
    }

    /**
     * Mouse Event가 발생한 x, y좌표 기준으로 pargraph의 각 줄간격을 계산하여 해당하는 HTML Element와 rect값을 반환합니다.
     * @param {HTMLElement} paragraphHTMLElement 
     * @param {Number} x x좌표
     * @param {Number} y y좌표
     * @returns {Object} { 'span' : spanHTMLElement,
     *                     'rect' : {
     *                          'top' : 윗줄의 guideLine,
     *                          'bottom' : 현재줄의 guideLine,
     *                          'left' : spanHTMLElement의 left,
     *                          'right' : spanHTMLElement의 right
     *                      }
     *                   }
     */
    getHTMLElementInfoFromPoint(paragraphHTMLElement, x, y) {
        let result = null;
        let lineInfos = this.getLineInfos(paragraphHTMLElement);
        let {top, bottom, left, right} = paragraphHTMLElement.getBoundingClientRect();

        for (let line of lineInfos) {
            bottom = line.guideLine;
            if (top <= y && y <= bottom) {
                for (let {span, rect} of line.spanInfos) {
                    result = {
                        'span' : span,
                        'rect' : {
                            'top' : top,
                            'bottom' : bottom,
                            'left' : rect.left,
                            'right' : rect.right
                        }
                    };
                    if (x <= rect.right) {
                        break;
                    }
                }
                break;
            }
            top = bottom;
        }
        
        return result;
    }

    /**
     * line 정보를 반환합니다.
     * @param {HTMLElement} paragraphHTMLElement paragraph level THMLElement
     * @returns {Array} [{ 'spanInfos' : [{ 'span' : span(HTMLElement),
     *                                      'rect' : rect(라인에 해당하는 span의 rect),
     *                                    }, ...],
     *                     'top' : 라인의 top
     *                     'bottom' : 라인의 bottom,
     *                     'left' : 라인의 left,
     *                     'right' : 라인의 right
     *                   }, ...]
     */
    getLineInfos(paragraphHTMLElement) {
        let lineInfos = [];
        let lineCount = 0;
        let prevRight = -Infinity;
        let addLine = (index, span, rect) => {
            if (!lineInfos[index]) { // 새로운 줄이 추가됨
                lineInfos[index] = {
                    spanInfos:[],
                    top:Infinity,
                    bottom:-Infinity,
                    left:Infinity,
                    right:-Infinity,
                    guideLine:-Infinity
                };
            };
            let prevLine = lineInfos[index - 1];
            let line = lineInfos[index];
            line.spanInfos.push({
                'span' : span,
                'rect' : rect
            });
            
            line.top = Math.min(line.top, rect.top);
            line.bottom = Math.max(line.bottom, rect.bottom);
            line.left = Math.min(line.left, rect.left);
            line.right = Math.max(line.right, rect.right);

            if (prevLine) {
                let gap = line.top - prevLine.bottom;
                prevLine.guideLine = prevLine.bottom + (gap/2);
            }
        };

        let complete = (paragraphHTMLElementBottom) => {
            let lastIndex = lineInfos.length - 1;
            lineInfos[lastIndex].guideLine = paragraphHTMLElementBottom;
        };

        if (util.isTableHTMLElement(paragraphHTMLElement) || util.isHrHTMLElement(paragraphHTMLElement)) {
            let rect = paragraphHTMLElement.getBoundingClientRect();
            addLine(lineCount, paragraphHTMLElement, rect);
            complete(rect.bottom);
        } else {
            let runsLevelSapns = Array.from(paragraphHTMLElement.children).reduce((spans, childElement) => {
                let runsLevelChildren = [childElement];
                if (util.isAHTMLElement(childElement)) {
                    runsLevelChildren = Array.from(childElement.children);
                }
                return spans.concat(runsLevelChildren);
            }, []);
    
            // lineInfos 정보 셋팅
            for (let span of runsLevelSapns) {
                for (let rect of Array.from(span.getClientRects())) {
                    // firefox에서 이전 span의 right가 다음 span의 left보다 크게옴
                    // 소수점 8자리이하가 약간달라 소수점 2번째 자리까지만 비교하도록 수정
                    if (util.round(rect.left, 2) < util.round(prevRight, 2)) { // 줄이 변경됨
                        lineCount++;
                    };
                    addLine(lineCount, span, rect);
                    prevRight = rect.right + (util.isBrHTMLElement(span) ? 1 : 0);
                }
                complete(paragraphHTMLElement.getBoundingClientRect().bottom);
            }
        }
        return lineInfos;
    }

    /**
     * 현재 셀랙션에서의 모든 line정보를 반환합니다.
     * @param {Boolean} [deep = false] table안쪽까지 깊이 탐색할지 여부
     * @returns {Array} [lineInfo, ...] lineInfo 배열
     */
    getLineInfosFromSelection(deep = false) {
        return this.selection.getParagraphs(deep).reduce((lineInfos, para) => {
            let HTMLElement = util.getHTMLElement(this.editorId, para.getId());
            return lineInfos.concat(...this.getLineInfos(HTMLElement));
        }, []);
    }

    /**
     * line의 가장 앞으로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    lineStart(isExtend = false) {
        let paragraphHTMLElement = util.getHTMLElement(this.editorId, this.selection.focus.id);
        let paraLeft = paragraphHTMLElement.getBoundingClientRect().left + 1;
        let {top, height} = this.getFocusLineRect();
        let middleY = top + (height / 2);
        let position = this.getPositionFromParagraphElement(paragraphHTMLElement, paraLeft, middleY);

        this.setSelectionPosition(position, isExtend);
    }

    /**
     * line의 가장 뒤로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    lineEnd(isExtend = false) {
        let paragraphHTMLElement = util.getHTMLElement(this.editorId, this.selection.focus.id);
        let paraRight = paragraphHTMLElement.getBoundingClientRect().right - 1;
        let {top, height} = this.getFocusLineRect();
        let middleY = top + (height / 2);
        let position = this.getPositionFromParagraphElement(paragraphHTMLElement, paraRight, middleY);

        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 문단에서 focus가 몇번째 줄인지에 대한 정보를 반환합니다.
     * @returns {Object} {
     *                       isFirst: 첫번째 줄인지 여부(Boolean),
     *                       isLast: 마지막 줄인지 여주(Boolean),
     *                       line: 현재줄(Number : 0부터 시작)
     *                   }
     */
    getCurrentLineInfo() {
        let {top, height} = this.selection.focus.getRect(this.editorId);
        let middleY = top + (height/2);
        let paragraphHTMLElement = util.getHTMLElement(this.editorId, this.selection.focus.id);
        let lineInfos = this.getLineInfos(paragraphHTMLElement);
        let lineCount = 0;
        for (let lineInfo of lineInfos) {
            if (middleY < lineInfo.guideLine) {
                break;
            }
            ++lineCount;
        }
        return {
            isFirst: lineCount === 0,
            isLast: lineCount === (lineInfos.length - 1),
            line: lineCount
        };
    }

    /**
     * 위쪽으로 순회하며, 다음 paragraph를 반환합니다.
     * @param {Number} y
     * @param {HTMLElement} [targetParaHTMLElement] 기준이되는 HTML element(Paragraph level)
     * @returns {HTMLElement} HTML element(paragraph level)
     */
    getUpParagraphHTMLElement(y, targetParaHTMLElement) {
        let seContentsTop = this.elSEContents.getBoundingClientRect().top;
        let prevParaHTMLElement;
        while(seContentsTop < y) {
            prevParaHTMLElement = util.getParagraphHTMLElementFromPoint(this.elSEContents, this.standardLeft, y--);
            if (!!prevParaHTMLElement && prevParaHTMLElement !== targetParaHTMLElement) {
                break;
            }
        }
        return prevParaHTMLElement;
    }

    /**
     * 아래쪽으로 순회하며, 다음 paragraph를 반환합니다.
     * @param {Number} y
     * @param {HTMLElement} [targetParaHTMLElement] 기준이되는 HTML element(Paragraph level)
     * @returns {HTMLElement} HTML element(paragraph level)
     */
    getDownParagraphHTMLElement(y, targetParaHTMLElement){
        let seContentsBottom = this.elSEContents.getBoundingClientRect().bottom;
        let nextParaHTMLElement;
        while(y < seContentsBottom) {
            nextParaHTMLElement = util.getParagraphHTMLElementFromPoint(this.elSEContents, this.standardLeft, y++);
            if (!!nextParaHTMLElement && nextParaHTMLElement !== targetParaHTMLElement) {
                break;
            }
        }
        return nextParaHTMLElement;
    }

    /**
     * 셀랙션을 위로 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    up(isExtend = false) {
        let focus = this.selection.focus;
        let paragraphHTMLElement, y, position;
        if (this.selection.isCellSelection()) {
            let upPosition = this.positionManager.getUpPositionFromCellPosition(focus);
            paragraphHTMLElement = util.getHTMLElement(this.editorId, upPosition.id);
            y = paragraphHTMLElement.getBoundingClientRect().bottom - 1;
        } else {
            paragraphHTMLElement = util.getHTMLElement(this.editorId, focus.id);
            y = this.getFocusLineRect().top - 1;
            if (this.getCurrentLineInfo().isFirst) {
                paragraphHTMLElement = this.getUpParagraphHTMLElement(y, paragraphHTMLElement);
                if (!paragraphHTMLElement) {
                    return;
                }
                y = paragraphHTMLElement.getBoundingClientRect().bottom - 1;
            }
        }

        position = this.getPositionFromParagraphElement(paragraphHTMLElement, this.standardLeft, y);
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 셀랙션을 아래로 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    down(isExtend = false) {
        let focus = this.selection.focus;
        let paragraphHTMLElement, y, position;
        if (this.selection.isCellSelection()) {
            let downPositioin = this.positionManager.getDownPositionFromCellPosition(focus);
            paragraphHTMLElement = util.getHTMLElement(this.editorId, downPositioin.id);
            y = paragraphHTMLElement.getBoundingClientRect().top + 1;
        } else {
            paragraphHTMLElement = util.getHTMLElement(this.editorId, focus.id);
            y = this.getFocusLineRect().bottom + 1;
            if (this.getCurrentLineInfo().isLast) {
                paragraphHTMLElement = this.getDownParagraphHTMLElement(y, paragraphHTMLElement);
                if (!paragraphHTMLElement) {
                    return;
                }
                y = paragraphHTMLElement.getBoundingClientRect().top + 1;
            }
        }
        
        position = this.getPositionFromParagraphElement(paragraphHTMLElement, this.standardLeft, y);
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 스크롤 위로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    pageUp(isExtend = false) {
        let pageUpY = this.getFocusLineRect().top - (this.elSEMain.clientHeight);
        let paragraphHTMLElement = this.getUpParagraphHTMLElement(pageUpY);
        let position, y;
        if (!paragraphHTMLElement) {
            position = this.positionManager.documentStart(); 
        } else {
            position = this.getPositionFromParagraphElement(paragraphHTMLElement, this.standardLeft, pageUpY);
        }
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 스크롤 아래로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    pageDown(isExtend = false) {
        let pageDownY = this.getFocusLineRect().bottom + (this.elSEMain.clientHeight);
        let paragraphHTMLElement = this.getDownParagraphHTMLElement(pageDownY);
        let position, y;
        if (!paragraphHTMLElement) {
            position = this.positionManager.documentEnd(); 
        } else {
            position = this.getPositionFromParagraphElement(paragraphHTMLElement, this.standardLeft, pageDownY);
        }
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 편집모델의 가장 앞으로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    documentStart(isExtend = false) {
        let position = this.positionManager.documentStart(); 
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 편집모델의 가장 뒤로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    documentEnd(isExtend = false) {
        let position = this.positionManager.documentEnd(); 
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 이전 문단의 가장 앞으로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    previousParagraphStart(isExtend = false) {
        let position = this.positionManager.getPreviousParagraphStartPosition(this.selection.focus); 
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 다음 문단의 가장 앞으로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    nextParagraphStart(isExtend = false) {
        let position = this.positionManager.getNextParagraphStartPosition(this.selection.focus);
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 이전 단어의 가장 앞으로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    previousWord(isExtend = false) {
        let position = this.positionManager.getPreviousWordPosition(this.selection.focus); 
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * 다음 단어의 가장 앞으로 셀랙션을 수정합니다.
     * @param {Boolean} [isExtend = false] selection 확장여부
     */
    nextWord(isExtend = false) {
        let position = this.positionManager.getNextWordPosition(this.selection.focus);
        this.setSelectionPosition(position, isExtend);
    }

    /**
     * focus의 위치에 해당하는 line의 top, bottom이 적용된 rect값을 반환합니다.
     * @returns {Object} rect형태의 Object
     */
    getFocusLineRect() {
        let paragraphHTMLElement = util.getHTMLElement(this.editorId, this.selection.focus.id);
        let {top, bottom} = paragraphHTMLElement.getBoundingClientRect();
        let focusRect = this.selection.focus.getRect(this.editorId);
        let middleY = focusRect.top + (focusRect.height / 2);
        let lineInfos = this.getLineInfos(paragraphHTMLElement);
        for (let line of lineInfos) {
            bottom = line.guideLine;
            if (top <= middleY && middleY <= bottom) {
                break;
            }
            top = bottom;
        }

        return {
            left: focusRect.left,
            right: focusRect.right,
            top: top,
            bottom: bottom,
            width: focusRect.right - focusRect.left,
            height: bottom - top
        };
    }

    /**
     * startPosition과 endPosition이 HyperlinkSelection에 있다면 HyperlinkSelection으로 변경합니다.
     */
    expendHyperlinkSelection() {
        const hyperlink = this.getHyperlink();
        if (hyperlink) {
            const startPosition = this.positionManager.getRunStartPosition(hyperlink.hyperlinkStart);
            const endPosition = this.positionManager.getRunEndPosition(hyperlink.hyperlinkEnd);

            this.selection.set(startPosition, endPosition);
        }
    }

    /**
     * Selection에 포함되는 하이퍼링크 정보를 반환합니다.
     * @return {Object} {id, URL, text}
     */
    getHyperlinkData() {
        let hyperlinkData = { id: null, URL: '', text: '' };
        let hyperlink = null;

        if (this.selection.isPictureSelection()) {
            const picId = this.selection.anchor.pictureId;
            const picture = this.SEModelManager.getPicture(picId);

            hyperlinkData.URL = picture.getHyperlinkTarget();
        } else {
            hyperlink = this.getHyperlink();
            if (!!hyperlink) {
                hyperlinkData.id = hyperlink.hyperlinkStart.id;
                hyperlinkData.URL = hyperlink.hyperlinkStart.getTarget();
            }
            hyperlinkData.text = this.selection.getText();
        }

        return hyperlinkData;
    }

    /**
     * Selection 정보에 해당하는 Hyperlink를 반환합니다.
     * @return {Object} Hyperlink
     */
    getHyperlink() {
        const hyperlinkMap = this.SEModelManager.getHyperlinkMap();
        let startPosiion = this.selection.getStartPosition();
        let endPosition = this.selection.getEndPosition();
        let hyperlink = null;
        let isCollapsed = this.selection.isCollapsed();

        for (const id in hyperlinkMap) {
            const {hyperlinkStart, hyperlinkEnd} = hyperlinkMap[id];
            const hyperlinkStartPosition = this.positionManager.getRunStartPosition(hyperlinkStart);
            const hyperlinkEndPosition = this.positionManager.getRunEndPosition(hyperlinkEnd);
            const hyperlinkSelection = this.selection.clone();

            hyperlinkSelection.set(hyperlinkStartPosition, hyperlinkEndPosition);

            // 캐럿상태인경우 startPosition은 하이퍼링크 범위에 포함하지 않는다.
            if (this.selection.isCollapsed() && hyperlinkStartPosition.isEqual(startPosiion)) {
                continue;
            }
            if (hyperlinkSelection.isContain(startPosiion) && hyperlinkSelection.isContain(endPosition)) {
                hyperlink = hyperlinkMap[id];
                break;
            }
        }

        return hyperlink;
    }
}

export default SelectionManager;


