import Element from './Element';
 
/**
 * Body
 * @extends Element
 */
class Body extends Element {

    /**
     * childrenPropety를 반환합니다.
     * @private
     * @returns {String} children
     */
    __getChildrenProperty__() {
        return 'children';
    }

    /**
     * Element의 Type을 반환합니다.
     * @returns {String} Body
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'Body';
    }
}

export default Body;


