import util from '../util/util';
import $ from 'jquery';

/**
 * 가로줄(HR)을 렌더링 하는 Class입니다.
 */
class HorizontalLineRenderer {
    constructor(renderer) {
        this.parent = renderer;
    }

    /**
     * 상위 에디터 객체를 반환합니다.
     * @returns {Object} - 에디터 객체
     */
    getEditor() {
        return this.parent.editor;
    }

    /**
     * 가로줄을 랜더링 하기 위해 HorizontalLine Element를 받아 HTML을 반환합니다.
     * @param {HorizontalLine} horizontalLine
     * @returns {String} HTML 형태의 문자열
     */
    getHTML(horizontalLine) {
        let $horizontalLine;
        if (horizontalLine) {
            let prop = this.getProp(horizontalLine);
            let height = util.sunit2pt(util.get(prop, ['height_in_sunit']));
            let color = 'black';
            $horizontalLine = $(`<hr>`);
            
            return $horizontalLine.prop('outerHTML');
        }
    }

    /**
     * horizontalLine의 속성 정보를 반환합니다.
     * @private
     * @param {Object} model 
     * @returns {Object} prop
     */
    getProp(model) {
        return model.prop;
    }
}

export default HorizontalLineRenderer;


