import $ from 'jquery';
import consts from '../../consts/consts';
import ACTION from '../../consts/actionMap';
import PopupInterface from './PopupInterface';

const UI_NAME = consts.UI_NAME;

/**
 * LinkPopup 클래스입니다.
 * Popup 클래스를 상속 받습니다.
 * @extends {PopupInterface}
 */
class LinkPopup extends PopupInterface {
    /**
     * Creates an instance of LinkPopup.
     * @param {String} editorId - 에디터를 식별할 수 있는 아이디
     * @param {Object} controller - controller 객체
     */
    constructor(editorId, controller) {
        super(editorId);

        this.editorId = editorId;
        this.controller = controller;

        this.linkId = null;
        this.selectedTab = null;
        this.beforeText = '';

        this.$element = this.__createDom__();
        this.$elPopupBody = this.$element.find('.se-popup-body');
        this.elURL = this.$elPopupBody.find('.se-link-attr')[0];
        this.elText = this.$elPopupBody.find('.se-link-attr')[1];

        this.__attachEvent__();
    }

    /**
     * Link Popup URL, Text 정보를 초기화합니다.
     */
    __initValue__() {
        this.elURL.value = '';
        this.elText.value = '';
        this.beforeText = '';
        this.linkId = null;
    }

    /**
     * LinkPopup Dom을 생성합니다.
     * @private
     */
    __createDom__() {
        const popupHTML = `
            <div class='se-popup' name="${UI_NAME.LINK}">
                <div class='se-popup-arrow'></div>
                <div class="se-popup-header">
                    <div class="se-tab" value="url"><span>${consts.LINK_TAB.URL}</span></div>
                    <div class="se-tab" value="bookmark"><span>${consts.LINK_TAB.BOOKMARK}</span></div>
                </div>
                <div class="se-popup-body">
                    <div class="link-url">
                        <div>주소(URL)</div>
                        <input type="text" class="se-input-box se-link-attr" placeholder="URL">
                    </div>
                    <div class="link-text">
                        <div>텍스트</div>
                        <input type="text" class="se-input-box se-link-attr" placeholder="Text">
                    </div>
                </div>
                <div class="se-popup-bar"></div>
                <div class='se-popup-footer'>
                    <div class='se-cancel se-btn'><span>취소</span></div>
                    <div class='se-confirm se-btn'><span>확인</span></div>
                </div>
            </div>
        `;

        const $popup = $(popupHTML);
        const $popupContainer = $('#' + this.editorId).find('.se-popup-container');
        $popupContainer.append($popup);

        return $popup;
    }

    /**
     * 이벤트를 등록합니다.
     */
    __attachEvent__() {
        $('.se-confirm').bind('click', this.__confirm__.bind(this));
        $('.se-cancel').bind('click', this.__cancel__.bind(this));
        $('.se-tab').bind('click', this.__selectTab__.bind(this));
    }

    /**
     * 확인 버튼이 눌렸을 때 동작합니다.
     * @private
     */
    __confirm__() {
        if (this.selectedTab === consts.LINK_TAB.URL) {
            const text = this.beforeText === this.elText.value ? null : this.elText.value;

            if (this.linkId) {
                this.controller.execute(ACTION.UPDATE_LINK, this.linkId, this.elURL.value, text);
            } else {
                this.controller.execute(ACTION.INSERT_LINK, this.elURL.value, text);
            }
        }
        this.hide();
        this.__initValue__();
    }

    /**
     * 취소 버튼이 눌렸을 때 동작합니다.
     * @private
     */
    __cancel__() {
        this.hide();
        this.__initValue__();
    }

    /**
     * 탭을 선택합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __selectTab__(e) {
        const $elTab = $(e.currentTarget);

        if ($elTab.attr('value') === consts.LINK_TAB.URL) {
            this.__selectUrlTab__();
        } else {
            this.__selectBookmarkTab__();
        }
    }

    /**
     * URL 탭을 선택합니다.
     * @private
     */
    __selectUrlTab__() {
        this.selectedTab = consts.LINK_TAB.URL;
    }

    /**
     * Bookmark 탭을 선택합니다.
     * @private
     */
    __selectBookmarkTab__() {
        this.selectedTab = consts.LINK_TAB.BOOKMARK;
    }

    /**
     * Popup Element를 보여줍니다.
     * @param {HTMLElement} elTarget event Target
     * @param {Object} linkData {id, URL}
     * @param {Boolean} [isPicture=false]
     */
    show(elTarget, linkData, isPicture = false) {
        super.show(elTarget);

        this.setHyperlinkData(linkData);
        this.__selectUrlTab__();

        if (isPicture) {
            this.$elPopupBody.find('.link-text').addClass('hide');
        }
    }

    /**
     * Popup Element를 숨깁니다.
     * @private
     */
    hide() {
        super.hide();
        this.$elPopupBody.find('.link-text').removeClass('hide');
    }

    /**
     * Popup Element를 토글합니다.
     * @param {HTMLElement} elTarget event Target
     * @param {Object} linkData {id, URL}
     * @param {Boolean} [isPicture=false]
     */
    toggle(elTarget, linkData, isPicture = false) {
        if (this.$element.hasClass('show')) {
            this.hide();
        } else {
            this.show(elTarget, linkData, isPicture);
        }
    }

    /**
     * 링크 팝업정보를 세팅합니다.
     * @param {Object} linkData
     */
    setHyperlinkData(linkData) {
        this.linkId = linkData.id;
        this.beforeText = linkData.text;
        this.elText.value = linkData.text;
        this.elURL.value = linkData.URL;
    }
}

export default LinkPopup;


