/**
 * 지역화를 관리하는 class입니다.
 */
const localeManager = {
    /**
     * 기본 언어, 사용자가 설정한 언어, 브라우저에 설정된 언어를 확인하여
     * 에디터에 적용되어야 할 언어가 무엇인지를 반환합니다.
     * @param {String} defaultLanguage 사용자가 설정한 기본 언어
     * @param {String} userLanguage 사용자가 설정한 언어
     * @returns {String} 적용되어야 할 언어 (ko, en, ja ...)
     */
    getLanguage: function (defaultLanguage, userLanguage) {
        // userLanguage로 설정된 언어가 있으면 설정된 언어를 보여줍니다.
        if (!!window.SE_LOCALE_MESSAGES[userLanguage]) {
            return userLanguage;
        }

        // 설정된 언어가 따로 없으면 브라우저 언어를 참고합니다.
        const browserLanguage = this.getBrowserLanguage();
        if (!!window.SE_LOCALE_MESSAGES[browserLanguage]) {
            return browserLanguage;
        }

        // 설정된 언어가 따로 없거나 언어팩이 존재하지 않고, 브라우저 언어에 맞는 언어팩이 존재하지 않으면
        // defaultLanguage로 설정된 언어로(설정이 안되어있다면 영어로) 보여줍니다.
        return !!window.SE_LOCALE_MESSAGES[defaultLanguage] ? defaultLanguage : 'en';
    },

    /**
     * 브라우저에 설정된 언어 정보를 가져옵니다.
     * @returns {String} 브라우저 언어 정보 (ko, en, ja, ...)
     */
    getBrowserLanguage: function () {
        const lang = navigator.language || navigator.userLanguage;

        return lang.substring(0, 2);
    },

    /**
     * 에디터에 설정된 언어의 메세지를 가져옵니다.
     * @param {String} editorId 에디터 ID
     * @returns {Object|undefined} 메세지 목록
     */
    getLocaleMessages: function (editorId) {
        const info = window.SE_INFO[editorId];

        if (!!info) {
            return window.SE_LOCALE_MESSAGES[info.language];
        }
    }
};

export default localeManager;


