import DrawingObject from './DrawingObject';
/**
 * HorizontalLine
 * @extends Element
 */
class HorizontalLine extends DrawingObject {
    /**
     * new HorizontalLine 호출시 Element 생성자에서 __default__()로 기본 HorizontalLine 속성 적용할 수 있도록 속성을 반환합니다.
     * @private
     * @returns {Object} 기본 HorizontalLine 속성을 Object
     */
    __default__() {
        return {
            prop: {
                color: {
                    type: 'SRGB',
                    srgb_color: {
                        value_in_hex: 0
                    }
                },
                height_in_sunit: 1500,
                width: {
                    type: 'percent',
                    value_in_percent: 100000
                }
            }
        };
    }
    /**
     * Element의 Type을 반환합니다.
     * @returns {String} Picture
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'HorizontalLine';
    }
}

export default HorizontalLine;


