import $ from 'jquery';

const MIN_HANDLER_SIZE = 20;

/**
 * Picture Selection을 렌더링하는 Class 입니다.
 */
class PictureHandler {
    constructor(editorId) {
        this.editorId = editorId;
        this.$elMain = $(`#${this.editorId} .se-main`);
        this.$elPictureSelection = $(`#${this.editorId} .se-picture-selection`);
        this.$elPicture = null;
        this.init();
    }

    /**
     * Picture Selection을 초기화 합니다.
     */
    init() {
        if (!this.$elPictureSelection.length) {
            this.$elPictureSelection = $(`<div class="se-picture-selection">
            <div class="se-picture-handle middle top"></div>
            <div class="se-picture-handle middle bottom"></div>
            <div class="se-picture-handle left top"></div>
            <div class="se-picture-handle left middle"></div>
            <div class="se-picture-handle left bottom"></div>
            <div class="se-picture-handle right top"></div>
            <div class="se-picture-handle right middle"></div>
            <div class="se-picture-handle right bottom"></div></div>`);
            
            this.$elMain.append(this.$elPictureSelection);
        }
        this.hide();
    }

    /**
     * Picture Selection을 보이게합니다.
     */
    show(elObject) {
        this.$elPictureSelection.addClass('show');
        this.$elPictureSelection.attr('select-pic-id', elObject.getAttribute('pic-id'));
        this.$elPicture = $(`#${this.editorId} [pic-id="${elObject.getAttribute('pic-id')}"]`);
        this.setSelection(elObject);
    }

    /**
     * Picture Selection을 숨깁니다.
     */
    hide() {
        this.$elPictureSelection.removeClass('show');
        this.$elPictureSelection.attr('select-pic-id', '');
        this.$elPicture = null;
    }

    /**
     * Picture Selection을 HTML Element에 맞게 위치와 크기를 설정합니다.
     * @param {HTMLElement} elPicture 
     */
    setSelection(elPicture) {
        const elMain = this.$elMain.get(0);
        const seMainRect = elMain.getBoundingClientRect();
        const rect = elPicture.getBoundingClientRect();
        const width = Math.max(rect.right - rect.left, MIN_HANDLER_SIZE);
        const height = Math.max(rect.bottom - rect.top, MIN_HANDLER_SIZE);
        const reviseLeft = (width - rect.width) / 2;
        const reviseTop = (height - rect.height) / 2;

        this.$elPictureSelection.css({
            left: rect.left - seMainRect.left - reviseLeft,
            top: rect.top - seMainRect.top + elMain.scrollTop - reviseTop,
            width: width,
            height: height
        });
    }
    
    /**
     * movePosition 좌표에 따라 Picture 크기를 조절합니다.
     * @param {Number} width 
     * @param {Number} height
     */
    resize(width, height) {
        this.$elPicture.width(width);
        this.$elPicture.height(height);
        this.setSelection(this.$elPicture.get(0));
    }
}

export default PictureHandler;



