import Element from './Element';
import TextRun from './TextRun';

const DrawingObjectLength = 1;

/**
 * DrawingObject
 * @extends Element
 */
class DrawingObject extends Element {
    /**
     * Element의 Type을 반환합니다.
     * @returns {String} Body String
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'DrawingObject';
    }

    length() {
        return DrawingObjectLength;
    }

    /**
     * 나중의 export를 고려하여, SEModel을 docModel 형태로 만들기 위한 함수입니다.<br/>
     * DrawingObject Element는 TextRun을 상속받지 않고 있기 때문에, <br/>
     * 직접 textrun으로 감싸주어 docModel의 textrun형태로 만들어줍니다.<br/>
     * 최종적으로 docModel 형태의 drawingobject를 만들어서 JSON Object를 반환합니다.
     * @returns {Object} drawingobject JSON Object
     */
    toPbModel(graphicObj) {
        let textrun = {
            "textrun": {
                "drawing_obj": {
                    "element_id": this.element_id,
                    "layout_prop": this.layout_prop,
                    "graphic_obj": graphicObj
                }
            },
            "type": "TEXTRUN"
        };

        if (this.textProp) { // 동영상의 경우 textrun의 prop이 undefined
            textrun.textrun.prop = this.textProp;
        }
        return textrun;
    }

    /**
     * DrawingObject split시 index에 따라 빈 TextRun 생성하여 반환합니다.
     * @param {Number} index
     * @returns {TextRun} TextRun
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }
}

export default DrawingObject;


