import $ from 'jquery';
import UIDefinition from '../ui/UIDefinition';
import Separator from '../ui/Separator';
import Dropdown from '../ui/Dropdown';
import Button from '../ui/Button';
import ColorPicker from '../ui/popup/ColorPicker';
import LinkPopup from '../ui/popup/LinkPopup';
import TablePopup from '../ui/popup/TablePopup';
import BalloonPopup from '../ui/popup/BalloonPopup';
import ImagePopup from '../ui/popup/ImagePopup';
import consts from '../consts/consts';
import CharacterPicker from "../ui/dialog/CharacterPicker";
import StartValueDialog from "../ui/dialog/StartValueDialog";

const UI_NAME = consts.UI_NAME;

/**
 * 툴바 등 전반적인 에디터의 UI를 관리합니다.
 * @example
 * var uiManager = new UIManager();
 */
class UIManager {
    /**
     * UIManager를 생성합니다.
     * @param {String} editorId 에디터 고유 아이디
     * @param {Controller} controller 액션 컨트롤러
     */
    constructor(editorId, controller) {
        this.editorId = editorId;
        this.controller = controller; //액션 컨트롤러입니다.

        this.UI_DEFINITION = UIDefinition.generate(editorId);

        // 생성된 버튼을 관리하는 맵입니다. 형식은 아래와 같습니다.
        // 형식 예)) {'bold': [UI instance, UI instance, ...], 'italic': [UI instance], ..}
        this.buttonMap = {};

        // 풍선 도구를 관리하는 맵입니다. 형식은 아래와 같습니다.
        // 형식 예)) {'textBalloon' : Balloon}
        this.balloons = {};

        // 팝업을 관리하는 맵입니다.
        this.popups = {};

        // 다이얼로그를 관리하는 맵입니다.
        this.dialogs = {};

        this.$elMain = null;
    }

    /**
     * 세퍼레이터를 생성합니다.
     * @param {String} separatorName
     * @return {Separator} 생성된 세퍼레이터의 인스턴스
     */
    createSeparator(separatorName) {
        return new Separator(separatorName);
    }

    /**
     * 버튼을 생성합니다.
     * @param {String} buttonName 버튼 이름
     * @param {Object} buttonOption 버튼 옵션
     * @return {Button} 생성된 버튼의 인스턴스
     */
    createButton(buttonName, buttonOption) {
        return new Button(buttonName, buttonOption);
    }

    /**
     * 드롭다운을 생성합니다.
     * @param {String} dropdownName 드롭다운 이름
     * @param {Object} dropdownOption 드롭다운 옵션
     * @return {Dropdown} 생성된 드롭다운의 인스턴스
     */
    createDropdown(dropdownName, dropdownOption) {
        return new Dropdown(dropdownName, dropdownOption);
    }

    /**
     * 액션을 수행하는 함수를 생성하고 가져옵니다.
     * @param {String} actionName 액션 Map에 정의된 액션 이름
     * @param {*} actionValue 액션 수행시 인자로 넘겨줄 값입니다.
     * @returns {Function} 액션을 수행하는 함수
     */
    createActionWithValue(actionName, actionValue) {
        return this.controller.execute.bind(this.controller, actionName, actionValue);
    }

    /**
     * 액션을 수행하는 함수를 생성하고 가져옵니다.
     * @param {String} actionName 액션 Map에 정의된 액션 이름
     * @returns {Function} 액션을 수행하는 함수
     */
    createAction(actionName) {
        return this.controller.execute.bind(this.controller, actionName);
    }

    /**
     * 풍선 도구를 생성합니다.
     * @param {String} balloonName 풍선 도구 이름
     * @param {Array} uiComponentNames 풍선 도구에 생성할 버튼 이름 Array
     */
    createBalloon(balloonName, uiComponentNames) {
        const uiComponents = [];

        uiComponentNames.forEach((uiComponentName) => {
            const uiInstance = this.createUiInstance(uiComponentName);
            if (!uiInstance) {
                return;
            }

            uiComponents.push(uiInstance);
        });

        this.balloons[balloonName] = new BalloonPopup(this.editorId, balloonName, uiComponents);
    }

    /**
     * UI를 초기화합니다.
     * @param {Object} configuration
     */
    init(configuration) {
        this.initToolbar(configuration.toolbar);
        this.initBalloon();
        this.initPopup();
        this.initDialog();

        this.$elMain = $(`#${this.editorId}`).find('.se-main');
    }

    /**
     * 에디터 툴바를 생성합니다.
     * @param {Array} uiComponentNames 툴바에 생성할 버튼배열
     * @example
     * // html
     * <div id="toolbar"></div>
     * 
     * // script
     * var btnNameArray = ['bold', 'italic', '|', 'underline', 'strike', '-', 'image'];
     * 
     * new UIManager().initToolbar($('#toolbar'), btnNameArray);
     */
    initToolbar(uiComponentNames) {
        const $toolbarRoot = $('#' + this.editorId).find('.se-toolbar');

        let $elRow = $('<div class="se-toolbar-row"></div>');
        $toolbarRoot.append($elRow);

        uiComponentNames.forEach((uiComponentName) => {
            const uiInstance = this.createUiInstance(uiComponentName);
            if (!uiInstance) {
                return;
            }

            if (uiComponentName === UI_NAME.HORIZONTAL_SEPARATOR) {
                $elRow = $('<div class="se-toolbar-row"></div>');
                $toolbarRoot.append($elRow);
            } else {
                $elRow.append(uiInstance.getElement());
            }

            const def = this.UI_DEFINITION[uiComponentName]; //TODO 임시코드. 삭제필요
            this.doNoActionButtonBeRed(uiInstance, def); //TODO 임시코드. 삭제필요
        });
    }

    initBalloon() {
        const textBalloonBtns = [UI_NAME.FONT_FAMILY_TEXT_VERSION, UI_NAME.FONT_SIZE_TEXT_VERSION,
            UI_NAME.HORIZONTAL_SEPARATOR, UI_NAME.BOLD, UI_NAME.ITALIC, UI_NAME.UNDERLINE, UI_NAME.STRIKE, UI_NAME.FONT_COLOR, UI_NAME.FONT_BACKGROUND_COLOR];
        this.createBalloon(UI_NAME.TEXT_BALLOON, textBalloonBtns);

        const pictureBalloonBtns = [UI_NAME.ROTATE_PICTURE_LEFT, UI_NAME.ROTATE_PICTURE_RIGHT];
        this.createBalloon(UI_NAME.PICTURE_BALLOON, pictureBalloonBtns);

        const tableCellBalloonBtns = [UI_NAME.FILL, UI_NAME.SELECT_BORDER, UI_NAME.BORDER_COLOR, UI_NAME.LINE_THICKNESS, UI_NAME.LINE_STYLE, UI_NAME.MERGE_CELL, UI_NAME.HORIZONTAL_SEPARATOR, UI_NAME.ALIGN, UI_NAME.VERTICAL_ALIGN, UI_NAME.DELETE_ROW, UI_NAME.DELETE_COL, UI_NAME.CELL_PROPERTIES, UI_NAME.TABLE_PROPERTIES, UI_NAME.DELETE_TABLE];
        this.createBalloon(UI_NAME.TABLE_CELL_BALLOON, tableCellBalloonBtns);
    }

    /**
     * 팝업을 초기화 합니다.
     */
    initPopup() {
        this.popups[UI_NAME.LINK] = new LinkPopup(this.editorId, this.controller);
        this.popups[UI_NAME.IMAGE] = new ImagePopup(this.editorId, this.controller);
        this.popups[UI_NAME.INSERT_TABLE] = new TablePopup(this.editorId);
        this.popups[UI_NAME.COLOR_PICKER] = new ColorPicker(this.editorId, this.controller);
    }

    /**
     * 다이얼로그를 초기화합니다.
     */
    initDialog() {
        this.dialogs[UI_NAME.CHARACTER_PICKER] = new CharacterPicker(this.editorId, this.controller);
        this.dialogs[UI_NAME.START_VALUE_DIALOG] = new StartValueDialog(this.editorId, this.controller);
    }

    /**
     * 버튼맵에 생성된 버튼을 등록합니다.
     * @param {String} buttonName 키로 사용될 버튼 이름
     * @param {Button|Dropdown|Separator} uiInstance UI 인스턴스
     */
    addToButtonMap(buttonName, uiInstance) {
        if (!this.buttonMap[buttonName]) {
            this.buttonMap[buttonName] = [];
        }

        this.buttonMap[buttonName].push(uiInstance);
    }

    /**
     * UI definition에 해당하는 인스턴스를 가져옵니다
     * @private
     * @param {String} defName UI definition에 정의된 이름
     * @returns {BUTTON|DROPDOWN|SEPARATOR} UI 인스턴스
     */
    createUiInstance(defName) {
        const def = this.UI_DEFINITION[defName];
        if (!def) {
            return; // 정의되어있지 않은 버튼
        }

        const copiedDef = Object.assign({}, def);
        let uiInstance;

        switch (def.type) {
            case 'button':
                copiedDef.onClickFunc = this.getActionFunction(def.actionName, def.actionValue);
                uiInstance = this.createButton(defName, copiedDef);
                break;
            case 'dropdown':
                // dropdownItem에 action Mapping
                const dropdownItems = def.dropdownItems.map((item) => {
                    const copiedItem = Object.assign({}, item);
                    copiedItem.onClickFunc = this.getActionFunction(item.actionName, item.actionValue);
                    return copiedItem;
                });
                copiedDef.dropdownItems = dropdownItems;

                if (!!def.actionName) {
                    copiedDef.onClickFunc = this.getActionFunction(def.actionName, def.actionValue);
                }

                uiInstance = this.createDropdown(defName, copiedDef);
                break;
            case 'separator':
                uiInstance = this.createSeparator(defName);
                break;
        }

        this.addToButtonMap(defName, uiInstance);

        return uiInstance;
    }

    /**
     * actionName에 해당하는 함수를 반환합니다.
     * @private
     * @param {String} actionName 액션 이름
     * @param {String} [actionValue] 액션 파라미터 값
     * @returns {Function} actionName에 해당하는 함수
     */
    getActionFunction(actionName, actionValue) {
        if (!!actionValue) {
            return this.createActionWithValue(actionName, actionValue);
        } else {
            return this.createAction(actionName);
        }
    }

    /**
     * 풍선 도구들을 반환합니다.
     * @returns {Object} balloons
     */
    getBalloons() {
        return this.balloons;
    }

    /**
     * 이름에 해당하는 풍선 도구를 가져옵니다.
     * @param {String} balloonName 풍선 도구 이름
     * @returns {BalloonPopup} 풍선 도구
     */
    getBalloon(balloonName) {
        return this.balloons[balloonName];
    }

    /**
     * 팝업 인스턴스를 반환합니다.
     * @returns {LinkPopup}
     */
    getLinkPopup() {
        return this.popups[UI_NAME.LINK];
    }

    /**
     * Table Popup 인스턴스를 반환합니다.
     * @returns {TablePopup}
     */
    getTablePopup() {
        return this.popups[UI_NAME.INSERT_TABLE];
    }

    /**
     * Image Popup 인스턴스를 반환합니다.
     * @returns {ImagePopup}
     */
    getImagePopup() {
        return this.popups[UI_NAME.IMAGE];
    }

    /**
     * ColorPicker 인스턴스를 반환합니다.
     * @returns {ColorPicker}
     */
    getColorPicker() {
        return this.popups[UI_NAME.COLOR_PICKER];
    }

    /**
     * CharacterPicker 인스턴스를 반환합니다.
     * @returns {CharacterPicker}
     */
    getCharacterPicker() {
        return this.dialogs[UI_NAME.CHARACTER_PICKER];
    }

    /**
     * StartValueDialog 인스턴스를 반환합니다.
     * @returns {StartValueDialog}
     */
    getStartValueDialog() {
        return this.dialogs[UI_NAME.START_VALUE_DIALOG];
    }

    /**
     * 에디터의 풍선 도구를 초기화합니다.
     */
    doNoActionButtonBeRed(instance, def) {
        const $element = instance.getElement();

        if (def.type === 'dropdown') {
            let cnt = 0;
            def.dropdownItems.forEach((defItem, idx) => {
                if (!defItem.actionName) {
                    instance.dropdownItems[idx].getElement().addClass('label-danger');
                    cnt++;
                }
            });
            const className = cnt > 0 ? (cnt === def.dropdownItems.length ? 'btn-danger' : 'btn-warning') : '';
            $element.find('button').addClass(className);
        } else if (def.type === 'button' && !def.actionName) {
            $element.addClass('btn-danger');
        }
    }

    /**
     * target 위치에 맞게 풍선 팝업을 보여줍니다.
     * @param {HTMLElement} elTarget
     * @param {String} balloonName 
     */
    showBalloonPopup(elTarget, balloonName) {
        this.getBalloon(balloonName).show(elTarget);
    }

    /**
     * uiManager에 등록된 풍선 팝업들을 숨깁니다.
     * @param {String} targetBtnName
     */
    hideAllBalloonPopup(targetBtnName) {
        const balloons = this.getBalloons();

        for (let name in balloons) {
            if (name === targetBtnName) {
                continue;
            }
            balloons[name].hide();
        }
    }

    /**
     * 풍선 팝업을 포함한 모든 팝업을 숨깁니다.
     * 단, targetBtnName에서 열린 팝업은 숨기지 않습니다.
     * @param {String} targetBtnName
     */
    hideAllPopup(targetBtnName) {
        this.hideAllBalloonPopup(targetBtnName);

        //풍선 팝업을 제외한 팝업들을 처리합니다.
        for(let uiName in this.popups) {
            const popup = this.popups[uiName];

            if (targetBtnName) {
                if (targetBtnName === UI_NAME.FONT_COLOR || targetBtnName === UI_NAME.FONT_BACKGROUND_COLOR) {
                    targetBtnName = UI_NAME.COLOR_PICKER;
                }

                if (uiName === targetBtnName) {
                    continue;
                }
            }

            popup.hide(true);
        }
    }
}


export default UIManager;


