import util from '../util/util';
import $ from 'jquery';

/**
 * 이미지를 렌더링 하는 Class입니다.
 */
class PictureRenderer {
    constructor(renderer) {
        this.parent = renderer;
        this.editor = this.parent.editor;
        this.relationshipListManager = this.editor.getRelationshipListManager();
    }

    /**
     * 파라미터로 받은 Picture Element를 표현 할 HTML Element을 반환합니다.
     * @param {Picture} picture 
     * @returns {HTMLElement}
     */
    getHTML(picture) {
        if (picture) {
            const prop = this.getProp(picture);
            const type = this.getType(prop);
            const $elPicture = $(this.getElementByType(type));

            $elPicture.attr('src', this.getSrc(prop, type));
            $elPicture.attr('pic-id', picture.element_id);

            const cssText = this.getCssText(prop);

            if (!!cssText) {
                $elPicture.get(0).style.cssText = cssText;
            }

            return $elPicture.prop('outerHTML');
        }
    }
    /**
     * picture의 속성 정보를 반환합니다.
     * @private
     * @param {Object} model 
     * @returns {Object} prop
     */
    getProp(model) {
        return model.prop;
    }

    /**
     * picture의 Type 속성 정보를 반환합니다.
     * @private
     * @param {Object} prop 
     * @returns {String} type
     */
    getType(prop) {
        return util.get(prop, ['non_visual_prop', 'drawing_prop', 'hyperlink_on_click']) ? 'embed' : 'img';
    }

    /**
     * picture의 width 속성 정보를 반환합니다.
     * @private
     * @param {Object} prop picture properties
     * @returns {Number} width
     */
    getPictureWidth(prop) {
        const width = util.get(prop, ['shape_prop', 'transform', 'size', 'width_in_emu']);
        return !isNaN(width) ? util.emu2px(width) : undefined;
    }

    /**
     * picture의 height 속성 정보를 반환합니다.
     * @private
     * @param {Object} prop picture properties
     * @returns {Number} width
     */
    getPictureHeight(prop) {
        const height = util.get(prop, ['shape_prop', 'transform', 'size', 'height_in_emu']);
        return !isNaN(height) ? util.emu2px(height) : undefined;
    }

    /**
     * picture의 rotation 속성 정보를 반환합니다.
     * @param {Object} prop 
     * @returns {Number} rotation
     */
    getPictureRotation(prop) {
        const rotation = util.get(prop, ['shape_prop', 'transform', 'rotation_in_angle']);
        return !isNaN(rotation) ? util.reviseRotation(rotation / 1000) : 0;
    }

    /**
     * picture의 스타일을 적용하기 위해 picture properties를 받아 cssText를 반환합니다.
     * @private
     * @param {object} prop 
     * @returns {String} cssText
     */
    getCssText(prop) {
        let cssText = '';
        let width = this.getPictureWidth(prop);
        let height = this.getPictureHeight(prop);
        const rotation = this.getPictureRotation(prop);

        if (!isNaN(width)) {
            cssText += `width:${width}px;`;
        }

        if (!isNaN(height)) {
            cssText += `height:${height}px;`;
        }

        if (rotation) {
            const marginLeft = rotation % 180 === 0 ? 0 : (height - width) / 2;
            const marginRignt = rotation % 180 === 0 ? 0 : (height - width) / 2;
            const marginTop = rotation % 180 === 0 ? 0 : (width - height) / 2;
            const marginBottom = rotation % 180 === 0 ? 0 : (width - height) / 2;

            cssText += `transform:rotate(${rotation}deg);`;
            cssText += `margin-left:${marginLeft}px;`;
            cssText += `margin-right:${marginRignt}px;`;
            cssText += `margin-top:${marginTop}px;`;
            cssText += `margin-bottom:${marginBottom}px;`;
        }


        const rId = util.get(prop, ['blip_fill', 'blip', 'embed']);
        const relationshipListItem = this.relationshipListManager.getItem(rId);

        if (util.get(relationshipListItem, ['method']) === 'base64') {
            cssText += 'opacity:0.4;';
        }

        return cssText;
    }

    /**
     * type을 받아 embed|img Element 를 반환합니다.<br/>
     * embed Element의 경우 type을 지정해줍니다.
     * @private
     * @param {object} prop 
     * @returns {HTMLElement} HTMLElement
     */
    getElementByType(type) {
        let el = document.createElement(type);
        if (type === 'embed') {
            el.type = "application/x-shockwave-flash";
        }
        return el;
    }

    /**
     * picture properties과 type을 받아 src를 반환합니다.<br/>
     * embed Element의 경우 사용하기때문에 src를 embed태그에 맞게 수정합니다.
     * @private
     * @param {object} prop 
     * @returns {string} src
     */
    getSrc(prop, type) {
        let src;

        if (type === 'embed') { //동영상
            src = util.get(prop, ['non_visual_prop', 'drawing_prop', 'hyperlink_on_click', 'target']);
            src = src.replace('watch?v=', 'v/');
        } else { //이미지
            const rId = util.get(prop, ['blip_fill', 'blip', 'embed']);
            const relationshipListItem = this.relationshipListManager.getItem(rId);

            switch (util.get(relationshipListItem, ['method'])) {
                case 'base64':
                case 'url':
                    src = relationshipListItem.target;
                    break;
                default:
                    const importPath = this.editor.getEditorInfo('importPath');
                    const imagePath = this.relationshipListManager.getImagePath(rId);
                    src = importPath && imagePath.indexOf('media') > -1 ? `${importPath}/${imagePath}` : imagePath;
                    break;
            }
        }

        return src;
    }
}

export default PictureRenderer;


