import $ from 'jquery';
import PopupInterface from './PopupInterface';
import icon from '../icon';
import consts from '../../consts/consts';

const UI_NAME = consts.UI_NAME;
const STATUS = {
    DEFAULT: 'default',
    ADVANCED: 'advanced'
};
const COLOR_BLACK = '#000000';

/**
 * ColorPicker 클래스입니다.
 * Popup 클래스를 상속 받습니다.
 * @extends {PopupInterface}
 */
class ColorPicker extends PopupInterface {
    /**
     * PopupInterface의 인스턴스를 생성합니다.
     * 프로퍼티로는 editorId와 Popup Element가 있습니다.
     * @param {String} editorId - 에디터를 식별할 수 있는 아이디
     * @param {Object} controller - controller 객체
     */
    constructor(editorId, controller) {
        super(editorId);

        this.editorId = editorId;
        this.controller = controller;

        this.status = STATUS.DEFAULT;
        this.recentColorList = ['', '', '', '', '', '', ''];
        this.colorList = [
            '#000000', '#333333', '#666666', '#999999', '#CCCCCC', '#F3F3F3', '#FFFFFF',
            '#E30A17', '#FD9827', '#FFFD38', '#118914', '#0F69C9', '#9840FB', '#FC2AFC',
            '#F9CCCD', '#FEEACD', '#FFFECE', '#CDE7CD', '#CCE1F4', '#EBD7FE', '#FECDFE',
            '#EE6769', '#FDC16D', '#FFFD71', '#69B769', '#69A5DE', '#C189FC', '#FD6CFC',
            '#97040C', '#B06A18', '#B2B124', '#09600B', '#0A4AAF', '#6A2CAF', '#A44F79',
            '#9840FB', '#653D09', '#666510', '#033604', '#032B64', '#3D1864', '#731D47'
        ];

        this.$element = this.__createDom__();

        this.__attachEvent__();
    }

    /**
     * colorItem의 HTML을 반환합니다.
     * @private
     * @param {String} color
     * @returns {String}
     */
    __getColorItemHTML__(color) {
        return `<span class='se-color-item' style='background-color: ${color};' value='${color}'></span>`;
    }

    /**
     * colorBoard의 HTML을 반환합니다.
     * @private
     * @param {Array} colorList
     * @returns {String} - HTML형태의 문자열
     */
    __getColorBoardHTML__(colorList) {
        const $colorBoard = $('<div class="se-color-board"></div>');
        let $colorRow;

        colorList.forEach((color = 'inherit', index) => {
            const $colorItem = $(this.__getColorItemHTML__(color));

            if (index % 7 === 0) {
                $colorRow = $(`<div class='se-color-row'></div>`);
                $colorRow.append($colorItem);
                $colorBoard.append($colorRow);
            } else {
                $colorRow.append($colorItem);
            }
        });

        return $colorBoard.prop('outerHTML');
    }

    /**
     * popup 상단에 그려져야 하는 화살표 HTML을 반환합니다.
     * @private
     * @returns {jQuery}
     */
    __getArrow__() {
        return $(`<div class='se-popup-arrow'></div>`);
    }

    /**
     * popup의 header 부분의 HTML을 반환합니다.
     * @private
     * @returns {jQuery}
     */
    __getHeader__() {
        let $html = $(`<div class='se-popup-header'></div>`);

        if (this.status === STATUS.ADVANCED) {
            $html.hide();
        } else {
            if (this.fromBalloon) {
                $html.append(`
                    <div class='se-tab' value='back'>
                        <img src='${icon[UI_NAME.BACK]}'>
                    </div>
                `);
            } else {
                $html.hide();
            }
        }

        return $html;
    }

    /**
     * popup의 body 부분의 HTML을 반환합니다.
     * @private
     * @returns {jQuery}
     */
    __getBody__() {
        let $html = $(`<div class='se-popup-body'></div>`);

        if (this.status === STATUS.ADVANCED) {
            $html.append(`
                <div class='se-color-input'>
                    ${this.__getColorItemHTML__(COLOR_BLACK)}
                    <input class='se-input-box' type='text' placeholder='${COLOR_BLACK}'>
                </div>
                <div class='se-advanced-color'></div>
            `);
        } else {
            $html.append(`
                <div class='se-color-list'>
                    ${this.__getColorBoardHTML__(this.colorList)}
                </div>
                <div class='se-label se-diff-color'><span>다른 색</span></div>
                <div class='se-color-list' value='recent'>
                    <div class='se-label'>최근에 사용한 색</div>
                    ${this.__getColorBoardHTML__(this.recentColorList)}
                </div>
            `);
        }

        return $html;
    }

    /**
     * popup의 footer 부분의 jQuery Element를 반환합니다.
     * @private
     * @returns {jQuery}
     */
    __getFooter__() {
        let $html = $(`<div class='se-popup-footer'></div>`);

        if (this.status === STATUS.ADVANCED) {
            $html.append(`
                <div class='se-cancel se-btn'><span>취소</span></div> 
                <div class='se-confirm se-btn'><span>확인</span></div>
            `);
        } else {
            $html.append(`
                <div class='se-color-input'>
                    <div class='se-label' >HEX Color</div>
                    <input class='se-input-box' type='text' placeholder='${COLOR_BLACK}'>
                </div> 
                <div class='se-confirm se-btn'>
                    <span>확인</span>
                </div>
            `);
        }

        return $html;
    }

    /**
     * Popup Container 안에 Popup Dom을 생성하여 추가합니다.
     * @private
     */
    __createDom__() {
        const $popup = $(`<div class='se-popup se-color-picker' name="${UI_NAME.COLOR_PICKER}"></div>`);
        $('#' + this.editorId).find('.se-popup-container').append($popup);

        $popup.attr('data-status', this.status);

        $popup.append(this.__getArrow__());
        $popup.append(this.__getHeader__());
        $popup.append(this.__getBody__());
        $popup.append(this.__getFooter__());

        return $popup;
    }

    /**
     * 이벤트 등록을 합니다.
     * @private
     */
    __attachEvent__() {
        if (this.status === STATUS.DEFAULT) {
            this.$element.find('.se-confirm').on('click', this.__confirm__.bind(this));
            this.$element.find('.se-color-item').on('click', this.__selectColor__.bind(this));
            this.$element.find('.se-diff-color').on('click', this.__changeStatus__.bind(this, true));
            this.$element.find('[value=back]').on('click', this.back.bind(this));
        } else {
            this.$element.find('.se-confirm').on('click', this.__confirm__.bind(this));
            this.$element.find('.se-cancel').on('click', this.__changeStatus__.bind(this, false));
        }
    }

    /**
     * Popup Element의 위치를 설정합니다.
     * @private
     * @param {Object} position - top, left를 key로 가집니다.
     */
    setPosition(position) {
        this.$element.css(position);
    }

    /**
     * Color Picker Popup의 상태를 변경합니다.
     * @private
     * @param {Boolean} [isAdvanced=false]
     * @param {jQuery.Event} event
     */
    __changeStatus__(isAdvanced = false, event) {
        this.status = isAdvanced ? STATUS.ADVANCED : STATUS.DEFAULT;

        this.$element.find('.se-popup-header').replaceWith(this.__getHeader__());
        this.$element.find('.se-popup-body').replaceWith(this.__getBody__());
        this.$element.find('.se-popup-footer').replaceWith(this.__getFooter__());

        this.__attachEvent__();

        if (!!event) { //WindowEventManager에 등록된 이벤트로 전파되는 것을 막아야 함.
            event.preventDefault();
            event.stopPropagation();
        }
    }

    /**
     * 확인 버튼이 눌렸을 때 동작합니다.
     * @private
     */
    __confirm__() {
        const colorCode = this.$element.find('.se-input-box').val() || '';

        if (colorCode.startsWith('#') && colorCode.length === 7) {
            const hex = colorCode.split('#')[1];
            this.execFunc(hex);
            this.__addRecentColor__(colorCode);
        }

        this.hide();
    }

    /**
     * Popup Element를 보여줍니다.
     * @param {HTMLElement} elTarget
     * @param {String} actionName
     */
    show(elTarget, actionName) {
        this.execFunc = this.controller.execute.bind(this.controller, actionName);
        this.elTarget = elTarget;
        this.fromBalloon = $(elTarget).parents('.se-balloon').length > 0;

        this.__changeStatus__(false);

        if (this.fromBalloon) {
            this.$balloon = $(elTarget).parents('.se-balloon');

            const colorPickerOffsetTop = this.$balloon.get(0).offsetTop;
            const colorPickerOffsetLeft = this.$balloon.get(0).offsetLeft;
            const arrowOffsetLeft = this.$balloon.find('.se-popup-arrow').get(0).offsetLeft;

            const $arrow = this.$element.find('.se-popup-arrow');
            $arrow.css('left', arrowOffsetLeft);

            super.setPosition({top: colorPickerOffsetTop, left: colorPickerOffsetLeft});

            this.$balloon.removeClass('show');
            this.$element.addClass('show');
        } else {
            this.$balloon = null;
            super.show(elTarget);
        }
    }

    /**
     * Popup Element를 숨깁니다.
     */
    hide() {
        super.hide();
        this.fromBalloon = false;
    }

    /**
     * 뒤로 가기 버튼
     */
    back() {
        if (this.fromBalloon) {
            this.$balloon.addClass('show');
        }
        this.hide();
    }

    /**
     * Popup Element를 토글합니다.
     * @param {HTMLElement} elTarget
     * @param {String} actionName
     */
    toggle(elTarget, actionName) {
        if (elTarget === this.elTarget && this.$element.hasClass('show')) {
            this.hide();
        } else {
            this.show(elTarget, actionName);
        }
    }

    /**
     * 색상을 선택합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __selectColor__(e) {
        const colorCode = $(e.target).attr('value');
        const hex = !!colorCode && colorCode.split('#')[1];

        if (hex) {
            this.execFunc(hex);

            this.__addRecentColor__(colorCode);

            this.back();
        }
    }

    /**
     * 최근 색상을 추가합니다.
     * @private
     * @param {String} colorCode
     */
    __addRecentColor__(colorCode) {
        if (this.recentColorList.indexOf(colorCode) < 0) {
            this.recentColorList.unshift(colorCode);
        }

        if (this.recentColorList.length > 7) {
            this.recentColorList.pop();
        }
    }
}

export default ColorPicker;


