import util from '../util/util';
import $ from 'jquery';

/**
 * 편집모델의 하위 모델인 Run을 렌더링하는 Class입니다.
 * @example
 * import RunRenderer from 'path'
 * let RunRenderer = RunRenderer.getInstance();
 */
class RunRenderer {
    constructor(renderer) {
        this.parent = renderer;
    }

    /**
     * 상위 에디터 객체를 반환합니다.
     * @returns {Object} - 에디터 객체
     */
    getEditor() {
        return this.parent.editor;
    }

    /**
     * Run을 렌더링 하기 위해 Run Element를 받아 HTML을 반환합니다.
     * @param {Object} run Run Element
     * @returns {String} HTML 형태의 문자열
     * @example
     * //return example
     * <span style='font-weight: bold;'>동해물과</span>
     */
    getHTML(run) {
        let $run;
        if (util.isLineBreak(run)) {
            $run = $('<br/>');
        } else if (util.isTab(run)) {
            $run = $('<span style="margin-left:32px">&nbsp;</span>');
        } else {
            let text = run.length() !==0 ? util.escape(run.getText()) : '&#8203;';
            let prop = run.getProp();
            let cssText = this.getCssText(prop);
            $run = $(`<span>${text}</span>`);
            if (cssText) {
                $run.attr('style', cssText);
            }
        }
        
        return $run;
    }

    /**
     * '굵게' 속성이 적용되어야 하는지를 검사합니다.
     * @private
     * @param {Object} prop run properties
     * @returns {Boolean}
     */
    isBold(prop) {
        return !!util.get(prop, ['bold']);
    }

    /**
     * '이탤릭' 속성이 적용되어야 하는지를 검사합니다.
     * @private
     * @param {Object} prop run properties
     * @returns {Boolean}
     */
    isItalic(prop) {
        return !!util.get(prop, ['italic']);
    }

    /**
     * '밑줄' 속성이 적용되어야 하는지를 검사합니다.
     * @private
     * @param {Object} prop run properties
     * @returns {Boolean}
     */
    isUnderline(prop) {
        let underlinetStyle = util.get(prop, ['underline', 'style']);
        return underlinetStyle === 'single';
    }

    /**
     * '취소선' 속성이 적용되어야 하는지를 검사합니다.
     * @private
     * @param {Object} prop run properties
     * @returns {Boolean}
     */
    isStrike(prop) {
        let strikeStyle = util.get(prop, ['strike', 'style']);
        return strikeStyle === 'single';
    }

    /**
     * run의 '글꼴' 속성 값을 반홥합니다.
     * @private
     * @param {Object} prop run properties
     * @returns {String}
     */
    getFontFace(prop) {
        let oFont = util.get(prop, ['fonts']);
        let fontFace;

        if (util.get(oFont, ['hint']) === 'eastAsia') {
            fontFace = util.get(oFont, ['east_asia']);
        } else if (util.get(oFont, ['hint']) === '_default') {
            fontFace = util.get(oFont, ['ascii']);
        } else {
            fontFace = util.get(oFont, ['ascii']);
        }

        return fontFace;
    }

    /**
     * run의 '글꼴 크기' 속성 값을 반홥합니다.
     * 값이 없으면 undefined를 반홥합니다.
     * @private
     * @param {object} prop run properties
     * @returns {Number | undefined}
     */
    getFontSize(prop) {
        let fontSize = util.get(prop, ['font_size_in_sunit']);
        return !isNaN(fontSize) ? util.sunit2pt(fontSize) : undefined;
    }

    /**
     * run의 '글꼴 색' 속성 값을 반환합니다.
     * @private
     * @param {object} prop run properties
     * @returns {String}
     */
    getTextColor(prop) {
        let oTextFill = util.get(prop, ['text_fill']);
        let renderUtil = this.getEditor().getRenderUtil();

        if (oTextFill) {
            let colorObj = renderUtil.getColorObject(oTextFill);
            let colorValue = renderUtil.getColorValue(colorObj);
            return util.getColorCodeFromDecimal(colorValue);
        }
    }

    /**
     * run의 '배경색' 속성 값을 반환합니다.
     * @private
     * @param {object} prop run properties
     * @returns {String}
     */
    getBackgroundColor(prop) {
        let oFill = util.get(prop, ['fill']);
        let renderUtil = this.getEditor().getRenderUtil();

        if (oFill) {
            let colorObj = renderUtil.getColorObject(oFill);
            let colorValue = renderUtil.getColorValue(colorObj);
            return util.getColorCodeFromDecimal(colorValue);
        }
    }

    /**
     * run의 스타일을 적용하기 위해 run properties를 받아 cssText를 반홥합니다.
     * @param {Object} prop run properties
     * @returns {String}
     * @example
     * 'font-weight: bold; font-size: 14px;'
     */
    getCssText(prop) {
        let cssText = '';
        let textDecoration = '';

        let fontFace = this.getFontFace(prop);
        let fontSize = this.getFontSize(prop);
        let textColor = this.getTextColor(prop);
        let bgColor = this.getBackgroundColor(prop);

        if (this.isBold(prop)) {
            cssText += 'font-weight: bold;';
        }

        if (this.isItalic(prop)) {
            cssText += 'font-style: italic;';
        }

        if (this.isUnderline(prop)) {
            textDecoration += 'underline ';
        }

        if (this.isStrike(prop)) {
            textDecoration += 'line-through';
        }

        if (textDecoration) {
            cssText += `text-decoration: ${textDecoration};`;
        }

        if (fontFace) {
            cssText += `font-family: ${fontFace};`;
        }

        if (fontSize) {
            cssText += `font-size: ${fontSize}pt;`;
        }

        if (textColor) {
            cssText += `color: ${textColor};`;
        }

        if (bgColor) {
            cssText += `background-color: ${bgColor};`;
        }

        return cssText;
    }
}

export default RunRenderer;


