/**
 * Web Office pb parser for Javascript
 */

/**
	vmParser class constructor
	* serializedBinary 
		- vm serialized binary
	* vmDescriptor 
		- vm message descriptor
	
	Example)
		var parser = new vmParser([10, 20, 30, 40], vmCellDescriptor);
 */
var vmDescriptor = require('./vmWordDescriptor');
var vmDecoder = require('./vmDecoder');

var vmParser = function (serializedBinary, vmDescriptor) {
	this.buffer = serializedBinary;
	this.vmDescriptor = !!vmDescriptor ? vmDescriptor.init(vmDescriptor.defaultDescriptor) : null;
	this.vmDecoder = new vmDecoder(this, vmDescriptor);
	this.jsonPath = [{ name: vmDescriptor ? vmDescriptor.descriptor : '', is_repeat: false, 'value': {} }];
};

/** 
	vmParser prototype functions
 */
vmParser.prototype = {
	// tag reader
	readTag: function (pos) {
		var start = pos;
		while (this.buffer[pos] & 0x80) {
			pos += 1;
		}
		pos += 1;
		return {
			tag: this.buffer.slice(start, pos),
			new_pos: pos
		}
	},

	// parsing module
	parse: function (pos, end, descriptor) {
		while (pos != end) {
			var tagInfo = this.readTag(pos);
			this.vmDecoder.setDecoderByTag(tagInfo.tag, descriptor || this.vmDescriptor.defaultDescriptor);
			if (!this.vmDecoder.decoder_func) {
				console.log('Exception : descriptor - ' + descriptor + ' , tag - ' + tagInfo.tag.toString());
				break;
			}
			if (!this.vmDescriptor.value_field_map[this.vmDecoder.func_name]) {
				this.jsonPath.push({
					name: this.vmDecoder.field_name,
					is_repeat: this.vmDecoder.func_name == 'repeated'
				});
			}
			pos = this.vmDecoder.decoder_func(tagInfo.new_pos);
		}
		return pos;
	},

	// convert array value to UTF8 String
	arrToString: function (arr) {
		if (!arr) {
			return arr;
		}
		var result = '';
		var c1, c2, c3, c4, shift;
		for (var i = 0; i < arr.length;) {
			c1 = arr[i++];
			shift = c1 >> 4;
			if (shift < 8) {
				result += String.fromCharCode(c1);
			} else if (shift == 12 || shift == 13) {
				c2 = arr[i++];
				result += String.fromCharCode(((c1 & 0x1F) << 6) | (c2 & 0x3F));
			} else if (shift == 14) {
				c2 = arr[i++];
				c3 = arr[i++];
				result += String.fromCharCode(
					((c1 & 0x0F) << 12) |
					((c2 & 0x3F) << 6) |
					((c3 & 0x3F) << 0)
				);
			} else if(shift == 15){ // surrogate 
				c2 = arr[i++];
				c3 = arr[i++];
				c4 = arr[i++];
				result += String.fromCodePoint(
					((c1 & 0x07) << 18) |
					((c2 & 0x3F) << 12) |
					((c3 & 0x3F) << 6) |
					(c4 & 0x3F)
				);
			}
		}
		return result;
	},

	// read bits array buffer
	readBits: function (buffer, start, length) {
		var shl = function (a, b) {
			for (++b; --b; a = ((a %= 0x7fffffff + 1) & 0x40000000) == 0x40000000 ? a * 2 : (a - 0x40000000) * 2 + 0x7fffffff + 1);
			return a;
		};
		if (start < 0 || length <= 0) {
			return 0;
		}

		if (buffer.length < -(-(start + length) >> 3)) {
			throw new Error('checkBuffer::missing bytes');
		}
		var offsetLeft = (start + length) % 8;
		var offsetRight = start % 8;
		var curByte = buffer.length - (start >> 3) - 1;
		var lastByte = buffer.length + (-(start + length) >> 3);
		var diff = curByte - lastByte;
		var sum = ((buffer[curByte] >> offsetRight) & ((1 << (diff ? 8 - offsetRight : length)) - 1))
			+ (diff && (offsetLeft) ? (buffer[lastByte++] & ((1 << offsetLeft) - 1)) << (diff-- << 3) - offsetRight : 0);
		for (; diff; diff-- , lastByte++) {
			sum += shl(buffer[lastByte], (diff << 3) - offsetRight);
		}
		return sum;
	},

	// decode float from array buffer
	decodeFloat: function (arrData, precisionBits, exponentBits, isBigEndian) {
		var buffer = !!isBigEndian ? arrData : arrData.reverse();
		var bias = Math.pow(2, exponentBits - 1) - 1;
		var signal = this.readBits(buffer, precisionBits + exponentBits, 1);
		var currByte = buffer.length + (-precisionBits >> 3) - 1;
		var divisor = 2;
		var significand = 0;
		var exponent = this.readBits(buffer, precisionBits, exponentBits);
		var byteValue, startBit, mask;

		do {
			byteValue = buffer[++currByte];
			startBit = precisionBits % 8 || 8;
			mask = 1 << startBit;
			for (; mask >>= 1; (byteValue & mask) && (significand += 1 / divisor), divisor *= 2);
		} while (precisionBits -= startBit);
		return exponent == (bias << 1) + 1 ? significand ? NaN : signal ? -Infinity : +Infinity
			: (1 + signal * -2) * (exponent || significand ? !exponent ? Math.pow(2, -bias + 1) * significand
				: Math.pow(2, exponent - bias) * (1 + significand) : 0);
	},

	// decode integer from array buffer
	decodeInt: function (arrData, bits, signed, isBigEndian) {
		var buffer = !!isBigEndian ? arrData : arrData.reverse();
		var x = this.readBits(0, bits);
		var max = Math.pow(2, bits);
		return signed && x >= max / 2 ? x - max : x;
	},

	toInt: function (arrData) {
		return this.decodeInt(arrData, 32, true);
	},

	toDouble: function (arrData) {
		return this.decodeFloat(arrData, 52, 11);
	},
	toFloat: function (arrData) {
		return this.decodeFloat(arrData, 23, 8);
	}
};

module.exports = vmParser;

/**
 * fast foreach
 */
Array.prototype.fastEach = function (arr) {
	var len = this.length;
	for (var i = 0; i < len; i++) {
		arr(this[i], i);
	}
};

/** 
 * IE8 isArray polyfill
 */
if (!Array.isArray) {
	Array.isArray = function (arg) {
		return Object.prototype.toString.call(arg) === '[object Array]';
	};
}

/**
 * IE8 Object.keys polyfill
 */
if (!Object.keys) {
	Object.keys = function (obj) {
		var keys = [];
		for (var k in obj) {
			if (obj.hasOwnProperty(k)) {
				keys.push(k);
			}
		}
		return keys;
	};
}

/**
 *  IE8 function bind fallback
 */
if (!Function.prototype.bind) {
	Function.prototype.bind = function (oThis) {
		var aArgs = Array.prototype.slice.call(arguments, 1),
			fToBind = this,
			fNOP = function () { },
			fBound = function () {
				return fToBind.apply(this instanceof fNOP && oThis
					? this
					: oThis,
					aArgs.concat(Array.prototype.slice.call(arguments)));
			};

		fNOP.prototype = this.prototype;
		fBound.prototype = new fNOP();

		return fBound;
	};
}

/**
 * IE fromCodePoint method fallback
 * https://developer.mozilla.org/ko/docs/Web/JavaScript/Reference/Global_Objects/String/fromCodePoint
 */
if (!String.fromCodePoint) {
	(function() {
		var defineProperty = (function() {
			// IE 8 only supports `Object.defineProperty` on DOM elements
			try {
				var object = {};
				var $defineProperty = Object.defineProperty;
				var result = $defineProperty(object, object, object)
						&& $defineProperty;
			} catch (error) {
			}
			return result;
		}());
		var stringFromCharCode = String.fromCharCode;
		var floor = Math.floor;
		var fromCodePoint = function() {
			var MAX_SIZE = 0x4000;
			var codeUnits = [];
			var highSurrogate;
			var lowSurrogate;
			var index = -1;
			var length = arguments.length;
			if (!length) {
				return '';
			}
			var result = '';
			while (++index < length) {
				var codePoint = Number(arguments[index]);
				if (!isFinite(codePoint) || // `NaN`, `+Infinity`, or `-Infinity`
				codePoint < 0 || // not a valid Unicode code point
				codePoint > 0x10FFFF || // not a valid Unicode code point
				floor(codePoint) != codePoint // not an integer
				) {
					throw RangeError('Invalid code point: ' + codePoint);
				}
				if (codePoint <= 0xFFFF) { // BMP code point
					codeUnits.push(codePoint);
				} else { // Astral code point; split in surrogate halves
					// http://mathiasbynens.be/notes/javascript-encoding#surrogate-formulae
					codePoint -= 0x10000;
					highSurrogate = (codePoint >> 10) + 0xD800;
					lowSurrogate = (codePoint % 0x400) + 0xDC00;
					codeUnits.push(highSurrogate, lowSurrogate);
				}
				if (index + 1 == length || codeUnits.length > MAX_SIZE) {
					result += stringFromCharCode.apply(null, codeUnits);
					codeUnits.length = 0;
				}
			}
			return result;
		};
		if (defineProperty) {
			defineProperty(String, 'fromCodePoint', {
				'value' : fromCodePoint,
				'configurable' : true,
				'writable' : true
			});
		} else {
			String.fromCodePoint = fromCodePoint;
		}
	}());
}



