import consts from '../consts/consts';

const UNDO_REDO_MAX_SIZE = consts.UNDO_REDO_MAX_SIZE;

/**
 * Undo/Redo Class 객체.
 */
class UndoRedo {
    constructor() {
        this.undoStack = [];
        this.redoStack = [];
    }

    /**
     * undoStack을 반환합니다.
     * @returns {Array} undoStack
     */
    getUndoStack() {
        return this.undoStack;
    }
    
    /**
     * redoStack을 반환합니다.
     * @returns {Array} redoStack
     */
    getRedoStack() {
        return this.redoStack;
    }

    /**
     * Undo Stack에 있는 액션을 실행합니다.
     * Undo Stack list에 액션이 없을 경우 동작하지 않습니다.
     */
    undo() {
        if (this.isEmptyUndo()) {
            return;
        }

        const item = this.undoStack.pop();
        this.__pushRedo__(item, true);

        return item;
    }

    /**
     * Redo Stack에 있는 액션을 실행합니다.
     * Redo Stack list에 액션이 없을 경우 동작하지 않습니다.
     */
    redo() {
        if (this.isEmptyRedo()) {
            return;
        }

        const item = this.redoStack.pop();
        this.pushUndo(item);

        return item;
    }

    /**
     * Undo Stack이 비어있는지 검사합니다.
     * @returns {Boolean}
     */
    isEmptyUndo() {
        return this.undoStack.length === 0;
    }

    /**
     * Redo Stack이 비어있는지 검사합니다.
     * @returns {Boolean}
     */
    isEmptyRedo() {
        return this.redoStack.length === 0;
    }

    /**
     * Undo Stack이 꽉 찼는지 검사합니다.
     * @returns {Boolean}
     */
    isFullUndo() {
        return this.undoStack.length === UNDO_REDO_MAX_SIZE;
    }

    /**
     * Undo Stack에 Item을 넣습니다.
     * @param {Object} item 
     */
    pushUndo(item) {
        if (this.isFullUndo()) {
            this.undoStack.shift();
        }
        this.undoStack.push(item);
    }

    /**
     * Redo Stack에 Item을 넣습니다.
     * @param {Object} item 
     */
    __pushRedo__(item) {
        this.redoStack.push(item);
    }

    /**
     * Redo Stack을 비웁니다.
     */
    clearRedo() {
        this.redoStack.length = 0;
    }
}

export default UndoRedo;


