import $ from 'jquery';
import consts from '../../consts/consts';
import PopupInterface from './PopupInterface';
import actionMap from '../../consts/actionMap';

const UI_NAME = consts.UI_NAME;

/**
 * ImagePopup 클래스입니다.
 * Popup 클래스를 상속 받습니다.
 * @extends {PopupInterface}
 */
class ImagePopup extends PopupInterface {
    /**
     * Creates an instance of ImagePopup.
     * @param {String} editorId - 에디터를 식별할 수 있는 아이디
     * @param {Object} controller - controller 객체
     */
    constructor(editorId, controller) {
        super(editorId);

        this.editorId = editorId;
        this.controller = controller;

        this.selectedFile = null;
        this.selectedTab = null;

        this.$element = this.__createDom__();

        this.__attachEvent__();
        this.__selectUploadTab__();
    }

    /**
     * Popup Container 안에 Image Popup Dom을 생성하여 추가합니다.
     * @private
     * @returns {jQuery}
     */
    __createDom__() {
        const popupHTML = `
        <div class='se-popup' name="${UI_NAME.IMAGE}">
            <div class='se-popup-arrow'></div>
            <div class='se-popup-header'>
                <div class='se-tab' value='upload'><span>UP</span></div>
                <div class='se-tab' value='url'><span>URL</span></div>
            </div>
            <div class='se-popup-body'>
                <div class='se-upload'>
                    <input class='se-image-uploader' type="file">
                    <div class='se-open-file'>
                        <span>이곳을 </span>
                        <span class='se-text-point'>클릭 </span>
                        <span>하거나 파일을</span>
                        <span class='se-text-point'>Drag & Drop </span>
                        <span>하세요. </span>
                    </div>
                </div>
                <div class='se-url'>
                    <span>URL</span>
                    <input class='se-input-box' type="text" placeholder="Homepage">
                </div>
            </div>
            <div class='se-popup-footer'>
                <div class='se-cancel se-btn'><span>취소</span></div>
                <div class='se-confirm se-btn'><span>확인</span></div>
            </div>
        </div>
        `;

        const $popup = $(popupHTML);
        const $popupContainer = $('#' + this.editorId).find('.se-popup-container');
        $popupContainer.append($popup);

        return $popup;
    }

    /**
     * 이벤트 등록을 합니다.
     * @private
     */
    __attachEvent__() {
        const $confirm = $('.se-confirm');
        const $cancel = $('.se-cancel');
        const $tab = $('.se-tab');
        const $openFile = $('.se-open-file');
        const $upload = $('.se-upload');
        const $imageUploader = $('.se-image-uploader');

        $confirm.bind('click', this.__confirm__.bind(this));
        $cancel.bind('click', this.__cancel__.bind(this));
        $tab.bind('click', this.__selectTab__.bind(this));
        $openFile.bind('click', this.__openFileDialog__.bind(this));
        $upload.bind('dragenter', this.__dragEnter__.bind(this));
        $upload.bind('dragleave', this.__dragLeave__.bind(this));
        $upload.bind('drop', this.__dropFile__.bind(this));
        $upload.bind('dragover', this.__dragOver__.bind(this));
        $imageUploader.bind('change', this.__selectFile__.bind(this));
    }

    /**
     * 확인 버튼이 눌렸을 때 동작합니다.
     * @private
     */
    __confirm__() {
        if (this.selectedTab === 'url') {
            this.__insertUrlImage__();
        }
        this.hide();
    }

    /**
     * Popup Element를 보여줍니다.
     * @param {Object} elTarget
     */
    show(elTarget) {
        super.show(elTarget);
        this.__selectUploadTab__();
    }

    /**
     * Popup Element를 숨깁니다.
     */
    hide() {
        super.hide();
        this.__clearFile__();
    }

    /**
     * url로 이미지를 삽입합니다.
     * @private
     */
    __insertUrlImage__() {
        const $elInput = this.$element.find('.se-url .se-input-box');

        if ($elInput.length > 0) {
            this.controller.execute(actionMap.INSERT_URL_IMAGE, $elInput.val());
        }
    }

    /**
     * 이미지를 삽입합니다.
     * @private
     */
    __insertImage__() {
        if (this.selectedFile) {
            this.controller.execute(actionMap.INSERT_IMAGE, this.selectedFile);
        } else {
            alert('선택된 이미지가 없습니다!');
        }

        this.hide();
    }

    /**
     * input 태그의 파일 정보를 초기화합니다.
     * @private
     */
    __clearFile__() {
        const $elInput = $('.se-image-uploader');
        $elInput.wrap('<form>').closest('form').get(0).reset();
        $elInput.unwrap();
    }

    /**
     * 탭을 선택합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __selectTab__(e) {
        const $elTab = $(e.currentTarget);

        if ($elTab.attr('value') === 'upload') {
            this.__selectUploadTab__();
        } else {
            this.__selectUrlTab__();
        }
    }

    /**
     * 업로드 탭을 선택합니다.
     * @private
     */
    __selectUploadTab__() {
        const $popup = this.$element;

        const $elUploadTab = $popup.find('[value=upload]');
        $elUploadTab.addClass('active');

        const $elUrlTab = $popup.find('[value=url]');
        $elUrlTab.removeClass('active');

        const $elUpload = $popup.find('.se-upload');
        $elUpload.addClass('show');

        const $elUrl = $popup.find('.se-url');
        $elUrl.removeClass('show');

        const $elConfirmBtn = $popup.find('.se-confirm');
        $elConfirmBtn.removeClass('show');

        this.selectedTab = 'upload';
    }

    /**
     * URL 탭을 선택합니다.
     * @private
     */
    __selectUrlTab__() {
        const $popup = this.$element;

        const $uploadTab = $popup.find('[value=upload]');
        $uploadTab.removeClass('active');

        const $urlTab = $popup.find('[value=url]');
        $urlTab.addClass('active');

        const $upload = $popup.find('.se-upload');
        $upload.removeClass('show');

        const $url = $popup.find('.se-url');
        $url.addClass('show');

        const $confirmBtn = $popup.find('.se-confirm');
        $confirmBtn.addClass('show');

        const $input = $popup.find('.se-input-box');
        $input.val('');

        this.selectedTab = 'url';
    }

    /**
     * 파일 다이얼로그를 엽니다.
     * @private
     */
    __openFileDialog__() {
        const $elInput = $('.se-image-uploader');
        $elInput.trigger("click");
    }

    /**
     * 다이얼로그에서 파일을 선택한 뒤, 발생한 콜백함수입니다.
     * @private
     * @param {jQuery.Event} e
     */
    __selectFile__(e) {
        const elInput = e.target;
        this.selectedFile = elInput.files[0];

        this.__insertImage__();
    }

    /**
     * 파일을 드래그해서 업로드 영역으로 들어갔을 때의 이벤트를 처리합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __dragEnter__(e) {
        e.stopPropagation();
        e.preventDefault();
    }

    /**
     * 파일을 드래그해서 업로드 영역을 벗어났을 때의 이벤트를 처리합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __dragLeave__(e) {
        e.stopPropagation();
        e.preventDefault();
        $('.se-upload').removeClass('dragover');
    }

    /**
     * 파일을 드래그해서 업로드 영역 위에 있을 때의 이벤트를 처리합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __dragOver__(e) {
        e.stopPropagation();
        e.preventDefault();
        $('.se-upload').addClass('dragover');
    }

    /**
     * 파일을 업로드 영역에 드롭 했을 때의 이벤트를 처리합니다.
     * @private
     * @param {jQuery.Event} e
     */
    __dropFile__(e) {
        e.preventDefault();
        this.selectedFile = e.originalEvent.dataTransfer.files[0];

        this.__insertImage__();
    }
}

export default ImagePopup;


