/**
 * UI 정의 생성기입니다.
 * @example
 * //형식
 * 이름: {
 *     type: 타입('button' | 'dropdown' | 'separator'),
 *     해당 UI를 생성하는데 필요한 옵션: 값,
 *     ...
 *     ...
 * },
 * ...
 *
 * // 각 타입별 필요한 옵션
 * >> separator의 필요값 :
 *      - isHorizontal: (Boolean)
 * >> button의 필요값 :
 *      - label: (String) 버튼에 보여질 레이블입니다.
 *      - tooltipMsg: (String) 툴팁에 보여질 메세지. 기본값은 label로 주어진 값입니다.
 *      - actionName: (String) 액션 이름입니다.
 *      - actionValue: (*) 액션 수행시 인자로 넘겨줄 값입니다.
 * >> dropdown의 필요값 :
 *      - label: (String) 버튼에 보여질 레이블입니다.
 *      - actionName: (String) 액션 이름입니다.
 *      - actionValue: (*) 액션 수행시 인자로 넘겨줄 값입니다.
 *      - isSingle: (Boolean) 싱글버튼으로 할 것인지, 스플릿버튼(버튼과 드롭다운이 나뉜 형태)으로 할 것인지 여부입니다. (default: true)
 *      - dropdownItems: Array[Object] 드롭다운을 클릭했을 때 보여지는 아이템입니다.
 *            아래는 드롭다운 아이템 옵션입니다 :
 *          - label: (String) 드롭다운 아이템에 보여질 레이블입니다.
 *          - cssText: (String) 레이블을 나타낼 css 스타일 텍스트입니다.
 *          - actionName: (String) 액션 이름입니다.
 *          - actionValue: (*) 액션 수행시 인자로 넘겨줄 값입니다.
 */
