import util from '../util/util';
import $ from 'jquery';
import consts from '../consts/consts';

const LISTS_FORMAT = consts.LISTS_FORMAT;

/**
 * 글머리 번호, 기호를 렌더링하는 Class 입니다.
 */
class ListsRenderer {
    constructor(renderer) {
        this.parent = renderer;
        this.editor = renderer.editor;
        this.editorId = this.editor.id;
        this.listsManageMap = {};

        this.SEModelManager = this.editor.getSEModelManager();
        this.listsManager = this.editor.getListsManager();
        this.relationshipListManager = this.getEditor().getRelationshipListManager();

        this.runRenderer = this.parent.getRunRenderer();

        this.$style = $(`<style class="se-lists-css" data-editor-id="${this.editorId}"></style>`);
        $('head').append(this.$style);
    }

    /**
     * 상위 에디터 객체를 반환합니다.
     * @returns {Object} - 에디터 객체
     */
    getEditor() {
        return this.editor;
    }

    /**
     * 글머리 표현을 위한 인라인 스타일을 동적으로 생성합니다.
     */
    generateListsCss() {
        let css = '';

        this.SEModelManager.getAllParagraph(true).forEach(paragraph => {
            if (paragraph.isLists()) {
                const paragraphId = paragraph.getId();
                const id = paragraph.getListsId();
                const level = paragraph.getListsLevel();
                const type = this.listsManager.getListsType(id, level);

                this.setListsManageMap(id, level, paragraphId);

                const cssText = type === LISTS_FORMAT.BULLET
                    ? this.getBulletedListCssText(id, level, paragraphId)
                    : this.getNumberedListCssText(id, level, paragraphId);

                if (!!cssText) {
                    css += `#${this.editorId}_${paragraphId}::before {${cssText}} ` + '\n';
                }
            }
        });

        if (!!css) {
            this.$style.html(css);
        }
    }

    /**
     * 글머리 기호 표현을 위한 CSS 생성하여 반환합니다.
     * @param {Number} id 글머리 기호 id
     * @param {Number} level 글머리 기호 level
     * @param {Number} paragraphId paragraph id
     * @returns {String} 글머리 기호를 표현 하기 위한 css text
     */
    getBulletedListCssText(id, level, paragraphId) {
        let cssText = '';

        if (util.isNil(id) || util.isNil(level) || util.isNil(paragraphId)) {
            //Do Nothing
        } else {
            const levelItem = this.listsManager.getLevelItem(id, level);

            if (levelItem) {
                const content = util.get(levelItem, ['level_text']);
                const pictureBulletId = util.get(levelItem, ['picture_bullet_id']);
                const listsRunProp = util.get(levelItem, ['textrun_prop']);
                const firstTextRun = this.SEModelManager.getParagraph(paragraphId).firstChild();
                const textRunProp = !!firstTextRun ? firstTextRun.getProp() : {};

                cssText = this.runRenderer.getCssText(Object.assign({}, textRunProp, listsRunProp));

                if (util.isNil(pictureBulletId)) { //일반 글머리 기호인 경우
                    cssText += `content: '${content}';`;

                } else { //이미지 글머리 기호인 경우
                    const bulletItem = this.listsManager.getPictureLists().find(item => {
                        return item.picture_bullet_id === pictureBulletId;
                    });

                    if (bulletItem) {
                        const rId = util.get(bulletItem, ['picture', 'embed']);
                        const width = util.sunit2px(util.get(bulletItem, ['picture', 'width_in_sunit']));
                        const height = util.sunit2px(util.get(bulletItem, ['picture', 'height_in_sunit']));
                        const importPath = this.editor.getEditorInfo('importPath');
                        const imagePath = this.relationshipListManager.getImagePath(rId);
                        const src = importPath && imagePath.indexOf('media') > -1 ? `${importPath}/${imagePath}` : imagePath;
                        cssText += `content: url('${src}'); width: ${width}px; height: ${height}px;`;
                    }
                }
            }
        }

        return cssText;
    }

    /**
     * 글머리 번호 표현을 위한 CSS 생성하여 반환합니다.
     * @param {Number} id 글머리 번호 id
     * @param {Number} level 글머리 번호 level
     * @param {Number} paragraphId paragraph id
     * @returns {String} 글머리 번호를 표현 하기 위한 css text
     */
    getNumberedListCssText(id, level, paragraphId) {
        let cssText = '';

        if (util.isNil(id) || util.isNil(level) || util.isNil(paragraphId)) {
            //Do Nothing
        } else {
            const format = this.listsManager.getListsFormat(id, level);

            if (format) {
                const content = this.getNumberText(format, id, level, paragraphId);
                const levelItem = this.listsManager.getLevelItem(id, level);
                const listsRunProp = util.get(levelItem, ['textrun_prop']);
                const firstTextRun = this.SEModelManager.getParagraph(paragraphId).firstChild();
                const textRunProp = !!firstTextRun ? firstTextRun.getProp() : {};

                cssText = this.runRenderer.getCssText(Object.assign({}, textRunProp, listsRunProp));
                cssText += `content: '${content}';`;
            }
        }

        return cssText;
    }

    /**
     * 글머리 번호의 헤더 표현을 위한 넘버링 정보를 paragraph id를 key로 갖는 map 형태로 설정합니다.
     * 자세한 스펙은 {@link http://wiki.synap.co.kr/pages/viewpage.action?pageId=32418412|글머리 목록 표현}을 참고합니다.
     * @private
     * @param {Number} id 글머리번호 id
     * @param {Number} level 글머리번호 level
     * @param {Number} paraId paragraph id
     */
    setListsManageMap(id, level, paraId) {
        this.listsManageMap[paraId] = [];

        const currParagraph = this.SEModelManager.getParagraph(paraId);

        if (currParagraph && currParagraph.isLists()) {
            const prevParagraph = currParagraph.previousSibling();
            let startValue, value;

            //이전 문단이 글머리 목록이면서, 현재 문단과 동일한 글머리 아이디를 갖는다고 할 때 이전 문단의 넘버링 정보를 이용해 계산한다.
            if (prevParagraph && prevParagraph.isLists() && id === prevParagraph.getListsId()) {
                const prevLevel = prevParagraph.getListsLevel();
                const prevParagraphId = prevParagraph.getId();

                for (let idx = 0, lastIdx = level; idx <= lastIdx; idx++) {
                    if (idx === lastIdx) {
                        if (level > prevLevel) {
                            startValue = this.listsManager.getStartValue(id, idx);
                            value = startValue;
                        } else {
                            value = util.get(this.listsManageMap, [prevParagraphId, idx]) + 1;
                        }
                    } else {
                        startValue = this.listsManager.getStartValue(id, level);
                        value = util.get(this.listsManageMap, [prevParagraphId, idx], startValue);
                    }

                    util.set(this.listsManageMap, [paraId, idx], value);
                }

            } else { // 그렇지 않은 경우에는 각 글머리 문단의 기본 값을 갖게 한다.
                for (let idx = 0, lastIdx = level; idx <= lastIdx; idx++) {
                    startValue = this.listsManager.getStartValue(id, idx);
                    util.set(this.listsManageMap, [paraId, idx], startValue);
                }
            }
        }
    }

    /**
     * format에 따른 글머리 번호를 반환합니다.
     * @private
     * @param {String} format 글머리번호 포맷
     * @param {Number} id 글머리번호 id
     * @param {Number} level 글머리 번호 level
     * @param {Number} paragraphId paragraph id
     * @returns {String}
     */
    getNumberText(format, id, level, paragraphId) {
        let converted = '';

        if (util.isNil(format) || util.isNil(id) || util.isNil(level) || util.isNil(paragraphId)) {
            //Do Nothing
        } else {

            switch (format) {
                case LISTS_FORMAT.DECIMAL:
                case LISTS_FORMAT.DECIMAL_FULL_WIDTH:
                    converted = this.getConvertedDecimal(paragraphId, id, level);
                    break;
                case LISTS_FORMAT.UPPER_LETTER:
                    converted = this.getConvertedLetter(paragraphId, id, level, true);
                    break;
                case LISTS_FORMAT.LOWER_LETTER:
                    converted = this.getConvertedLetter(paragraphId, id, level, false);
                    break;
                case LISTS_FORMAT.UPPER_ROMAN:
                    converted = this.getConvertedRoman(paragraphId, id, level, true);
                    break;
                case LISTS_FORMAT.LOWER_ROMAN:
                    converted = this.getConvertedRoman(paragraphId, id, level, false);
                    break;
                case LISTS_FORMAT.DECIMAL_ENCLOSED_CIRCLE:
                    converted = this.getConvertedCircleDecimal(paragraphId, id, level);
                    break;
                case LISTS_FORMAT.GANADA:
                    converted = this.getConvertedGanada(paragraphId, id, level);
                    break;
                case LISTS_FORMAT.CHOSUNG:
                    converted = this.getConvertedChosung(paragraphId, id, level);
                    break;
                default: //LISTS_FORMAT.NONE
                    break;
            }
        }

        return converted;
    }

    /**
     * decimal format으로 변환된 글머리 번호를 반환합니다.
     * @private
     * @param {Number} paragraphId paragraph id
     * @param {Number} id 글머리번호 id
     * @param {Number} level 글머리번호 level
     * @returns {String}
     */
    getConvertedDecimal(paragraphId, id, level) {
        let levelText = this.listsManager.getLevelText(id, level);
        let levelValue = this.listsManageMap[paragraphId];

        if (util.isArray(levelValue)) {
            levelValue.forEach((newSubStr, idx) => {
                let subStr = '%' + (idx + 1);
                levelText = levelText.replace(subStr, newSubStr);
            });
        }

        return levelText;
    }

    /**
     * letter format으로 변환된 글머리 번호를 반환합니다.
     * @private
     * @param {Number} paragraphId paragraph id
     * @param {Number} id 글머리번호 id
     * @param {Number} level 글머리번호 level
     * @param {Boolean} isUpper 대문자 여부 판단
     * @returns {String}
     */
    getConvertedLetter(paragraphId, id, level, isUpper) {
        let levelText = this.listsManager.getLevelText(id, level);
        let levelValue = this.listsManageMap[paragraphId];

        if (util.isArray(levelValue)) {
            let number = util.last(levelValue);
            let subStr = `%${parseInt(levelText.split('%')[1])}`;
            let newSubStr = util.decimalToLetter(number, isUpper);
            levelText = levelText.replace(subStr, newSubStr);
        }

        return levelText;
    }

    /**
     * roman format으로 변환된 글머리 번호를 반환합니다.
     * @private
     * @param {Number} paragraphId paragraph id
     * @param {Number} id 글머리번호 id
     * @param {Number} level 글머리번호 level
     * @param {Boolean} isUpper 대문자 여부 판단
     * @returns {String}
     */
    getConvertedRoman(paraId, id, level, isUpper) {
        let levelText = this.listsManager.getLevelText(id, level);
        let levelValue = this.listsManageMap[paraId];

        if (util.isArray(levelValue)) {
            let number = util.last(levelValue);
            let subStr = `%${parseInt(levelText.split('%')[1])}`;
            let newSubStr = util.decimalToRoman(number, isUpper);
            levelText = levelText.replace(subStr, newSubStr);
        }

        return levelText;
    }

    /**
     * circle decimal format으로 변환된 글머리 번호를 반환합니다.
     * @private
     * @param {Number} paragraphId paragraph id
     * @param {Number} id 글머리번호 id
     * @param {Number} level 글머리번호 level
     * @returns {String}
     */
    getConvertedCircleDecimal(paragraphId, id, level) {
        let levelText = this.listsManager.getLevelText(id, level);
        let levelValue = this.listsManageMap[paragraphId];

        if (util.isArray(levelValue)) {
            let number = util.last(levelValue);
            let subStr = `%${parseInt(levelText.split('%')[1])}`;
            let newSubStr = util.decimalToCircleDecimal(number);
            levelText = levelText.replace(subStr, newSubStr);
        }

        return levelText;
    }

    /**
     * ganada format으로 변환된 글머리 번호를 반환합니다.
     * @private
     * @param {Number} paragraphId paragraph id
     * @param {Number} id 글머리번호 id
     * @param {Number} level 글머리번호 level
     * @returns {String}
     */
    getConvertedGanada(paragraphId, id, level) {
        let levelText = this.listsManager.getLevelText(id, level);
        let levelValue = this.listsManageMap[paragraphId];

        if (util.isArray(levelValue)) {
            let number = util.last(levelValue);
            let subStr = `%${parseInt(levelText.split('%')[1])}`;
            let newSubStr = util.decimalToGanada(number);
            levelText = levelText.replace(subStr, newSubStr);
        }

        return levelText;
    }

    /**
     * chosung format으로 변환된 글머리 번호를 반환합니다.
     * @private
     * @param {Number} paragraphId paragraph id
     * @param {Number} id 글머리번호 id
     * @param {Number} level 글머리번호 level
     * @returns {String}
     */
    getConvertedChosung(paragraphId, id, level) {
        let levelText = this.listsManager.getLevelText(id, level);
        let levelValue = this.listsManageMap[paragraphId];

        if (util.isArray(levelValue)) {
            let number = util.last(levelValue);
            let subStr = `%${parseInt(levelText.split('%')[1])}`;
            let newSubStr = util.decimalToChosung(number);
            levelText = levelText.replace(subStr, newSubStr);
        }

        return levelText;
    }
}
export default ListsRenderer;


