/**
 * 워드의 테마속성을 관리하는 Class입니다.
 */
class ThemeListManager {
    constructor() {
        this.list = null;
    }

    setList(list) {
        this.list = list;
    }

    getList() {
        return this.list;
    }
}

export default ThemeListManager;


