import TextRun from './TextRun';

/**
 * HyperlinkEnd
 * @extends TextRun
 */
class HyperlinkEnd extends TextRun {
    /**
     * HyperlinkEnd 생성하여 반환합니다.
     * @param {Number} id
     * @return {HyperlinkEnd}
     */
    static createHyperlinkEnd(id) {
        return new HyperlinkEnd({
            id: id
        });
    }

    /**
     * HyperlinkEnd length를 반환합니다.
     * @returns {Number}
     */
    length() {
        return 0;
    }

    /**
     * Element의 Type을 반환합니다.
     * @returns {String} HyperlinkEnd
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'HyperlinkEnd';
    }

    /**
     * 빈 TextRun을 생성하여 index에 맞게 HyperlinkEnd 앞 뒤로 삽입하고 TextRun을 반환합니다.
     * @param {Number} index 
     * @return {TextRun} run
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }
}

export default HyperlinkEnd;


