import Element from './Element';
import util from '../util/util';
import CellIndex from './CellIndex';

/**
 * TableCell
 * @extends Element
 */
class TableCell extends Element {
    __getChildrenProperty__() {
        return 'paragraph';
    }
    /**
     * Element의 Type을 반환합니다.
     * @returns {String} - Element Type
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'TableCell';
    }

    /**
     * TableCell Element의 properties를 반환합니다.
     * @returns {Object} - TableCell Element Properties
     */
    getProp() {
        return this.prop;
    }

    /**
     * TableCell Element의 width 속성의 값 반환합니다.
     * @returns {Number} - TableCell Element의 width 속성 값
     */
    getWidth() {
        let oWidth = util.get(this.getProp(), ['width']);
        let width = -1;
        if (oWidth.type === 'sunit') {
            width = util.sunit2px(oWidth.value);
        } else {
            //auto
        }

        return width;
    }

    /**
     * TableCell의 rowSpan을 반환합니다.<br/>
     * 기본값은 1입니다.
     * @returns {Number}
     */
    getRowSpan() {
        let defaultRowSpan = this.isMerged() ? 0 : 1;
        return util.get(this.getProp(), ['row_span']) || defaultRowSpan;
    }

    /**
     * TableCell의 colSpan 반환합니다.<br/>
     * 기본값은 1입니다.
     * @returns {Number}
     */
    getColSpan() {
        let defaultColSpan = this.isMerged() ? 0 : 1;
        return util.get(this.getProp(), ['col_span']) || defaultColSpan;
    }

    /**
     * 병합된 cell인지 여부를 판단합니다.
     * @returns {Boolean}
     */
    isMerged() {
        return ['horz_merged', 'vert_merged'].some(merged => util.has(this.getProp(), [merged]));
    }

    /**
     * 병합한 cell을 찾아줍니다.(좌상단의 cell을 찾아줌)
     * @returns {TableCell}
     */
    getMergeCell() {
        if (!this.isMerged()) {
            return this;
        }
        const isVertMerged = util.has(this.getProp(), ['vert_merged']);
        const isHorzMerged = util.has(this.getProp(), ['horz_merged']);
        const row = this.getTable().nthChild(this.getRowIndex() + (isVertMerged ? -1 : 0));
        const cell = row.nthChild(this.getColIndex() + (isHorzMerged ? -1 : 0));
        return cell.getMergeCell();
    }

    /**
     * 현재 cell의 영역(CellIndex)을 반환합니다.
     * @returns {CellIndex}
     */
    getCellIndex() {
        if (this.isMerged()) {
            return null;
        }
        let rowIndex = this.getRowIndex();
        let colIndex = this.getColIndex();
        let endRowIndex = rowIndex + this.getRowSpan() - 1;
        let endColIndex = colIndex + this.getColSpan() - 1;
        return new CellIndex(rowIndex, colIndex, endRowIndex, endColIndex);
    }

    /**
     * TableCell Element의 vertical align 속성을 반환합니다.
     * 반환되는 값은 top, center, bottom 중 하나이며, 기본 값은 top입니다.
     * @returns {String} - TableCell Element의 vertical align 속성
     */
    getVertAlign() {
        return util.get(this.getProp(), ['vert_align'], 'top');
    }

    /**
     * TableCell Element의 border 속성을 반환합니다.
     * @returns {Object} - TableCell Element의 borders 속성
     */
    getBorders() {
        return util.get(this.getProp(), ['borders'], {});
    }

    /**
     * TableCell Element에 속한 Paragraph들을 배열에 담아 반환합니다.
     * @returns {Array} paragraph 배열
     */
    getParagraphs() {
        return this.getChildren().reduce((paragraphs, paragraph) => {
            paragraph.getChildren().forEach(run => {
                if (util.isTable(run)) {
                    paragraphs.push(...run.getParagraphs());
                }
            });
            paragraphs.push(paragraph);
            return paragraphs;
        }, []);
    }

    /**
     * TableCell Element의 상위 Table Element를 반환합니다.
     * @returns {Table} - Table Element
     */
    getTable() {
        return this.closest(util.isTable);
    }

    /**
     * TableCell의 row index를 반환합니다
     * @returns {Number}
     */
    getRowIndex() {
        return this.parent().index();
    }

    /**
     * TableCell의 col index를 반환합니다.
     * @param {Boolean} [exceptMergedCell = false] 병합된 셀을 제외할지 여부
     * @returns {Number}
     */
    getColIndex(exceptMergedCell = false) {
        let index = this.index();
        if (exceptMergedCell) {
            let cell = this;
            while(cell = cell.previousSibling()) {
                if(cell.isMerged()) {
                    index -= 1;
                }
            }
        }
        return index;
    }

    /**
     * table cell 모델의 fill prop을 설정합니다.
     * @param {String} hex
     */
    setBackground(hex) {
        let prop = this.getProp();
        prop.fill = {
            'solid_fill': {
                'color': {
                    'srgb_color': {
                        'value_in_hex': util.hexToDecimal(hex)
                    },
                    'type': 'SRGB'
                }
            },
            'type': 'SOLID_FILL'
        };
    }

    /**
     * table cell 모델의 세로정렬을 설정합니다.
     * @param {String} value 
     */
    setVertAlign(value) {
        util.set(this.getProp(), 'vert_align', value);
    }
}

export default TableCell;


