/**
 * vanuatu message Decoder
 */

/**
	vmDecoder class constructor
	* vmDescriptor 
		- initial vm message descriptor
	* options
		- decoder options
	Example)
		var decoder = new vmDecoder(vmParser, vmCellDescriptor);
 */
var vmDecoder = function (vmParser, vmDescriptor) {
	this.parser = vmParser;
	this.vmDescriptor = vmDescriptor;
	this.maskMap = {
		'MESSAGE': 0xFFFFFFFFFF,
		'UNSIGNED_INT': 0xFFFFFFFFFFFFF,
		'SIGNED_INT': 0x7FFFFFFFFFFFF
	};
};

/**
	vmDecoder prototype functions
 */
vmDecoder.prototype = {
	// get decoder by tag name
	setDecoderByTag: function (tag, descriptor) {
		var func_name = null;
		var field_name = null;
		var targetDescriptor = null;
		var _descriptor = descriptor || this.vmDescriptor.descriptor;
		var func_index = this.vmDescriptor.enum_decoder.enum_func_name;
		var decoder = !!this.vmDescriptor[_descriptor] ? this.vmDescriptor[_descriptor][tag.toString()] : null;
		if (!!decoder) {
			targetDescriptor = decoder[this.vmDescriptor.enum_decoder.descriptor];
			func_name = this.vmDescriptor.enum_func_name[decoder[func_index]];
			field_name = decoder[this.vmDescriptor.enum_decoder.name];
		};
		if (!!func_name && !!this.vmDescriptor[targetDescriptor]) {
			this.vmDescriptor.descriptor = targetDescriptor;
		}
		this.tag = tag;
		this.decoder = decoder;
		this.field_name = field_name;
		this.func_name = func_name;
		this.decoder_func = !!func_name ? this[func_name].bind(this) : null;
	},

	// decode repeat field
	repeated: function (pos) {
		var result;
		var new_pos;
		var tag_len = this.tag.length;
		var descriptor = this.vmDescriptor.descriptor;
		while (1) {
			result = this.unsignedInt(pos);
			new_pos = result.value + result.pos;
			this.parser.parse(result.pos, new_pos, descriptor);

			pos = new_pos + tag_len;
			if ((this.parser.buffer.slice(new_pos, pos) != this.tag) || new_pos == end) {
				this.moveJsonValueToParent();
				return new_pos;
			}
		}
	},

	// decode message field
	message: function (pos) {
		var result = this.unsignedInt(pos);
		var new_pos = result.value + result.pos;
		var descriptor = this.vmDescriptor.descriptor;
		this.parser.parse(result.pos, new_pos, descriptor);
		this.moveJsonValueToParent();
		return new_pos;
	},

	// decode bytes type field
	bytesField: function (pos) {
		var result = this.unsignedInt(pos);
		var new_pos = result.value + result.pos;
		var value = this.parser.buffer.slice(result.pos, new_pos);
		this.setValue(value);
		return new_pos;
	},

	// decode signed type field
	signedField: function (pos) {
		var result = this.signedInt(pos);
		this.setValue(result.value);
		return result.pos;
	},

	// decode unsigned type field
	unsignedField: function (pos) {
		var result = this.unsignedInt(pos);
		this.setValue(result.value);
		return result.pos;
	},

	// decode string type field
	stringField: function (pos) {
		var result = this.signedInt(pos);
		var new_pos = result.value + result.pos;
		var value = this.parser.arrToString(this.parser.buffer.slice(result.pos, new_pos));
		this.setValue(value);
		return new_pos;
	},

	// decode repeated string type field
	repeatedStringField: function (pos, end) {
		var result;
		var new_pos;
		var new_tag;
		var value = [];
		var tag_len = this.tag.length;
		var tag_str = JSON.stringify(this.tag);
		while(1){
			result = this.signedInt(pos);
			new_pos = result.value + result.pos;
			value.push(this.parser.arrToString(this.parser.buffer.slice(result.pos, new_pos)));
			pos = new_pos + tag_len;
			new_tag = this.parser.buffer.slice(new_pos, pos);
			if ( JSON.stringify(new_tag) != tag_str || new_pos == end ) {
				this.setValue(value, true);
				this.moveJsonValueToParent();
				return new_pos;
			}
		}
	},

	// decode double type field
	doubleField: function (pos) {
		var new_pos = pos + 8;
		var bytes = this.parser.buffer.slice(pos, new_pos);
		this.setValue(this.parser.toDouble(bytes));
		return new_pos;
	},

	// decode float type field
	floatField: function (pos) {
		var new_pos = pos + 4;
		var bytes = this.parser.buffer.slice(pos, new_pos);
		this.setValue(this.parser.toFloat(bytes));
		return new_pos;
	},

	// decode enum type field
	enumField: function (pos) {
		var result = this.signedInt(pos);
		var enumDescriptor = this.decoder[this.vmDescriptor.enum_decoder.descriptor];
		var enumValue = this.vmDescriptor.enumDescriptor[enumDescriptor][result.value];
		this.setValue(enumValue);
		return result.pos;
	},

	unsignedInt: function (pos) {
		var result = 0;
		var shift = 0;
		while (1) {
			var b = this.parser.buffer[pos];
			result |= ((b & 0x7f) << shift);
			pos += 1;
			shift += 7;
			if (!(b & 0x80)) {
				result &= this.maskMap['UNSIGNED_INT'];
				result = result >>> 0;
				return { value: result, pos: pos };
			}
			if (shift >= 64) {
				console.log('ERROR');
				break;
			}
		}
	},

	signedInt: function (pos) {
		var result = 0;
		var shift = 0;
		while (1) {
			var b = this.parser.buffer[pos];
			if(result > -1){
				result |= ((b & 0x7f) << shift);
			}
			pos += 1;
			shift += 7;
			if (!(b & 0x80)) {
				result &= this.maskMap['SIGNED_INT'];
				return { value: result, pos: pos };
			}
			if (shift >= 64) {
				console.log('ERROR');
				break;
			}
		}
	},

	// set last jsonPath value
	setValue: function (value, valueOnly) {
		var current = this.parser.jsonPath[this.parser.jsonPath.length - 1];
		var parent = this.parser.jsonPath[this.parser.jsonPath.length - 2];
		var jsonNode = this.parser.jsonPath[this.parser.jsonPath.length - 1];
		if (jsonNode.is_repeat) {
			if (!!jsonNode['value']) {
				jsonNode.value[jsonNode.value.length - 1][this.decoder[this.vmDescriptor.enum_decoder.name]] = value;
			} else {
				var v = {};
				v[this.decoder[this.vmDescriptor.enum_decoder.name]] = value;
				jsonNode.value = new Array(v);
			}
		} else if(!!valueOnly) {
			jsonNode.value = value;
		} else {
			jsonNode.value = jsonNode['value'] || {};
			jsonNode.value[this.decoder[this.vmDescriptor.enum_decoder.name]] = value;
		}
	},

	// move last jsonPath value to parent jsonPath
	moveJsonValueToParent: function () {
		var current = this.parser.jsonPath.pop();
		var parent = this.parser.jsonPath[this.parser.jsonPath.length - 1];
		parent.value = parent.value || (parent.is_repeat ? [] : {});
		if (parent.is_repeat) {
			if (parent.value.length > 0) {
				if (current.is_repeat) {
					parent.value[0][current.name] = parent.value[0][current.name] ? parent.value[0][current.name] : [];
					parent.value[0][current.name].push(current.value[0]);
				} else {
					parent.value[parent.value.length - 1][current.name] = current.value;
				}
			} else {
				var v = {};
				v[current.name] = current.value;
				parent.value.push(v);
			}
		} else {
			if (current.is_repeat && !!parent.value[current.name]) {
				parent.value[current.name].push(current.value ? current.value[0] : null);
			} else {
				parent.value[current.name] = current.value;
			}
		}
	}
};

module.exports = vmDecoder;


