import $ from 'jquery';
import shortcut from './shortcut';
import ACTION from './consts/actionMap';
import util from './util/util';


/**
 * 입력 이벤트를 관리하는 Class입니다. <br/>
 * browser key event를 가로채 컨트롤합니다. <br/>
 * Ime Element로는 textarea 사용하며 keyDown, input 이벤트를 관리합니다. <br/>
 */
class Ime {
    constructor(editorId, controller) {
        this.controller = controller;
        this.editorId = editorId;
        this.$elImeContainer = $(`#${this.editorId} .ime-container`);
        this.$elIme = $(`#${this.editorId} .ime`);
        this.disabled = false;
        this.prevInput = '';

        this.init();
    }

    /**
     * Ime를 초기화 합니다.
     */
    init() {
        if (!this.$elImeContainer.length) {
            // ime에 직접 display none, visibility hidden 할 경우 event 발생하지 않아 ime-container로
            // 숨긴다.
            this.$elImeContainer = $('<div class="ime-container"></div>');
            this.$elIme = $('<textarea class="ime"></textarea>');
            this.$elImeContainer.append(this.$elIme);
            $(`#${this.editorId} .se-main`).append(this.$elImeContainer);
        }
        this.$elIme.on('input keydown copy paste', this, this.bindEvent);
    }

    /**
     * Ime HTML Element focus를 잡아줍니다.
     */
    focus() {
        this.$elIme.focus();
    }

    /**
     * Ime HTML Element blur 해줍니다. (focus 제거)
     */
    blur() {
        this.$elIme.blur();
    }

    /**
     * 이벤트를 바인딩 합니다. (keyDown, input, paste)
     * @param {Event} e
     */
    bindEvent(e) {
        if (this.disabled) {
            e.preventDefault();
        } else {
            switch (e.type) {
                case 'keydown':
                    let isMetaCtrl = e.originalEvent.ctrlKey || e.originalEvent.metaKey;
                    if (isMetaCtrl && e.originalEvent.keyCode === 67 && util.getUserAgent(window.navigator.userAgent) === 'MSIE') { // ie에서는 copy event를 binding 해도 copy를 타지 않음.
                        onCopy(e);
                        break;
                    } else if (isMetaCtrl && (e.originalEvent.keyCode === 86 || e.originalEvent.keyCode === 67)) { // copy paste시 keydown회피하도록 처리 처리하지 않을경우 이벤트가 안먹힘.
                        return;
                    }
                    onKeyDown(e);
                    break;
                case 'input':
                    onInput(e);
                    break;
                case 'copy':
                    onCopy(e);
                    break;
                case 'paste':
                    onPaste(e);
                    break;
            }
        }
    }

    /**
     * Ime의 데이터(입력된 텍스트)를 리셋합니다.<br/>
     * enter, shortcut 등 입력이 발생하면 호출됩니다.
     */
    resetIme() {
        this.prevInput = '';
        this.$elIme.val('');
    }

    /**
     * 인자로 받은 left, top 값으로 Ime Contaeinr 위치를 보정합니다.
     * @param {Number} left 
     * @param {Number} top 
     */
    moveIme(left, top) {
        // Ime 위치 보정
        this.$elImeContainer.css({
            left: left,
            top: top
        });
    }
}

/**
 * Ime keydown 이벤트를 처리합니다. <br/>
 * event에 해당되는 action이 있는지 검사후 controller를 통하여 액션을 실행합니다.
 * @param {Event} e
 */
function onKeyDown(e) {
    const ime = e.data;
    const action = shortcut(e);
    if (!!action) {
        e.preventDefault();
        e.stopPropagation();

        ime.controller.execute(action);
        ime.resetIme();
    }
}

/**
 * Ime input 이벤트를 처리합니다. <br/>
 * controller를 통하여 ACTION.INSERT_TEXT를 실행합니다.
 * @param {Event} e
 */
function onInput(e) {
    const ime = e.data;
    const text = ime.$elIme.val(), prevInput = ime.prevInput;
    let same = 0, l = Math.min(prevInput.length, text.length);
    while (same < l && prevInput.charCodeAt(same) == text.charCodeAt(same)) {
        ++same;
    }

    const updateTextLength = prevInput.length - same;
    const insertText = text.slice(same);

    ime.controller.execute(ACTION.INSERT_TEXT, insertText, updateTextLength);
    ime.prevInput = text;
}

/**
 * Ime paste 이벤트를 처리합니다. <br/>
 * controller를 통하여 ACTION.PASTE를 실행합니다.
 * @param {Event} e 
 */
function onPaste(e) {
    const ime = e.data;
    e.preventDefault();
    e.stopPropagation();
    ime.controller.execute('paste', e);
}

/**
 * Ime copy 이벤트를 처리합니다. <br/>
 * controller를 통하여 ACTION.COPY를 실행합니다.
 * @param {Event} e 
 */
function onCopy(e) {
    const ime = e.data;
    e.preventDefault();
    e.stopPropagation();
    ime.controller.execute('copy', e);
}

export default Ime;


