/**
 * 유틸리티입니다.
 * @static
 * @namespace util
 */
const ModelUtil = {
    /**
     * 인자로 받은 Element가 Paragraph인지 검사합니다.
     * Paragraph Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean} 
     */
    isParagraph: function (element) {
        return !!element && element.getType() === 'Paragraph';
    },

    /**
     * 인자로 받은 Element가 Run인지를 검사합니다.
     * Run level의 Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean}
     */
    isRun: function (element) {
        return this.isTextRun(element) || this.isTable(element) || this.isPicture(element) || this.isHyperlink(element);
    },

    /**
     * 인자로 받은 Element가 TextRun인지 검사합니다.
     * TextRun Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean} 
     */
    isTextRun: function (element) {
        return !!element && element.getType() === 'TextRun';
    },

    /**
     * Element가 line break를 인지 검사합니다.</br>
     * line break라면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean}
     */
    isLineBreak: function (element) {
        return this.isTextRun(element) && !!element.break && element.break.type === 'line';
    },

    /**
     * Element가 Tab 인지 검사합니다.</br>
     * Tab이라면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean}
     */
    isTab: function(element) {
        return this.isTextRun(element) && !!element.tab;
    },

    /**
     * 인자로 받은 Element가 Hyperlink인지 검사합니다.
     * Hyperlink Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean} 
     */
    isHyperlink: function (element) {
        return !!element && (element.getType() === 'HyperlinkStart' || element.getType() === 'HyperlinkEnd');
    },

    /**
     * Element가 Bookmark인지 검사합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean}
     */
    isBookmark: function (element) {
        return !!element && (element.getType() === 'BookmarkStart' || element.getType() === 'BookmarkEnd');
    },

    /**
     * 인자로 받은 Element가 Table인지 검사합니다.
     * Table Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean} 
     */
    isTable: function (element) {
        return !!element && element.getType() === 'Table';
    },

    /**
     * 인자로 받은 Element가 TableRow인지 검사합니다.
     * TableRow Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean} 
     */
    isTableRow: function (element) {
        return !!element && element.getType() === 'TableRow';
    },

    /**
     * 인자로 받은 Element가 TableCell인지 검사합니다.
     * TableCell Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean} 
     */
    isTableCell: function (element) {
        return !!element && element.getType() === 'TableCell';
    },

    /**
     * 인자로 받은 Element가 Picture 검사합니다.
     * Picture Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean} 
     */
    isPicture: function (element) {
        return !!element && element.getType() === 'Picture';
    },

    /**
     * 인자로 받은 Element가 HorizontalLine인지 검사합니다.
     * HorizontalLine Element이면 true를 반환합니다.
     * @param {Element} element SEModel의 하위 Element
     * @returns {Boolean} 
     */
    isHorizontalLine: function (element) {
        return !!element && element.getType() === 'HorizontalLine';
    },

    /**
     * 인자로 받은 Run Element를 index에 맞게 split 합니다. <br/>
     * index가 Run의 시작 (0) 이거나, Run의 길이와 같을경우 자르지 않고 그대로 반환합니다.
     * @param {Element} run
     * @param {Number} index
     * @returns {Array} runs
     */
    splitRun: function(run, index) {
        if(index === 0 || index === run.length()) {
            return [run];
        } else {
            return [run, run.split(index)];
        }
    }
};

export default ModelUtil;


