import $ from 'jquery';
import ACTION from '../consts/actionMap';
import util from '../util/util';
import consts from '../consts/consts';

const MIN_PICTURE_SIZE = consts.MIN_PICTURE_SIZE;

/**
 * Synap Editor에서 발생하는 Mouse Event를 관리하는 Class입니다. <br>
 */
class MouseEventManager {
    /**
     * MouseEventManager.getInstance()와 같이 사용합니다. <br/>
     * new MouseEventManager()과 같은 형식으로는 사용을 권장하지 않습니다.
     */
    constructor(editorId, controller, selectionManager) {
        this.editorId = editorId;
        this.controller = controller;
        this.selectionManager = selectionManager;
        this.selection = this.selectionManager.selection;
        this.$elMain = $(`#${this.editorId} .se-main`);
        this.$elContents = this.$elMain.find(`.se-contents`);
        this.$elPictureSelection = $(`#${this.editorId} .se-picture-selection`);
        this.init();
    }

    /**
     * Mouse Event를 초기화 합니다.
     */
    init() {
        this.$elMain.on('mousedown', onMouseDown.bind(this));
        this.$elMain.on('dblclick', onDoubleClick.bind(this));
        $(this.$elPictureSelection.children()).on('mousedown', onResizePicture.bind(this));
    }
}

/**
 * contents에서 mouse down 이벤트를 처리합니다.
 * @private
 * @param {mouseEvent} e 
 */
function onMouseDown(e) {
    e.preventDefault();
    
    if (e.which === 1) { // left mouse button
        if (util.isInSEContents(e.target, this.editorId) || $(e.target).hasClass('se-main')) {
            this.controller.execute(ACTION.SET_SELECTION, e.clientX, e.clientY, e.shiftKey);
            $(util.getWindows()).on('mousemove', this, onMouseMove).on('mouseup', this, onMouseUp);
        } else {
            this.controller.execute(ACTION.SET_SELECTION_END, e.target);
        }
    }
}

/**
 * contents에서 mouse move 이벤트를 처리합니다.
 * @private
 * @param {mouseEvent} e 
 */
function onMouseMove(e) {
    const {controller, editorId, selection, $elContents} = e.data;
    e.preventDefault();

    if (selection.isPictureSelection()) {
        $elContents.find(`img[pic-id="${selection.focus.pictureId}"]`).css('opacity', 0.4);
        controller.execute(ACTION.MOVE_PICTURE, e.clientX, e.clientY);
    } else if (util.isInSEContents(e.target, editorId) || $(e.target).hasClass('se-main')) {
        controller.execute(ACTION.SET_SELECTION, e.clientX, e.clientY, true);
    }
}

/**
 * contents에서 mouse up 이벤트를 처리합니다.
 * @private
 * @param {mouseEvent} e 
 */
function onMouseUp(e) {
    const {controller, selection} = e.data;
    e.preventDefault();

    if (selection.isPictureSelection()) {
        controller.execute(ACTION.MOVE_PICTURE, e.clientX, e.clientY, true);
    }
    
    controller.execute(ACTION.SET_SELECTION_END);
    $(util.getWindows()).off('mousemove', onMouseMove).off('mouseup', onMouseUp);
}

/**
 * contents에서 double click 이벤트를 처리합니다.
 * @param {mouseEvent} e 
 */
function onDoubleClick(e) {
    const controller = this.controller;
    e.preventDefault();
    controller.execute(ACTION.SELECT_WORD, e.clientX, e.clientY);
}

/**
 * PictureSelection에서 mouse resize 이벤트를 처리합니다.
 * @param {MouseEvent} e 
 */
function onResizePicture(e) {
    e.preventDefault();
    const controller = this.controller;
    const $elContents = this.$elContents;
    const $target = $(e.target);
    const picId = this.$elPictureSelection.attr('select-pic-id');
    const $elPicture = $(`#${this.editorId} [pic-id="${picId}"]`);
    const originBounds = {
        width: $elPicture.width(),
        height: $elPicture.height(),
        x: e.clientX,
        y: e.clientY
    };
    const getWidthHeight = (e) => {
        const movePosition = {
            x: e.clientX,
            y: e.clientY
        };
        let {width, height} = originBounds;
        switch (true) {
            case $target.hasClass('middle') && $target.hasClass('top'):
                height = originBounds.height + originBounds.y - movePosition.y;
                break;
            case $target.hasClass('middle') && $target.hasClass('bottom'):
                height = originBounds.height + movePosition.y - originBounds.y;
                break;
            case $target.hasClass('left') && $target.hasClass('top'):
                width = originBounds.width + originBounds.x - movePosition.x;
                height = originBounds.height + originBounds.y - movePosition.y;
                break;
            case $target.hasClass('left') && $target.hasClass('middle'):
                width = originBounds.width + originBounds.x - movePosition.x;
                break;
            case $target.hasClass('left') && $target.hasClass('bottom'):
                width = originBounds.width + originBounds.x - movePosition.x;
                height = originBounds.height + movePosition.y - originBounds.y;
                break;
            case $target.hasClass('right') && $target.hasClass('top'):
                width = originBounds.width + movePosition.x - originBounds.x;
                height = originBounds.height + originBounds.y - movePosition.y;
                break;
            case $target.hasClass('right') && $target.hasClass('middle'):
                width = originBounds.width + movePosition.x - originBounds.x;
                break;
            case $target.hasClass('right') && $target.hasClass('bottom'):
                width = originBounds.width + movePosition.x - originBounds.x;
                height = originBounds.height + movePosition.y - originBounds.y;
                break;
        }

        return {
            width: Math.min(Math.max(width, MIN_PICTURE_SIZE), $elContents.width()),
            height: Math.max(height, MIN_PICTURE_SIZE)
        };
    };

    if (e.which === 1) { // left mouse button
        /**
         * PictureSelection에서 mouse move 이벤트를 처리합니다.
         * @param {MouseEvent} e 
         */
        const onMouseMove = (e) => {
            e.preventDefault();
            const {width, height} = getWidthHeight(e);
            controller.execute(ACTION.RESIZE_PICTURE, width, height);
        };

        /**
         * PictureSelection에서 mouse up 이벤트를 처리합니다.
         * @param {MouseEvent} e 
         */
        const onMouseUp = (e) => {
            e.preventDefault();
            const {width, height} = getWidthHeight(e);
            controller.execute(ACTION.RESIZE_PICTURE, width, height, true);
            $(util.getWindows()).off('mousemove', onMouseMove).off('mouseup', onMouseUp);
        };

        $(util.getWindows()).on('mousemove', e.data, onMouseMove).on('mouseup', e.data, onMouseUp);
    }
}

export default MouseEventManager;


