import ACTION from '../consts/actionMap';
import ControllerInterface from './ControllerInterface';
import PicturePosition from '../model/selection/PicturePosition';

/**
 * 개체 편집동작을 수행하는 class 입니다.<br>
 * 개체 크기조절 및 이동 등을 수행합니다.
 */
class PictureController extends ControllerInterface {
    constructor(editor, name) {
        super();
        this.name = name;
        this.editorId = editor.id;
        this.ime = editor.getIme();
        this.renderer = editor.getRenderer();
        this.selectionRenderer = editor.getSelectionRenderer();
        this.selectionManager = editor.getSelectionManager();
        this.modelHandler = editor.getModelHandler();
        this.SEModelManager = editor.getSEModelManager();
        this.uiManager = editor.getUIManager();
        this.selection = this.selectionManager.selection;
        this.pictureHandler = this.selectionRenderer.pictureHandler;
        this.undoRedo = editor.getUndoRedo();
    }

    /**
     * 문단 편집동작 이전에 렌더링될 문단을 저장합니다.
     */
    preExecute() {
        this.renderTargetParagraphs = this.selection.getParagraphs();
        this.undoRedo.clearRedo();
    }

    /**
     * 문단 편집동작 이후 문단 및 셀랙션을 렌더링하고 기본 x좌표값을 설정합니다.
     */
    postExecute() {
        this.renderer.renderParagraphs(this.renderTargetParagraphs);
        this.selectionRenderer.reviseScroll(this.selection.focus);
        this.selectionRenderer.renderSelection(this.selection);
    }
}

/**
 * picture 관련 동작을 수행하는 pictureController 객체입니다.
 * 각 동작 메소드는 PictureController를 상속 받습니다.
 */
const pictureController = {
    /**
     * movePosition에 따라 Picture Selection과 Object 크기를 조절합니다.
     * @param {Number} width 
     * @param {Number} height
     * @param {Boolean} [isFinish = false] resize가 끝났는지 여부
     */
    [ACTION.RESIZE_PICTURE]: class extends PictureController {
        execute(width, height, isFinish = false) {
            this.isFinish = isFinish;
            if (this.isFinish) {
                const anchor = this.selection.anchor;
                this.modelHandler.resizePicture(anchor.pictureId, width, height);
            } else {
                this.uiManager.hideAllBalloonPopup();
                this.pictureHandler.resize(width, height);
            }
        }

        postExecute() {
            if(this.isFinish) {
                super.postExecute();
            }
        }
    },
    /**
     * movePosition에 따라 Picture SEModel 위치를 조절합니다.
     * @param {Number} x
     * @param {Number} y
     * @param {Boolean} [isFinish = false] move가 끝났는지 여부
     */
    [ACTION.MOVE_PICTURE]: class extends PictureController {
        execute(x, y, isFinish = false) {
            const selection = this.selectionManager.getSelectionFromPoint(x, y);
            if (isFinish) {
                const anchor = this.selection.anchor;
                const pictureId = anchor.pictureId;
                const position = selection.getStartPosition();
                this.selection.setCaret(new PicturePosition(position.id, position.offset, pictureId));

                this.modelHandler.movePicture(pictureId, position);
                this.renderTargetParagraphs.push(this.SEModelManager.getParagraph(position.id));
            } else {
                this.uiManager.hideAllBalloonPopup();
                this.renderTargetParagraphs = [];
                this.selection = selection;
            }
        }
    },
    /**
     * Picture SEModel rotation을 왼쪽으로 조절합니다.
     */
    [ACTION.ROTATE_PICTURE_LEFT]: class extends PictureController {
        execute() {
            const anchor = this.selection.anchor;
            this.modelHandler.rotatePicture(anchor.pictureId, 'left');
            this.renderTargetParagraphs.push(this.SEModelManager.getParagraph(anchor.id));
        }
    },
    /**
     * Picture SEModel rotation을 오른쪽으로 조절합니다.
     */
    [ACTION.ROTATE_PICTURE_RIGHT]: class extends PictureController {
        execute() {
            const anchor = this.selection.anchor;
            this.modelHandler.rotatePicture(anchor.pictureId, 'right');
            this.renderTargetParagraphs.push(this.SEModelManager.getParagraph(anchor.id));
        }
    }
};

export default pictureController;


