import Element from './Element';
import TextRun from './TextRun';
import CellIndex from './CellIndex';
import util from '../util/util';

/**
 * Table
 * @extends Element
 */
class Table extends Element {
    __getChildrenProperty__() {
        return 'table_row';
    }

    /**
     * Table Element Length를 반환합니다.
     * @returns {Number} Table Element length
     */
    length() {
        return 1;
    }

    /**
     * Table이 비어있는지 검사하여 반환합니다.<br>
     * 기본적으로 Table은 비어있지 않은 것으로 반환시킵니다.
     * @returns {Boolean}
     */
    isEmpty() {
        return this.length() === 0;
    }

    /**
     * Element의 Type을 반환합니다.
     * @returns {String} - Element Type
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'Table';
    }

    /**
     * Table Element의 properties를 반환합니다.
     * @returns {Object} - Table Element Properties
     */
    getProp() {
        return this.prop;
    }

    /**
     * 테이블의 모든 셀을 반환합니다.
     * @param {Boolean} [maintainMergedCell = false] 병합된 셀을 유지할지 여부
     * @returns {Array} [cell, cell, ...] cell Array
     */
    getAllCells(maintainMergedCell = false) {
        const endRowIndex = this.getCountOfRow() - 1;
        const endColIndex = this.getCountOfColumn() - 1;
        const cellIndex = new CellIndex(0, 0, endRowIndex, endColIndex);
        return this.getCells(cellIndex, maintainMergedCell);
    }

    /**
     * 영역에 포함된 셀을 반환합니다.
     * @param {CellIndex} cellIndex 
     * @param {Boolean} [maintainMergedCell = false] 병합된 셀을 유지할지 여부
     * @returns {Array} [cell, cell, ...] cell Array
     */
    getCells(cellIndex, maintainMergedCell = false) {
        let cells = [];
        for(let r = cellIndex.sr; r <= cellIndex.er; r++) {
            for(let c = cellIndex.sc; c <= cellIndex.ec;) {
                let cell = this.getCell(new CellIndex(r, c), true);
                c += cell.getColSpan() || 1;
                
                if(!maintainMergedCell && cell.isMerged()) {
                    continue;
                }
                cells.push(cell);
            }
        }
        return cells;
    }

    /**
     * 영역에 해당하는 cell을 반환합니다.
     * @param {CellIndex} cellIndex 
     * @param {Boolean} [maintainMergedCell = false] 병합된 셀을 유지할지 여부
     * @returns {TableCell}
     */
    getCell(cellIndex, maintainMergedCell = false) {
        const cell = this.nthChild(cellIndex.sr).nthChild(cellIndex.sc);
        return maintainMergedCell ? cell : cell.getMergeCell();
    }

    /**
     * 병합된 셀을 검사하여 영역을 확장시켜 반환합니다.
     * @param {CellIndex} cellIndex 
     * @returns {CellIndex} 확장된 영역
     */
    getExtendCellIndex(cellIndex) {
        let cellCheck = {}; //셀을 검사했는지 여부
        let rows = this.getChildren();
        let {sr, sc, er, ec} = cellIndex;
        for (let r = sr; r <= er; r++) {
            let cols = rows[r].getChildren();
            for (let c = sc; c <= ec;) {
                let cell = cols[c];
                if (cell.isMerged()) {
                    cell = cell.getMergeCell();
                }
                let key = `${cell.getRowIndex()}_${cell.getColIndex()}`;
                
                if (sr === r || er === r) {
                    c += cell.getColSpan() || 1;
                } else if (ec !== c) {
                    c = ec;
                } else {
                    c += 1;
                }
                
                if (cellCheck[key]) {
                    continue;
                } else {
                    cellCheck[key] = true;
                    let targetCellIndex = cell.getCellIndex();
                    if (cellIndex.isOut(targetCellIndex)) {
                        return this.getExtendCellIndex(cellIndex.mergeIndex(targetCellIndex));
                    }
                }
            }
        }
        return cellIndex;
    }

    /**
     * Table Element의 width 속성을 반환합니다.
     * @returns {Number} - Table Element의 width 속성
     */
    getWidth() {
        let oWidth = util.get(this.getProp(), ['width']);
        let width = -1;

        if (oWidth.type === 'sunit') {
            width = util.sunit2px(oWidth.value);
        } else {
            //auto
        }

        return width;
    }

    /**
     * Table Element의 border 속성을 반환합니다.
     * @returns {Object} - Table Element의 borders 속성
     */
    getBorders() {
        return util.get(this.getProp(), ['borders']);
    }

    /**
     * Table Element에 속한 Paragraph들을 배열에 담아 반환합니다.
     * @returns {Array} - paragraph 배열
     */
    getParagraphs() {
        return this.getChildren().reduce((paragraphs, tableRow) => {
            paragraphs.push(...tableRow.getParagraphs());
            return paragraphs;
        }, []);
    }

    /**
     * Table Element에 속한 Run들을 배열에 담아 반환합니다.
     * @returns {Array} - Run 배열
     */
    getRuns() {
        return this.getParagraphs().reduce((runs, paragraph) => {
            runs.push(...paragraph.getChildren());
            return runs;
        }, []);
    }
 
    /**
     * Table Element에 속한 row 개수를 반환합니다. 
     * @returns {Number} - row 개수
     */
    getCountOfRow() {
        return this.getChildren().length;
    }

    /**
     * Table Element에 속한 column 개수를 반환합니다. 
     * @returns {Number} - column 개수
     */
    getCountOfColumn() {
        return this.firstChild().getChildren().length;
    }

    /**
     * Table split시 index에 따라 빈 TextRun 생성하여 반환합니다.
     * @param {Number} index
     * @returns {TextRun} TextRun
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }
}

export default Table;


