/**
 * 편집동작을 정의한 interface 성격의 class입니다.<br/>
 */
class ControllerInterface {
    /**
     * Controller class인지 확인하기 위한 함수 입니다.
     */
    static isControllerClass() {
        return true;
    }

    /**
     * 컨트롤러의 동작을 실행하기 전 실행합니다.
     */
    preExecute() {}
    
    /**
     * 컨트롤러의 동작을 실행합니다.
     * @param {arguments} [args] execute 호출시 받은 인자
     */
    execute(...args) {}
    
    /**
     * 컨트롤러의 동작을 실행한 후 실행합니다.
     */
    postExecute() {}
}

export default ControllerInterface;


