import ACTION from '../consts/actionMap';
import consts from '../consts/consts';

import ControllerInterface from './ControllerInterface';

const ALIGN = consts.HORIZONTAL_ALIGNMENT;
const LISTS_FORMAT = consts.LISTS_FORMAT;

/**
 * 문단 편집동작을 수행하는 class입니다.<br/>
 * editor에서 renderer, caret등을 초기화 합니다.
 */
class ParagraphController extends ControllerInterface {
    constructor(editor, name) {
        super();
        this.name = name;

        this.renderer = editor.getRenderer();
        this.selectionRenderer = editor.getSelectionRenderer();
        this.listsRenderer = this.renderer.getListsRenderer();

        this.selectionManager = editor.getSelectionManager();
        this.positionManager = editor.getPositionManager();
        this.SEModelManager = editor.getSEModelManager();
        this.listsManager = editor.getListsManager();

        this.modelHandler = editor.getModelHandler();

        this.ime = editor.getIme();
        this.undoRedo = editor.getUndoRedo();
        this.selection = this.selectionManager.selection;

        this.bSavePositionX = true;
    }

    /**
     * 문단 편집동작 이전에 렌더링될 문단을 저장합니다.
     */
    preExecute() {
        this.renderTargetParagraphs = this.selection.getParagraphs();
        this.undoRedo.clearRedo();
    }

    /**
     * 문단 편집동작 이후 문단 및 셀랙션을 렌더링하고 기본 x좌표값을 설정합니다.
     */
    postExecute() {
        this.listsRenderer.generateListsCss();
        this.renderer.renderParagraphs(this.renderTargetParagraphs);
        this.selectionRenderer.renderSelection(this.selection);
        this.bSavePositionX && this.selectionManager.setStandardLeft();
        this.ime.focus();
    }
}

const paragraphController = {
    [ACTION.ALIGN_LEFT]: class extends ParagraphController {
        execute() {
            const deepParagraphs = this.selection.getParagraphs(true);
            this.modelHandler.setAlign(deepParagraphs, ALIGN.LEFT);
        }
    },
    [ACTION.ALIGN_CENTER]: class extends ParagraphController {
        execute() {
            const deepParagraphs = this.selection.getParagraphs(true);
            this.modelHandler.setAlign(deepParagraphs, ALIGN.CENTER);
        }
    },
    [ACTION.ALIGN_RIGHT]: class extends ParagraphController {
        execute() {
            const deepParagraphs = this.selection.getParagraphs(true);
            this.modelHandler.setAlign(deepParagraphs, ALIGN.RIGHT);
        }
    },
    [ACTION.ALIGN_JUSTIFY]: class extends ParagraphController {
        execute() {
            const deepParagraphs = this.selection.getParagraphs(true);
            this.modelHandler.setAlign(deepParagraphs, ALIGN.JUSTIFY);
        }
    },
    [ACTION.INCREASE_INDENT]: class extends ParagraphController {
        execute() {
            let deepParagraphs = this.selection.getParagraphs(true);
            this.modelHandler.increaseIndent(deepParagraphs);
        }
    },
    [ACTION.DECREASE_INDENT]: class extends ParagraphController {
        execute() {
            let deepParagraphs = this.selection.getParagraphs(true);
            this.modelHandler.decreaseIndent(deepParagraphs);
        }
    },
    [ACTION.SET_LINE_HEIGHT]: class extends ParagraphController {
        execute(percent) {
            const deepParagraphs = this.selection.getParagraphs(true);
            this.modelHandler.setLineHeight(deepParagraphs, percent);
        }
    },
    [ACTION.SET_PARA_STYLE]: class extends ParagraphController {
        execute(value) {
            let deepParagraphs = this.selection.getParagraphs(true);
            deepParagraphs.forEach(paragraph => paragraph.setParaStyle(value));
        }
    },
    [ACTION.SET_LETTER_SPACING]: class extends ParagraphController {
        execute(value) {
            let deepParagraphs = this.selection.getParagraphs(true);
            deepParagraphs.forEach(paragraph => paragraph.setLetterSpacing(value));
        }
    },

    [ACTION.SET_BULLET_LISTS]: class extends ParagraphController {
        preExecute() {
            //DO NOTHING
        }
        execute(levelText) {
            const deepParagraphs = this.selection.getParagraphs(true);
            this.renderTargetParagraphs = this.modelHandler.setLists(deepParagraphs, LISTS_FORMAT.BULLET, levelText);
        }
    },

    [ACTION.SET_NUMBERED_LISTS]: class extends ParagraphController {
        preExecute() {
            //DO NOTHING
        }
        execute(format) {
            const deepParagraphs = this.selection.getParagraphs(true);
            this.renderTargetParagraphs = this.modelHandler.setLists(deepParagraphs, format, null);
        }
    },

    [ACTION.SET_MULTI_LISTS]:  class extends ParagraphController {
        preExecute() {
            //DO NOTHING
        }
        execute(style) {
            const deepParagraphs = this.selection.getParagraphs(true);
            this.renderTargetParagraphs = this.modelHandler.setMultiLists(deepParagraphs, style);
        }
    },

    [ACTION.TOGGLE_LISTS]: class extends ParagraphController {
        preExecute() {
            //DO NOTHING
        }
        execute(type) {
            const deepParagraphs = this.selection.getParagraphs(true);
            const startPosition = this.selection.getStartPosition();
            const startParagraph = this.positionManager.getElement(startPosition);
            this.renderTargetParagraphs = this.modelHandler.toggleLists(deepParagraphs, startParagraph, type);
        }
    },

    [ACTION.SET_LISTS_START_VALUE]: class extends ParagraphController {
        preExecute() {
            this.renderTargetParagraphs = [];
        }
        execute(value) {
            const startPosition = this.selection.getStartPosition();
            const paragraph = this.positionManager.getElement(startPosition);

            if (paragraph.isLists()) {
                const id = paragraph.getListsId();
                const level = paragraph.getListsLevel();
                const format = this.listsManager.getListsFormat(id, level);

                if (format !== LISTS_FORMAT.BULLET) {
                    const paragraphs = this.selection.getParagraphs(true).filter(para => {
                        return para.getListsId() === id;
                    });

                    this.renderTargetParagraphs = this.modelHandler.setListsStartValue(paragraphs, value);
                }
            }
        }
    },

    [ACTION.INSERT_HORIZONTAL_LINE]: class extends ParagraphController {
        preExecute() {
            super.preExecute();
            if (!this.selection.isCollapsed()) {
                this.modelHandler.deleteContent(this.selection);
            }

            let startPosition = this.selection.getStartPosition();
            if (startPosition.offset !== 0) {
                this.renderTargetParagraphs.push(this.modelHandler.enter(this.selection));
            }
        }
        execute() {
            this.renderTargetParagraphs.push(this.modelHandler.insertHorizontalLine(this.selection));
        }
    }
};

export default paragraphController;



