import $ from 'jquery';
import util from '../util/util';

/**
 * 편집 모델의 하위 모델인 TableCell을 렌더링하는 Class입니다.<br/>
 * ParagraphRenderer에서 TableCellRenderer를 호출합니다.
 */
class TableCellRenderer {
    constructor(renderer) {
        this.parent = renderer;
    }

    /**
     * 상위 에디터 객체를 반환합니다.
     * @returns {Object} - 에디터 객체
     */
    getEditor() {
        return this.parent.editor;
    }

    /**
     * 표의 셀을 렌더링하기 위해 TableCell Element를 인자로 받아 HTML을 반환합니다.
     * @param {TableCell} - TableCell Element
     * @returns {String} HTML 형태의 문자열
     */
    getHTML(tableCell) {
        if (util.isNil(tableCell) || tableCell.isMerged()) {
            return '';
        }

        let bodyRenderer = this.parent.getBodyRenderer();
        let renderUtil = this.getEditor().getRenderUtil();
        let $elTableCell = $(`<td>${bodyRenderer.getHTML(tableCell)}</td>`);

        //border
        let borderCssText = this.getBorderCssText(tableCell);
        if (borderCssText) {
            $elTableCell.css('cssText', borderCssText);
        }

        //background
        let fill = util.get(tableCell.getProp(), ['fill']);
        let colorObj = renderUtil.getColorObject(fill);
        let colorValue = renderUtil.getColorValue(colorObj);
        let backgroundColor = util.getColorCodeFromDecimal(colorValue);

        if (backgroundColor) {
            $elTableCell.css('background-color', backgroundColor);
        }

        //vertical alignment
        let vertAlign = tableCell.getVertAlign();
        $elTableCell.addClass(vertAlign);

        //width
        let width = tableCell.getWidth();
        if (width > 0) {
            $elTableCell.attr('width', `${width}px`);
        }

        //merge
        let rowSpan = tableCell.getRowSpan();
        let colSpan = tableCell.getColSpan();
        if (rowSpan > 1) {
            $elTableCell.attr('rowspan', rowSpan);
        }

        if (colSpan > 1) {
            $elTableCell.attr('colspan', colSpan);
        }

        return $elTableCell.prop('outerHTML');
    }

    /**
     * 셀 테두리의 렌더링을 위한 css text를 반환합니다.
     * @private
     * @param {TableCell} - TableCell Element
     * @returns {String} - css text
     */
    getBorderCssText(tableCell) {
        let borderCssText = '';
        let rowIndex = tableCell.getRowIndex();
        let colIndex = tableCell.getColIndex();
        let borders = this.mergeBorders(tableCell);
        let renderUtil = this.getEditor().getRenderUtil();

        for (let direction in borders) {
            if (direction === 'right' || direction === 'bottom') {
                borderCssText += renderUtil.getBorderCssText(direction, borders[direction]);
            }

            if (direction === 'top' && rowIndex === 0) {
                borderCssText += renderUtil.getBorderCssText(direction, borders[direction]);
            }

            if (direction === 'left' && colIndex === 0) {
                borderCssText += renderUtil.getBorderCssText(direction, borders[direction]);
            }
        }

        return borderCssText;
    }

    /**
     * 셀 테두리를 렌더링하기 위해 셀의 테두리와 표의 테두리를 병합하여 반환합니다.
     * @param {TableCell} - TableCell Element
     * @returns {Object} - TableCell의 Border 속성
     */
    mergeBorders(tableCell) {
        let rowIndex = tableCell.getRowIndex();
        let colIndex = tableCell.getColIndex();
        let borders = tableCell.getBorders();
        let table = tableCell.getTable();
        let tableBorders = table.getBorders();
        
        if (!tableBorders) {
            let documentStyleManager = this.getEditor().getDocumentStyleManager();
            let tableStyleId = util.get(table.getProp(), ['table_style_id']);
            let tableStyle = documentStyleManager.getTableStyle(tableStyleId);
            tableBorders = util.get(tableStyle, ['table_prop', 'borders']);
        }

        let tableBorderTop = util.get(tableBorders, ['top']);
        let tableBorderBottom = util.get(tableBorders, ['bottom']);
        let tableBorderLeft = util.get(tableBorders, ['left']);
        let tableBorderRight = util.get(tableBorders, ['right']);
        let tableBorderInsideH = util.get(tableBorders, ['insideH']);
        let tableBorderInsideV = util.get(tableBorders, ['insideV']);

        if (tableBorderInsideH) {
            borders['left'] = borders['left'] || tableBorderInsideH;
            borders['right'] = borders['right'] || tableBorderInsideH;
        }

        if (tableBorderInsideV) {
            borders['top'] = borders['top'] || tableBorderInsideV;
            borders['bottom'] = borders['bottom'] || tableBorderInsideV;
        }

        if (rowIndex === 0 && tableBorderTop && this.isHiddenBorder(borders['top'])) {
            borders['top'] = tableBorderTop;
        }

        if (rowIndex === table.getCountOfRow() - 1 && this.isHiddenBorder(borders['bottom'])) {
            borders['bottom'] = tableBorderBottom;
        }

        if (colIndex === 0 && tableBorderLeft && this.isHiddenBorder(borders['left'])) {
            borders['left'] = tableBorderLeft;
        }

        if (colIndex === table.getCountOfColumn() - 1 && tableBorderRight && this.isHiddenBorder(borders['right'])) {
            borders['right'] = tableBorderRight;
        }

        return borders;
    }

    isHiddenBorder(border) {
        return !util.get(border, ['style']) || util.get(border, ['style']) === 'none';
    }
}

export default TableCellRenderer;


