import TextRun from './TextRun';

class Tab extends TextRun {
    /**
     * TextRun tab을 생성하여 반환합니다.
     * @returns {TextRun} textRun Tab
     */
    static createTab() {
        return new Tab({tab: 1});
    }

    /**
     * Tab length를 반환합니다.
     * @returns {Number}
     */
    length() {
        return 1;
    }

    /**
     * 빈 TextRun을 생성하여 index에 맞게 Tab 앞 뒤로 삽입하고 TextRun을 반환합니다.
     * @param {Number} index 
     * @return {TextRun} run
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }
}

export default Tab;


