import $ from 'jquery';
import ACTION from '../consts/actionMap';
import consts from '../consts/consts';

const UI_NAME = consts.UI_NAME;
/**
 * Synap Editor에서 발생하는 Window Event를 관리하는 Class입니다.
 */
class WindowEventManager {
    constructor(editorId, ime, controller) {
        this.editorId = editorId;
        this.$window = $(window);
        this.$toolbar = $(`#${this.editorId} .se-toolbar`);
        this.ime = ime;
        this.controller = controller;
        this.popups = $(`#${this.editorId} .se-popup`);
        this.btnGroup = $(`#${this.editorId} .btn-group`);

        this.init();
    }

    /**
     * 윈도우 이벤트를 초기화 합니다.
     */
    init() {
        this.$toolbar.on('keydown', this.onKeyDown.bind(this));
        this.$window.on('click', this.onClick.bind(this));
        this.$window.on('resize', this.onResize.bind(this));

        this.$toolbar.find('.se-dropdown-menu').on('openDropdown', this.onOpenDropdown.bind(this));
    }

    /**
     * 드롭다운 메뉴가 열릴 때의 이벤트를 처리합니다.
     * @param e
     * @param dropdownName
     */
    onOpenDropdown(e, dropdownName) {
        switch (dropdownName) {
            case UI_NAME.NUMBERED_LISTS_TEXT_VERSION:
            case UI_NAME.NUMBERED_LISTS:
                this.controller.execute(ACTION.SET_START_VALUE_BUTTON_STATUS, dropdownName);
                break;
        }
    }

    /**
     * 클릭 이벤트를 처리합니다.
     * @param {Event} e
     */
    onClick(e) {
        const $target = $(e.target);

        if (!oArea.isEditor($target)) { // 에디터 영역 밖
            this.controller.execute(ACTION.HIDE_ALL_POPUP);
            this.controller.execute(ACTION.CLEAR_SELECTION);

        } else if (oArea.isToolbar($target)) { // 툴바 영역
            let $targetBtn;

            if ($target.hasClass('se-menu-btn-wrapper')) {
                $targetBtn = $target;
            } else if ($target.parents('.se-menu-btn-wrapper').length > 0) {
                $targetBtn = $target.parents('.se-menu-btn-wrapper');
            } else if ($target.hasClass('se-dropdown-btn-wrapper')) {
                $targetBtn = $target;
            } else if ($target.parents('.se-dropdown-btn-wrapper').length > 0) {
                $targetBtn = $target.parents('.se-dropdown-btn-wrapper');
            }

            this.controller.execute(ACTION.HIDE_ALL_POPUP, $targetBtn);
        } else if ($target.parents('.se-table-cell-prop').length > 0 || $target.hasClass('se-table-cell-prop')) { // cellProp 버튼을 클릭
            let $elTableCellBallon = $(`#${this.editorId} .se-popup-container div[name="${UI_NAME.TABLE_CELL_BALLOON}"]`);
            this.controller.execute(ACTION.HIDE_ALL_POPUP, $($elTableCellBallon));
        } else if (oArea.isMain($target) && !oArea.isContents($target)) { //컨텐츠 영역을 제외한 메인 영역
            if (!$('.se-picture-selection').hasClass('show')) {
                this.controller.execute(ACTION.HIDE_ALL_POPUP);
            }
        }
    }

    /**
     * 윈도우의 리사이즈 이벤트를 처리합니다.
     * @param {Event} e
     */
    onResize(e) {
        this.controller.execute(ACTION.HIDE_ALL_POPUP);
    }

    /**
     * Keyborad keyDown Event를 처리합니다.
     * popup, btnGroup이 모두 닫혀있다면 ime에 Focus를 줍니다.
     * @param {KeyboradEvent} e
     */
    onKeyDown(e) {
        let popups = this.popups;
        let btnGroup = this.btnGroup;
        let isImeFocus = true;

        for (let i = 0; i < popups.length; i++) {
            if ($(popups[i]).hasClass('show')) {
                isImeFocus = false;
                break;
            }
        }
        for (let i = 0; i < btnGroup.length; i++) {
            if ($(btnGroup[i]).hasClass('open')) {
                isImeFocus = false;
                break;
            }
        }

        if (isImeFocus) {
            this.ime.focus();
        }
    }
}

const oArea = {
    isEditor: function ($el) {
        return $el.hasClass('.se') || $el.parents('.se').length > 0;
    },

    isToolbar: function ($el) {
        return $el.hasClass('se-toolbar') || $el.parents('.se-toolbar').length > 0;
    },

    isMain: function ($el) {
        return $el.hasClass('se-main') || $el.parents('.se-main').length > 0;
    },

    isContents: function ($el) {
        return $el.hasClass('se-contents') || $el.parents('.se-contents').length > 0;
    },

    isPictureSelection: function $($el) {
        return $el.hasClass('se-picture-selection') || $el.parents('.se-picture-selection').length > 0;
    }
};

export default WindowEventManager;


