import Dialog from "./Dialog";
import $ from "jquery";
import consts from '../../consts/consts';
import ACTION from '../../consts/actionMap';

import util from '../../util/util';

const LISTS_FORMAT = consts.LISTS_FORMAT;
const UI_NAME = consts.UI_NAME;

/**
 * 글머리 번호의 시작번호를 지정하기 위한 StartValueDialog 클래스입니다.
 * Dialog를 상속 받습니다.
 * @extends {Dialog}
 */
class StartValueDialog extends Dialog {
    /**
     * StartValueDialog 생성자입니다.
     * @param {String} editorId - 에디터를 식별할 수 있는 아이디
     * @param {Controller} controller - 액션 편집을 위한 컨트롤러 인스턴스
     */
    constructor(editorId, controller) {
        super(editorId);

        this.editorId = editorId;
        this.controller = controller;

        this.$element = this.__createDom__();
        this.$input = this.$element.find('input');
        this.$preview = this.$element.find('.se-preview');
        this.__attachEvent__();
    }

    /**
     * Dialog Container 안에 StartValueDialog Dom을 생성하여 추가합니다.
     * @private
     * @returns {jQuery}
     */
    __createDom__() {
        const $element = $(`
            <div class='se-dialog' name="${UI_NAME.START_VALUE_DIALOG}">
                <div class='se-dialog-body'>
                    <div class='se-row'>
                        <span>시작번호</span>
                        <input type="number" value="1" min="0" max="100" step="1"> 
                    </div>
                    <div class='se-row'>
                        <span>미리보기</span>
                        <div class='se-preview'></div>
                    </div>
                </div>
                <div class='se-dialog-footer'>
                    <div class='se-cancel se-btn'><span>취소</span></div>
                    <div class='se-confirm se-btn'><span>확인</span></div>
                </div>
            </div>
        `);

        const $dialogContainer = $('#' + this.editorId).find('.se-dialog-container');
        $dialogContainer.append($element);

        return $element;
    }

    /**
     * 이벤트 등록을 합니다.
     * @private
     */
    __attachEvent__() {
        const $confirm = this.$element.find('.se-confirm');
        const $cancel = this.$element.find('.se-cancel');

        $confirm.on('click', this.confirm.bind(this));
        $cancel.on('click', this.cancel.bind(this));

        this.$input.on('change keyup mouseup', this.__onChange__.bind(this));
        this.$input.on('keypress', function (event) {
            if (event.which && (event.which <= 47 || event.which >= 58) && event.which !== 8) {
                event.preventDefault();
            }
        });
    }

    /**
     * input의 값이 변경되었을 때 호출됩니다.
     * @private
     */
    __onChange__() {
        const value = parseInt(this.$input.val()) || 0;
        this.__updatePreview__(this.format, value);
    }

    /**
     * 확인 버튼을 눌렀을 때 동작합니다.
     */
    confirm() {
        const value = parseInt(this.$input.val()) || 0;

        if (typeof value === 'number') {
            this.controller.execute(ACTION.SET_LISTS_START_VALUE, value);
        }

        super.confirm();
    }

    /**
     * input의 값을 초기화합니다.
     * @param {Number} [startValue=1]
     * @private
     */
    __initStartValue__(startValue = 1) {
        this.$input.val(startValue);
    }

    /**
     * 미리보기를 업데이트합니다.
     * @param {String} [format=LISTS_FORMAT.DECIMAL]
     * @param {Number} [startValue=1]
     * @private
     */
    __updatePreview__(format = LISTS_FORMAT.DECIMAL, startValue = 1) {
        if (format) {
            this.format = format;
        }
        let value;
        switch (format) {
            case LISTS_FORMAT.DECIMAL:
            case LISTS_FORMAT.DECIMAL_FULL_WIDTH:
                value = startValue + '.';
                break;
            case LISTS_FORMAT.UPPER_LETTER:
                value = util.decimalToLetter(startValue, true);
                break;
            case LISTS_FORMAT.LOWER_LETTER:
                value = util.decimalToLetter(startValue, false);
                break;
            case LISTS_FORMAT.UPPER_ROMAN:
                value = util.decimalToRoman(startValue, true);
                break;
            case LISTS_FORMAT.LOWER_ROMAN:
                value = util.decimalToRoman(startValue, false);
                break;
            case LISTS_FORMAT.DECIMAL_ENCLOSED_CIRCLE:
                value = util.decimalToCircleDecimal(startValue);
                break;
            case LISTS_FORMAT.GANADA:
                value = util.decimalToGanada(startValue);
                break;
            case LISTS_FORMAT.CHOSUNG:
                value = util.decimalToChosung(startValue);
                break;
            default: //LISTS_FORMAT.NONE
                break;
        }

        this.$preview.text(value);
    }

    /**
     * StartValueDialog Element를 보여줍니다.
     * @param {String} [format=LISTS_FORMAT.DECIMAL]
     * @param {Number} [startValue=1]
     */
    show(format = LISTS_FORMAT.DECIMAL, startValue = 1) {
        this.__initStartValue__(startValue);
        this.__updatePreview__(format, startValue);
        super.show();
    }
}

export default StartValueDialog;


