import Element from './Element';
import util from '../util/util';
/**
 * TableRow
 * @extends Element
 */
class TableRow extends Element {
    __getChildrenProperty__() {
        return 'table_cell';
    }
    
    /**
     * Element의 Type을 반환합니다.
     * @returns {String} - Element Type
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'TableRow';
    }

    /**
     * TableRow Element의 properties를 반환합니다.
     * @returns {Object} - Table Element Properties
     */
    getProp() {
        return this.prop;
    }

    /**
     * TableRow Element의 height 속성을 반환합니다.
     * @returns {Number} - TableRow Element의 height 속성
     */
    getHeight() {
        let height = -1;
        let sunit = util.get(this.getProp(), ['height', 'value_in_sunit']);
        
        if (sunit) {
            height = util.sunit2px(sunit);
        }

        return height;
    }

    /**
     * TableRow Element에 속한 Paragraph들을 배열에 담아 반환합니다.
     * @returns {Array} - paragraph 배열
     */
    getParagraphs() {
        return this.getChildren().reduce((paragraphs, tableCell) => {
            paragraphs.push(...tableCell.getParagraphs());
            return paragraphs;
        }, []);
    }
}

export default TableRow;


