import util from '../util/util';

/**
 * 하이퍼링크를 렌더링 하는 Class입니다.
 */
class HyperlinkRenderer {
    constructor(renderer) {
        const editor = renderer.editor;
        this.editorId = editor.id;
    }

    /**
     * Hyperlink를 렌더링 하기 위해 hyperlink Element를 받아 HTML을 반환합니다.
     * @param {Object} run hyperlink Element
     * @returns {String} HTML 형태의 문자열
     * @example
     * //return example
     * <a href="https://www.naver.com" style=' '>하이퍼링크</a>
     */
    getHTML(hyperlink) {
        let prop = util.get(hyperlink, ['prop']);
        let href = this.getHref(prop);
        return `<a hyperlink-id=${hyperlink.id} se-data-href='${href}' target='_blank'></a>`;
    }

    /**
     * hyperlink properties를 받아 href를 반환합니다.
     * @param {Object} prop 
     * @returns {String} href
     */
    getHref(prop) {
        if(!prop) {
            return;
        }
        let href = prop.target;
        return href;
    }
}

export default HyperlinkRenderer;


