import specialCharacter from '../data/specialCharacter';
import emoticon from '../data/emoticon';
import emoji from '../data/emoji';

/**
 * characterPicker에 필요한 데이터를 다루는 핸들러 객체입니다.
 */
const cpDataHandler = {
    data: {
        specialCharacter: specialCharacter,
        emoticon: emoticon,
        emoji: emoji
    },

    /**
     * type에 해당하는 데이터를 반환합니다.
     * @param {String} type - 'specialCharacter', 'emoticon', 'emoji'
     * @return {Array}
     */
    getItems: function (type) {
        return this.data[type];
    },

    /**
     * type과 category에 해당하는 값을 반환합니다.
     * CharacterPicker에서 테이블을 그릴 때 사용됩니다.
     * @param {String} type - 'specialCharacter', 'emoticon', 'emoji'
     * @param {String} category - 각 type에 해당하는 카테고리
     * @return {Array}
     */
    getCategoryValues: function (type, category) {
        let values = [];

        this.data[type].forEach(item => {
            if (item.category === category) {
                values = item.values;
            }
        });

        return values;
    }
};

export default cpDataHandler;


