import TextRun from './TextRun';

/**
 * BookmarkEnd
 * @extends TextRun
 */
class BookmarkEnd extends TextRun {
    /**
     * Element의 Type을 반환합니다.
     * @returns {String} BookmarkEnd
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'BookmarkEnd';
    }

    /**
     * BookmarkEnd의 length를 반환합니다.
     * @returns {Number} length
     */
    length() {
        return 0;
    }

    /**
     * BookmarkStart split시 빈텍스트런을 생성하여 뒷쪽에 삽입합니다.
     */
    split() {
        const run = TextRun.createTextRun();
        this.after(run);
        return run;
    }
}

export default BookmarkEnd;


