import util from '../util/util';

/**
 * 문서 스타일을 관리하는 역할을 하는 Class 입니다.
 */
class DocumentStyleManager {
    constructor() {
        this.list = null;
    }

    setDocumentStyles(documentStyles) {
        this.documentStyles = documentStyles;
    }

    /**
     * 미리 정의된 문서의 기본 스타일 정보를 담고 있는 객체를 반환합니다.
     * paragraph, lists, table, run등의 기본 스타일을 가지고 있습니다.
     * @returns document style object
     */
    getDocumentStyles() {
        return this.documentStyles;
    }

    /**
     * 미리 정의된 Table Style을 반환합니다.
     * @param {Number} styleId Style ID
     * @returns 미리 정의된 Table Style
     */
    getTableStyle(styleId) {
        let documentStyles = this.getDocumentStyles();
        let tableStyles = util.get(documentStyles, ['table_style'], []);
        
        return tableStyles.find(style => {
            return style.style_id === styleId;
        });
    }
}

export default DocumentStyleManager;


