import $ from 'jquery';

/**
 * 편집모델의 하위 모델인 Body를 렌더링하는 Class입니다.<br/>
 * BodyRenderer에서 ParagraphRenderer 호출합니다.
 * @example
 * import BodyRenderer from 'path'
 * let BodyRenderer = BodyRenderer.getInstance();
 */
class BodyRenderer {
    constructor(renderer) {
        this.parent = renderer;
    }

    getEditor() {
        return this.parent.editor;
    }

    /**
     * 문서를 렌더링하기 위해 Body Element를 받아 HTML을 반환합니다.
     * @param {Object} body Body Element
     * @returns {String} HTML형태의 문자열
     */
    getHTML(body) {
        const paragraphRenderer = this.parent.getParagraphRenderer();
        const paragraphs = body.getChildren();
        const $elTemp = $('<div></div>');
        
        const cachedUlElement = {};
        const getCachedUlElement = (listsId) => {
            if(!cachedUlElement[listsId]) {
                const $ul = this.createUl(listsId);
                $elTemp.append($ul);
                cachedUlElement[listsId] = $ul;
            }
            
            return cachedUlElement[listsId];
        };
        
        paragraphs.forEach(paragraph => {
            const isTablePara = paragraph.isTable();
            const isHorizontalLinePara = paragraph.isHorizontalLine();
            const isListsPara = paragraph.isLists();
            let $elParagraph;
            
            if (isListsPara) {
                const listsId = paragraph.getListsId();
                const $ul = getCachedUlElement(listsId);

                $elParagraph = $(paragraphRenderer.getListsHTML(paragraph));
                $ul.append($elParagraph);
                
            } else if(isTablePara) {
                $elParagraph = paragraphRenderer.getTableHTML(paragraph);
                $elTemp.append($elParagraph);
            } else if(isHorizontalLinePara) {
                $elParagraph = paragraphRenderer.getHorizonTalLineHTML(paragraph);
                $elTemp.append($elParagraph);
            } else {
                $elParagraph = paragraphRenderer.getHTML(paragraph);
                $elTemp.append($elParagraph);
            }
        });

        return $elTemp.html();
    }

    /**
     * ul 엘리먼트를 만들어 반환합니다.
     * @param {Number} listsId 
     * @param {String} type
     * @returns {jQueryElement}
     */
    createUl(listsId) {
        let type = this.getEditor().listsManager.getMultiLevelType(listsId);
        return $(`<ul class="se-lists" lists-id="${listsId}" type="${type}"></ul>`);
    }
}

export default BodyRenderer;


