import $ from 'jquery';
import CellIndex from '../model/CellIndex';

const CELL_BAR_THICKNESS = 15;
const SMALL_CIRCLE_BTN_DIAMETER = 7;
const DEFAULT_MARGIN = 4;
const CELL_PROP_BUTTON_WIDTH = 18;
const INSERT_BTN_MARGIN = CELL_BAR_THICKNESS + DEFAULT_MARGIN + SMALL_CIRCLE_BTN_DIAMETER;
const PLUS_SVG_STRING = '<svg viewBox="0 0 20 20"><path d="M11,4V9H16V11H11V16H9V11H4V9H9V4z"></path></svg>';
const INVERTED_TRIANGLE_SVG_STRING = '<svg viewBox="0 0 16 12"><path d="M8,9 L4 4 L12 4Z"></path></svg>';

/**
 * 표 핸들러를 렌더링하는 Class입니다.<br/>
 * 표 핸들러를 show/hide하고, 버튼 클릭시 발생하는 이벤트로 액션을 호출해 줍니다.
 */
class TableHandler {
    constructor(editorId, SEModelManager) {
        this.editorId = editorId;
        this.SEModelManager = SEModelManager;
        this.$elMain = $(`#${this.editorId} .se-main`);
        this.$elTableHandler = $(`#${this.editorId} .se-table-handler`);

        if (!this.$elTableHandler.length) {
            this.$elTableHandler = $('<div class="se-table-handler"></div>');
            this.$elMain.append(this.$elTableHandler);
        }
    }

    /**
     * 표 핸들러를 렌더링합니다.
     * @param {Number} tableId 
     * @param {CellIndex} cellIndex 
     */
    show(tableId, cellIndex) {
        this.$elTableHandler.empty();
        let {sr, er, sc, ec} = cellIndex;
        let elMain = this.$elMain.get(0);
        let elTableBody = elMain.querySelector(`table[table-id='${tableId}'] tbody`);
        let mainRect = elMain.getBoundingClientRect();
        let tableBodyRect = elTableBody.getBoundingClientRect();
        let table = this.SEModelManager.getTable(tableId);
        let cells = table.getAllCells();
        let rightTopCell = table.getCell(new CellIndex(sr, ec));
        let rightTopRect = null;
        let rowRects = []; 
        let colRects = [];

        for (let cell of cells) {
            let rowIndex = cell.getRowIndex();
            let colIndex = cell.getColIndex();
            let elTr = elTableBody.children[rowIndex];
            let elTd = elTr.children[cell.getColIndex(true)];

            if (cell.getRowSpan() === 1 && !rowRects[rowIndex]) {
                let {top, height} = elTd.getBoundingClientRect();
                rowRects[rowIndex] = {
                    top : top,
                    height : height
                };
            }
            if (cell.getColSpan() === 1 && !colRects[colIndex]) {
                let {left, width} = elTd.getBoundingClientRect();
                colRects[colIndex] = {
                    left : left,
                    width : width
                };
            }
            if (cell === rightTopCell) {
                rightTopRect = elTd.getBoundingClientRect();
            }
        }

        let lastRowRect = { top : tableBodyRect.bottom };
        let lastColRect = { left : tableBodyRect.right };
        let $rowInsertBtns = rowRects.concat([lastRowRect]).map((rowRect, index) => {
            const isActive = sr <= index && index <= er;
            const className = 'se-table-row-bar' + (isActive ? ' active' : '');
            return $(`<div class='${className}'></div>`).css({
                top : rowRect.top - mainRect.top + elMain.scrollTop,
                left : tableBodyRect.left - CELL_BAR_THICKNESS - mainRect.left,
                height : rowRect.height || 0,
                width : !!rowRect.height ? CELL_BAR_THICKNESS : 0
            }).append($(`<div class='se-table-row-insert'>${PLUS_SVG_STRING}</div>`));
        });

        let $colInsertBtns = colRects.concat([lastColRect]).map((colRect, index) => {
            const isActive = sc <= index && index <= ec;
            const className = 'se-table-col-bar' + (isActive ? ' active' : '');
            return $(`<div class='${className}'></div>`).css({
                top : tableBodyRect.top - CELL_BAR_THICKNESS - mainRect.top + elMain.scrollTop,
                left : colRect.left - mainRect.left,
                height : !!colRect.width ? CELL_BAR_THICKNESS : 0,
                width : colRect.width || 0
            }).append($(`<div class='se-table-col-insert'>${PLUS_SVG_STRING}</div>`));
        });

        let $allSelectBtn = $(`<div class='se-table-all-select'></div>`).css({
            top : tableBodyRect.top - INSERT_BTN_MARGIN - mainRect.top + elMain.scrollTop,
            left : tableBodyRect.left - INSERT_BTN_MARGIN - mainRect.left
        });

        let $cellPropBtn = $(`<div class='se-table-cell-prop'>${INVERTED_TRIANGLE_SVG_STRING}</div>`).css({
            top : rightTopRect.top + DEFAULT_MARGIN - mainRect.top + elMain.scrollTop,
            left : rightTopRect.right - CELL_PROP_BUTTON_WIDTH - DEFAULT_MARGIN - mainRect.left
        });

        this.$elTableHandler.append($rowInsertBtns);
        this.$elTableHandler.append($colInsertBtns);
        this.$elTableHandler.append($allSelectBtn);
        this.$elTableHandler.append($cellPropBtn);
    }

    /**
     * 표 핸들러를 비워줍니다.
     */
    hide() {
        this.$elTableHandler.empty();
    }
}

export default TableHandler;


