import util from '../util/util';
import consts from '../consts/consts';
import ACTION from '../consts/actionMap';
import Paragraph from './Paragraph';
import TextRun from './TextRun';
import Break from './Break';
import Tab from './Tab';
import HyperlinkStart from './HyperlinkStart';
import HyperlinkEnd from './HyperlinkEnd';
import HorizontalLine from './HorizontalLine';
import Position from './selection/Position';
import Selection from './selection/Selection';
import Picture from './Picture';
import $ from 'jquery';

const LISTS_TYPE = consts.LISTS_TYPE;
const LISTS_LEVEL_TEXT = consts.LISTS_LEVEL_TEXT;
const LISTS_LEVEL = consts.LISTS_LEVEL;
const MULTI_LISTS_STYLE = consts.MULTI_LISTS_STYLE;
const LISTS_FORMAT = consts.LISTS_FORMAT;
const INDENT_STEP = consts.INDENT_STEP;
const MIN_PICTURE_SIZE = consts.MIN_PICTURE_SIZE;

/**
 * SEModel을 편집하는 역할을 합니다.
 */
class ModelHandler {
    constructor(editor) {
        this.editorId = editor.id;
        this.listsManager = editor.getListsManager();
        this.SEModelManager = editor.getSEModelManager();
        this.positionManager = editor.getPositionManager();
        this.relationshipListManager = editor.getRelationshipListManager();
        this.selectionManager = editor.getSelectionManager();
        this.selection = this.selectionManager.selection;
        this.imageUploadAPI = editor.getConfiguration().imageUploadAPI;
        this.endRunProp = null;
    }

    /**
     * 텍스트 입력 Event가 실행되고 Run SEModel의 text를 변경합니다.<br>
     * ime에서 입력된 Text에서 이전 Text를 비교하여 Text를 붙여나갑니다. (Composition 유지) <br>
     * 입력된 Text 길이만큼 Position 정보 update하며 Caret을 이동합니다.
     * @param {String} text 입력받은 text
     * @param {Number} updateTextLength 입력받은 text 길이
     */
    insertText(text, updateTextLength) {
        let startPosition = this.selection.getStartPosition();
        let run;

        if (!this.endRunProp) {
            run = this.positionManager.getRun(startPosition);
        } else {
            run = this.endRunProp;
            const prevRun = this.positionManager.getRun(startPosition);

            if (prevRun.getRunEndOffset() === startPosition.offset) {
                prevRun.after(run);
            } else if(prevRun.getRunStartOffset() === startPosition.offset) {
                prevRun.before(run);
            } else {
                prevRun.split(startPosition.offset - prevRun.getRunStartOffset()).before(run);    
            }
            
            this.endRunProp = null;
        }

        if (!run) {
            const para = this.SEModelManager.getParagraph(startPosition.id);
            if (para && para.getChildren().length === 0) {
                run = TextRun.createTextRun();
                para.append(run);
            } else {
                return;
            }
        }

        if (util.isPicture(run)) {
            if (!run.nextSibling()) {
                run.after(TextRun.createTextRun());
            }
            run = run.nextSibling();
        }

        if (util.isTab(run) || util.isLineBreak(run) || util.isBookmark(run)) {
            let textRun = this.positionManager.getSameOffsetRuns(startPosition).find(run => {
                return util.has(run, ['text']);
            });

            run = textRun || run.split(startPosition.offset - run.getRunStartOffset());
        }

        let runStartOffset = run.getRunStartOffset();
        let runOffset = startPosition.offset - runStartOffset;
        run.text = run.text.substring(0, runOffset - updateTextLength) + text + run.text.substring(runOffset);

        startPosition.offset += text.length - updateTextLength;
        this.selection.setCaret(startPosition);
    }

     /**
      * Selection에 포함된 SEModel의 시작과 끝 런을 쪼개어 줍니다. <br/>
      * 먼저 endRun을 쪼갠 후에 startRun을 쪼갭니다. <br/>
      * (뒤부터 자를 경우 Selection된 런이 1개일 경우에도 index에 영향을 받지 않음.)
      */
    splitRunsBySelection() {
        if(this.selection.isCollapsed()) {
            return;
        }
        let endPosition = this.selection.getEndPosition();
        let startPosition = this.selection.getStartPosition();
        let endRun = this.positionManager.getRun(endPosition);
        let startRun = this.positionManager.getRun(startPosition);

        util.splitRun(endRun, endPosition.offset - endRun.getRunStartOffset());
        util.splitRun(startRun, startPosition.offset - startRun.getRunStartOffset());
    }

    /**
     * paragraphs를 인자로 받아서 모델에 붙여줍니다. <br/>
     * 연속으로 붙여넣을경우를 대비하여 붙여넣기할 대상은 clone된 모델을 사용합니다. <br/>
     * 붙여넣기할 대상의 element_id는 새로 부여합니다.
     * @param {Array} paragraphs 붙여넣기 대상
     */
    pasteModel(paragraphs) {
        //붙여 넣어질 paragraphs는 무조건 clone해서 붙여야함! 주의
        const startPostion = this.selection.getStartPosition();
        const startRun = this.positionManager.getRun(startPostion);
        const runOffset = startPostion.offset - startRun.getRunStartOffset();
        const [targetRun, splitedTargetRun] = util.splitRun(startRun, runOffset); // 무조건 포지션 앞의 런이 나오고 쪼개질 경우에만 splitedRun이 존재.
        const tmpRunIndex = targetRun.index() + 1;
        let targetPara = targetRun.parent();
        let index = (startPostion.offset === 0) ? 0 : tmpRunIndex;
        let tmpRuns = targetPara.getChildren().slice(index).map(run => run.clone());
        targetPara.removeChild(index, targetPara.getChildCount());
        paragraphs[0].getChildren().forEach(run => {
            if(run.getType() === 'Picture') {
                run.element_id = this.SEModelManager.getNewElementId();
            }
            targetPara.append(run.clone());
        });
        if (paragraphs.length > 1) {
            for (let i=1; i < paragraphs.length; i++) {
                let tempPara = paragraphs[i].clone(true);
                tempPara.element_id = this.SEModelManager.getNewElementId();
                targetPara.after(tempPara);
                targetPara = tempPara;
            }
        }
        this.selection.setCaret(new Position(targetPara.getId(), targetPara.length()));
        tmpRuns.forEach(run => {
            targetPara.append(run.clone());
        });
    }

    /**
     * Selection에 포함된 element SEModel들을 제거하고 시작 Run과 끝 Run을 재배치 합니다.
     * 1. Selection포함된 Paragraph 제거합니다.
     * 2. Selection에 걸린 시작 Run과 끝 Run Split 합니다.
     * 3. Selection에 포함된 Run 제거합니다.
     * 4. Text 없는 Run Model 제거합니다.
     * 5. Paragraph Merge 합니다.
     * @param {Selection} selection
     */
    deleteContent(selection) {
        let startPosition = selection.getStartPosition();
        let endPosition = selection.getEndPosition();
        if (startPosition.isEqual(endPosition)) {
            return;
        }

        let startParagraph = this.positionManager.getElement(startPosition);
        let endParagraph = this.positionManager.getElement(endPosition);
        let nextParagraph = startParagraph.nextSibling();
        // selection에 포함된 paragraph 제거
        while (startParagraph !== endParagraph && nextParagraph !== endParagraph) {
            let removeParagraph = nextParagraph;
            nextParagraph = nextParagraph.nextSibling();
            removeParagraph.remove();
        }

        // selection에 맞게 run split
        let startRun = this.positionManager.getRun(startPosition);
        startRun = startRun.split(startPosition.offset - startRun.getRunStartOffset());
        let endRun = this.positionManager.getRun(endPosition);
        endRun = endRun.split(endPosition.offset - endRun.getRunStartOffset());
        let startRunIndex = startRun.index();
        let endRunIndex = endRun.index();
        // selection에 포함된 run 제거
        if (startParagraph === endParagraph) {
            startParagraph.removeChild(startRunIndex, endRunIndex);
        } else {
            startParagraph.removeChild(startRunIndex, startParagraph.getChildCount());
            endParagraph.removeChild(0, endRunIndex);
        }

        // paragraph merged
        // * Table을 포함한 Paragraph은 Table Run Model 하나만을 가진다.
        // ex) Table 포함 Paragraph일 경우 현재 Dom표현과 SEModel표현이 달라 문제 발생
        if (startParagraph !== endParagraph && !util.isTable(startRun) && !util.isTable(endRun)) {
            endParagraph.getChildren().slice(0).forEach(run => {
                startParagraph.append(run);
            });
            endParagraph.remove();
        }

        // paragraph에 남아있는 빈 텍스트런을 정리합니다.
        // * paragraph은 text가 없는 경우에만 하나의 빈 텍스트런을 가집니다.
        const sameOffsetRuns = this.positionManager.getSameOffsetRuns(startPosition);
        this.clearSameOffsetEmptyRun(sameOffsetRuns, startParagraph);

        const run = this.positionManager.getRun(startPosition);
        const {
            hyperlinkMap,
            invalidStartHyperlink,
            invalidEndHyperlink
        } = this.filterHyperlink(startPosition);

        // 하이퍼링크 start end가 같은 offset에 있는 경우, 하이퍼링크 텍스트가 모두 제거되었다고 판단하여 하이퍼링크 정보 제거
        // 하이퍼링크 start end 둘중 하나만 끊어진경우 position 정보 이용하여 하이퍼링크 정보 갱신
        if (invalidStartHyperlink) {
            if (sameOffsetRuns.indexOf(invalidStartHyperlink.end) > -1) {
                invalidStartHyperlink.end.remove();
                delete hyperlinkMap[invalidStartHyperlink.end.id];
            } else {
                run.after(invalidStartHyperlink.start);
            }
        }
        if (invalidEndHyperlink) {
            if (sameOffsetRuns.indexOf(invalidEndHyperlink.start) > -1) {
                invalidEndHyperlink.start.remove();
                delete hyperlinkMap[invalidEndHyperlink.start.id];
            } else {
                run.after(invalidEndHyperlink.end);
            }
        }

        //삭제 후 합쳐줘야 할지에 대한 여부를 판단 후 합쳐줘야한다.
        this.__mergeLists__(startParagraph);

        this.selection.setCaret(startPosition);
    }

    /**
     * 같은 offset에 있는 empty TextRun을 제거합니다.
     * @param {Array} runs
     * @param {Paragraph} paragraph
     */
    clearSameOffsetEmptyRun(runs, paragraph) {
        const emptyRuns = runs.filter(run => util.isTextRun(run) && run.isEmpty());
        const isEmpty = paragraph.isEmpty();

        emptyRuns.forEach((run, index) => {
            const prevRun = run.previousSibling();
            if (!(index === 0 && isEmpty) && (prevRun && util.isTextRun(prevRun) && !util.isLineBreak(prevRun))) {
                run.remove();
            }
        });
    }

    /**
     * Selection 정보에 해당하는 Picture SEModel을 제거합니다.
     * @param {Selection} selection 
     */
    deletePicture(selection) {
        const startPosition = selection.getStartPosition();
        const paragraph = this.SEModelManager.getParagraph(startPosition.id);
        const picture = this.SEModelManager.getPicture(startPosition.pictureId);

        picture.remove();

        const sameOffsetRuns = this.positionManager.getSameOffsetRuns(startPosition);
        this.clearSameOffsetEmptyRun(sameOffsetRuns, paragraph);
        this.selectionManager.setSelectionPosition(new Position(startPosition.id, startPosition.offset));
    }

    /**
     * 현재 Selection에서 Paragraph을 나누어 새로운 Paragraph을 생성합니다.
     * @param {Selection} selection
     * @returns {Paragraph} new paragraph
     */
    enter(selection) {
        let startPosition = selection.getStartPosition();
        let paragraph = this.positionManager.getElement(startPosition);
        let run = this.positionManager.getRun(startPosition);
        run = run.split(startPosition.offset - run.getRunStartOffset());

        // table을 포함하는 Paragraph일 경우에는 앞 혹은 뒤로 빈 텍스트 런을 생성후 index 1에서 split합니다.
        let runIndex = (paragraph.hasTable() || paragraph.hasHorizontalLine()) ? 1 : run.index();
        let newParagraph = paragraph.split(runIndex, this.SEModelManager.getNewElementId());
        this.selection.setCaret(new Position((newParagraph.hasTable() || newParagraph.hasHorizontalLine()) ? paragraph.getId() : newParagraph.getId()));
        return newParagraph;
    }

    /**
     * 현재 Seleciton 위치에 Line Break를 추가합니다.<br>
     * @param {Selection} selection 
     */
    insertLineBreak(selection) {
        let startPosition = selection.getStartPosition();
        let paragraph = this.positionManager.getElement(startPosition);
        let run = this.positionManager.getRun(startPosition);
        let lineBreak = Break.createBreak('line');
        let splitRun = run.split(startPosition.offset - run.getRunStartOffset());

        if (util.isLineBreak(run)) {
            lineBreak.insertAfter(splitRun);
        } else {
            lineBreak.insertBefore(splitRun);
        }

        this.selection.setCaret(new Position(paragraph.getId(), lineBreak.getRunEndOffset()));
    }

    /**
     * 현재 Seleciton 위치에 Tab를 추가합니다.<br>
     * @param {Selection} selection 
     */
    insertTab(selection) {
        let startPosition = selection.getStartPosition();
        let paragraph = this.positionManager.getElement(startPosition);
        let run = this.positionManager.getRun(startPosition);
        let tab = Tab.createTab();
        let splitRun = run.split(startPosition.offset - run.getRunStartOffset());

        if (util.isTab(run)) {
            tab.insertAfter(splitRun);
        } else {
            tab.insertBefore(splitRun);
        }

        this.selection.setCaret(new Position(paragraph.getId(), tab.getRunEndOffset()));
    }

    /**
     * 현재 Selection 기준의 Paragraph 상위에 가로줄을 삽입합니다.
     * @param {Selection} selection
     * @returns {Paragraph} newParagraph
     */
    insertHorizontalLine(selection) {
        let startPosition = selection.getStartPosition();
        let paragraph = this.positionManager.getElement(startPosition);
        let newParagraph = paragraph.split(0, this.SEModelManager.getNewElementId());
        let hrParagraph = newParagraph.previousSibling();
        let horizontalLine = new HorizontalLine();

        hrParagraph.empty();
        hrParagraph.append(horizontalLine);

        this.clearLists([hrParagraph]);

        this.selection.setCaret(new Position(newParagraph.getId(), 0));
        return newParagraph;
    }

    /**
     * 글머리 목록을 설정합니다.
     * @param {Array} paragraphs
     * @param {String} format
     * @param {String} levelText
     * @returns {Array}
     */
    setLists(paragraphs, format, levelText) {
        if (!util.isArray(paragraphs) || util.isNil(format)) {
            return [];
        }
        let renderTargetParagraphs = [];

        paragraphs.forEach(paragraph => {
            let id = paragraph.getListsId();
            let level = paragraph.getListsLevel();

            const newParagraphs = [];

            if (level === -1) {
                level = paragraph.getIndentLevel();
                paragraph.setListsLevel(level);
            }

            if (id === 0) {
                id = this.__calculateListsId__(paragraph, format, level, levelText);
                paragraph.setListsId(id);

                const prevParagraph = paragraph.previousSibling();
                const nextParagraph = paragraph.nextSibling();

                if (prevParagraph && paragraphs.indexOf(prevParagraph) < 0) {
                    if (prevParagraph.isLists() && !this.__isSameLevelItem__(prevParagraph.getListsId(), format, level, levelText)) {
                        newParagraphs.push(this.__appendEmptyParagraph__(prevParagraph));
                    }
                }

                if (nextParagraph && paragraphs.indexOf(nextParagraph) < 0) {
                    if (nextParagraph.isLists() && !this.__isSameLevelItem__(nextParagraph.getListsId(), format, level, levelText)) {
                        newParagraphs.push(this.__appendEmptyParagraph__(paragraph));
                    }
                }
            }

            if (newParagraphs.length > 0) {
                this.clearLists(newParagraphs);
                renderTargetParagraphs = renderTargetParagraphs.concat(newParagraphs);
            }

            this.listsManager.setListsItem(id, level, format, levelText);
            this.__mergeLists__(paragraph);
        });

        return renderTargetParagraphs.concat(paragraphs);
    }

    /**
     * 다단계 글머리를 설정합니다.
     * @param {Array} paragraphs
     * @param {String} style
     * @return {Array}
     */
    setMultiLists(paragraphs, style) {
        if (!util.isArray(paragraphs) || util.isNil(style)) {
            return [];
        }

        if (style === MULTI_LISTS_STYLE.NONE) {
            return this.clearLists(paragraphs);
        }

        let renderTargetParagraphs = [];

        paragraphs.forEach(paragraph => {
            let id = paragraph.getListsId();
            let level = paragraph.getListsLevel();

            const newParagraphs = [];

            if (level === -1) {
                level = paragraph.getIndentLevel();
                paragraph.setListsLevel(level);
            }

            if (id === 0) {
                id = this.listsManager.makeNewId();
                paragraph.setListsId(id);

                const prevParagraph = paragraph.previousSibling();
                const nextParagraph = paragraph.nextSibling();

                if (prevParagraph && paragraphs.indexOf(prevParagraph) < 0 && prevParagraph.isLists()) {
                    newParagraphs.push(this.__appendEmptyParagraph__(prevParagraph));
                }

                if (nextParagraph && paragraphs.indexOf(nextParagraph) < 0 && nextParagraph.isLists()) {
                    newParagraphs.push(this.__appendEmptyParagraph__(paragraph));
                }
            }

            if (newParagraphs.length > 0) {
                this.clearLists(newParagraphs);
                renderTargetParagraphs = renderTargetParagraphs.concat(newParagraphs);
            }

            this.listsManager.setMultiListsItem(id, style);
            this.__mergeLists__(paragraph);

            //다단계 글머리인 경우 셀렉션을 한 문단 외에도 동일한 글머리 아이디를 갖는 모든 문단을 다시 그려줘야 합니다.
            this.SEModelManager.getParagraphsByListsId(id).forEach(paragraph => {
                if (renderTargetParagraphs.indexOf(paragraph) < 0) {
                    renderTargetParagraphs.push(paragraph);
                }
            });
        });

        return renderTargetParagraphs.concat(paragraphs);
    }

    /**
     * 파리미터로 넘겨 받은 paragraph의 다음에 빈 문단을 새로 생성하여 추가합니다.
     * @private
     * @param {Paragraph} paragraph
     */
    __appendEmptyParagraph__(paragraph) {
        let run = paragraph.getLastRun();
        run = run.split(run.length());

        return paragraph.split(run.index(), this.SEModelManager.getNewElementId());
    };

    /**
     * 글머리 목록 설정 시 적용해야 할 아이디 값을 계산합니다.
     * @private
     * @param {Paragraph} paragraph
     * @param {String} format
     * @param {Number} level
     * @param {String} levelText
     * @returns {Number}
     */
    __calculateListsId__(paragraph, format, level, levelText) {
        let listsId;
        const prevParagraph = paragraph.previousSibling();
        const nextParagraph = paragraph.nextSibling();

        const prevListsId = prevParagraph && prevParagraph.getListsId();
        const nextListsId = nextParagraph && nextParagraph.getListsId();

        if (this.__isSameLevelItem__(prevListsId, format, level, levelText)) {
            listsId = prevParagraph.getListsId();
        } else if (this.__isSameLevelItem__(nextListsId, format, level, levelText)) {
            listsId = nextParagraph.getListsId();
        }

        return listsId || this.listsManager.makeNewId();
    }

    /**
     * targetListsId를 가지는 글머리 목록의 레벨 아이템과 동일한 내용인지 확인합니다.
     * 글머리 기호인 경우 levelText로만 비교하고, 글머리 번호인 경우에는 format과 levelText 2가지를 모두 비교합니다.
     * @param targetListsId
     * @param format
     * @param level
     * @param levelText
     * @return {Boolean}
     * @private
     */
    __isSameLevelItem__(targetListsId, format, level, levelText) {
        const targetLevelText = this.listsManager.getLevelText(targetListsId, level);
        const targetFormat = this.listsManager.getListsFormat(targetListsId, level);

        if (format === LISTS_FORMAT.BULLET) {
            return levelText === targetLevelText;
        } else {
            return format === targetFormat && levelText === targetLevelText;
        }
    }

    /**
     * 글머리 목록을 토글합니다.
     * {@link http://wiki.synap.co.kr/pages/viewpage.action?pageId=33522245}
     * @param {Array} paragraphs
     * @param {Paragraph} startParagraph 
     * @param {String} listsType
     * @returns {Array}
     */
    toggleLists(paragraphs, startParagraph, listsType) {
        if (!paragraphs || !startParagraph || !listsType) {
            return [];
        }

        let doSetLists = true;
        let format = listsType === LISTS_TYPE.BULLET ? LISTS_FORMAT.BULLET : LISTS_FORMAT.DECIMAL;
        let levelText = format === LISTS_FORMAT.BULLET ? LISTS_LEVEL_TEXT.DISC : null;

        // 현재 적용하려는 글머리 타입과 이전 문단의 글머리 타입이 동일한 경우, 그대로 상속 받아 사용합니다.
        // 그렇지 않으면 글머리 기호 때는 DISC, 글머리 번호 일 때는 DECIMAL을 기본 값을 가집니다.
        const prevParagraph = startParagraph.previousSibling();

        if (prevParagraph && prevParagraph.isLists()) {
            const prevListsId = prevParagraph.getListsId();
            const prevListsLevel = prevParagraph.getListsLevel();

            if (this.listsManager.getListsType(prevListsId, prevListsLevel) === listsType) {
                format = this.listsManager.getListsFormat(prevListsId, prevListsLevel);
                levelText = this.listsManager.getLevelText(prevListsId, startParagraph.getIndentLevel());
            }
        }

        //셀렉션의 제일 첫 문단이 글머리 목록인 경우
        if (startParagraph && startParagraph.isLists()) {
            const listsId = startParagraph.getListsId();
            const listsLevel = startParagraph.getListsLevel();

            if (this.listsManager.getListsType(listsId, listsLevel) === listsType) {
                doSetLists = false;
            }
        }

        return doSetLists ? this.setLists(paragraphs, format, levelText) : this.clearLists(paragraphs);
    }

    /**
     * 글머리 목록을 해제합니다.
     * @param {Array} paragraphs
     * @returns {Array}
     */
    clearLists(paragraphs) {
        paragraphs.forEach(paragraph => {
            const listsLevel = paragraph.getListsLevel();
            paragraph.setIndent(listsLevel * INDENT_STEP);

            this.__splitLists__(paragraph);
            util.unset(paragraph.getProp(), ['numbering_ref_pr']);
        });

        return paragraphs;
    }

    /**
     * 글머리 목록을 합쳐줍니다.
     * @private
     * @param {Paragraph} paragraph
     */
    __mergeLists__(paragraph) {
        if (!paragraph || !paragraph.isLists()) {
            return;
        }

        let currListsId = paragraph.getListsId();

        //자신과 동일한 글머리는 건너뛰기
        let prevParagraph = paragraph.previousSibling();
        while (prevParagraph && prevParagraph.isLists() && currListsId === prevParagraph.getListsId()) {
            prevParagraph = prevParagraph.previousSibling();
        }

        //윗 문단의 글머리를 합쳐야 하는 조건이라면 합쳐준다.
        if (prevParagraph && prevParagraph.isLists()) {
            const prevListsId = prevParagraph.getListsId();
            this.SEModelManager.getParagraphsByListsId(currListsId).forEach(para => {
                para.setListsId(prevListsId);
            });
        }

        //현재 글머리 아이디를 다시 초기화 한다. 
        currListsId = paragraph.getListsId();

        //자신과 동일한 글머리는 건너뛰기
        let nextParagraph = paragraph.nextSibling();
        while (nextParagraph && nextParagraph.isLists() && currListsId === nextParagraph.getListsId()) {
            nextParagraph = nextParagraph.nextSibling();
        }

        //아래 문단을 돌면서 현재 글머리와 합쳐야 하는 조건이라면 합쳐준다.
        if (nextParagraph && nextParagraph.isLists()) {
            const nextListsId = nextParagraph.getListsId();

            for (let level = LISTS_LEVEL.MIN; level <= LISTS_LEVEL.MAX; level++) {
                const $nextLi = $(`[lists-id=${nextListsId}] .level_${level}`);
                const $currLi = $(`[lists-id=${currListsId}] .level_${level}`);

                if ($currLi.length === 0 && $nextLi.length > 0) {
                    const format = this.listsManager.getListsFormat(nextListsId, level);
                    const levelText = this.listsManager.getLevelText(nextListsId, level);
                    this.listsManager.setListsItem(currListsId, level, format, levelText);
                }
            }

            this.SEModelManager.getParagraphsByListsId(nextListsId).forEach(para => {
                para.setListsId(currListsId);
            });
        }
    }

    /**
     * 글머리 목록을 쪼갭니다.
     * @private
     * @param {Paragraph} paragraph 
     */
    __splitLists__(paragraph) {
        const listsId = paragraph.getListsId();
        const listsPara = this.SEModelManager.getParagraphsByListsId(listsId);
        const idx = listsPara.indexOf(paragraph);

        if (idx > 0 && listsPara.length - 1 > idx) {
            const newListsId = this.listsManager.makeNewId();

            for (let i = idx + 1, n = listsPara.length; i < n; i++) {
                listsPara[i].setListsId(newListsId);
            }

            const numberingItem = this.listsManager.getLists().find(item => {
                return util.get(item, ['numbering_id']) === listsId;
            });

            const copiedItem = util.cloneDeep(numberingItem);
            util.set(copiedItem, ['numbering_id'], newListsId);
            this.listsManager.appendItem(copiedItem);
        }
    }

    /**
     * 글머리 번호의 시작 번호를 설정합니다.
     * @param {Array} paragraphs - 문단
     * @param {Number} value - 시작 번호
     * @returns {Array}
     */
    setListsStartValue(paragraphs, value) {
        paragraphs.forEach(paragraph => {
            this.listsManager.setStartValue(paragraph.getListsId(), paragraph.getListsLevel(), value);
        });

        return paragraphs;
    }

    /**
     * 들여쓰기를 합니다. 
     * @param {Array} paragraphs 
     */
    increaseIndent(paragraphs) {
        paragraphs.forEach(paragraph => {
            this.__increaseIndent__(paragraph);   
        });
    }

    /**
     * 들여쓰기를 합니다. 
     * @param {Array} paragraphIds
     */
    increaseIndentByIds(paragraphIds) {
        paragraphIds.forEach(paragraphId => {
            const paragraph = this.SEModelManager.getParagraph(paragraphId);
            this.__increaseIndent__(paragraph);
        });
    }

    /**
     * paragraph를 들여쓰기 합니다.
     * @private
     * @param {Paragraph} paragraph 
     */
    __increaseIndent__(paragraph) {
        let prop = paragraph.getProp();
        let leftIndent = util.get(prop, ['indent', 'left_in_sunit'], 0);

        if (paragraph.isLists()) { // 글머리번호가 있을경우 처리.
            const level = paragraph.getListsLevel();
            
            if (level < LISTS_LEVEL.MAX) {
                util.set(prop, ['numbering_ref_pr', 'level'], level + 1);
            } 
        } else {
            const value = leftIndent + consts.INDENT_STEP;
            util.set(prop, ['indent', 'left_in_sunit'], value);
        }
    }

    /**
     * 내어쓰기를 합니다.
     * @param {Array} paragraphs 
     */
    decreaseIndent(paragraphs) {
        paragraphs.forEach(paragraph => {
            let prop = paragraph.getProp();
            let leftIndent = util.get(prop, ['indent', 'left_in_sunit'], 0);

            if (paragraph.isLists()) {
                const level = paragraph.getListsLevel();

                if (level > LISTS_LEVEL.MIN) {
                    util.set(prop, ['numbering_ref_pr', 'level'], level - 1);
                } 
            } else {
                const value = Math.max(leftIndent - consts.INDENT_STEP, 0);
                util.set(prop, ['indent', 'left_in_sunit'], value);
            }
        });
    }

    /**
     * Paragraph Id가 Hyperlink Selection에 포함된 Id인지 검사하고, 포함되어있다면 hyperlink Start Run을 반환합니다.
     * @param {Number} paragraphId
     * @returns {HyperlinkStart} hyperlinkStart
     */
    getHyperlinkStart(paragraphId) {
        const hyperlinkMap = this.SEModelManager.getHyperlinkMap();
        let hyperlinkSelection = new Selection(this.positionManager);
        let result = null;

        for (const id in hyperlinkMap) {
            const {
                hyperlinkStart,
                hyperlinkEnd
            } = hyperlinkMap[id];
            const startPosition = this.positionManager.getRunStartPosition(hyperlinkStart);
            const endPosition = this.positionManager.getRunEndPosition(hyperlinkEnd);

            hyperlinkSelection.set(startPosition, endPosition);

            const paragraphs = hyperlinkSelection.getParagraphs(true);
            if (paragraphs.some(hyParagraph => hyParagraph.getId() === paragraphId)) {
                result = hyperlinkStart;
                break;
            }
        }

        return result;
    }

    /**
     * Hyperlink의 root()를 검사하여 지워진 하이퍼링크 정보를 제거, 혹은 끊어진 하이퍼링크 정보를 재조정합니다.
     * Hyperlink Start와 End사이에 Text Run이 없는 경우에도 Hyperlink 정보 제거합니다.
     */
    filterHyperlink() {
        const hyperlinkMap = this.SEModelManager.getHyperlinkMap();
        const body = this.SEModelManager.getBody();
        let invalidStartHyperlink, invalidEndHyperlink = null;

        for (const id in hyperlinkMap) {
            const {
                hyperlinkStart,
                hyperlinkEnd
            } = hyperlinkMap[id];

            // 하이퍼링크 루트가 body가 아닌경우에 끊어졌다고 판단합니다.
            // 하이퍼링크 루트가 둘다 끊어진경우 정보 제거
            if (hyperlinkStart.root() !== body && hyperlinkEnd.root() !== body) {
                delete hyperlinkMap[id];
            } else if (hyperlinkStart.root() !== body) {
                invalidStartHyperlink = {
                    start: hyperlinkStart,
                    end: hyperlinkEnd
                };
            } else if (hyperlinkEnd.root() !== body) {
                invalidEndHyperlink = {
                    start: hyperlinkStart,
                    end: hyperlinkEnd
                };
            }
        }

        return {
            hyperlinkMap: hyperlinkMap,
            invalidStartHyperlink: invalidStartHyperlink,
            invalidEndHyperlink: invalidEndHyperlink
        };
    }

    /**
     * 인자로 받은 id에 해당하는 hyperlink정보와 SEModel hyperlink를 제거합니다.
     * @param {Number} id
     * @return {Array} hyperlink paragraphs
     */
    deleteHyperlink(id) {
        const hyperlink = this.SEModelManager.getHyperlink(id);
        const hyperlinkStartPosition = this.positionManager.getRunStartPosition(hyperlink.hyperlinkStart);
        const hyperlinkEndPosition = this.positionManager.getRunEndPosition(hyperlink.hyperlinkEnd);
        const hyperlinkSelection = new Selection(this.positionManager);
        hyperlinkSelection.set(hyperlinkStartPosition, hyperlinkEndPosition);
        const paragraphs = hyperlinkSelection.getParagraphs();

        hyperlink.hyperlinkStart.remove();
        hyperlink.hyperlinkEnd.remove();
        this.SEModelManager.deleteHyperlink(id);

        return paragraphs;
    }

    /**
     * Selection에 해당하는 위치에 Hyperlink를 삽입합니다.
     * @param {Selection} selection 
     * @param {String} url 
     * @param {String} text 
     */
    insertHyperlink(selection, url, text) {
        let id = this.SEModelManager.getNewElementId();
        let hyperlinkStart = HyperlinkStart.createHyperlinkStart(id, url);
        let hyperlinkEnd = HyperlinkEnd.createHyperlinkEnd(id);
        let startPosition = selection.getStartPosition();
        let endPosition = selection.getEndPosition();
        let hyperlink = selection.getRuns().filter(run => util.isHyperlink(run));
        hyperlink.forEach(hyperlinkRun => {
            let hyperlink = this.SEModelManager.getHyperlink(hyperlinkRun.id);
            if (!hyperlink) {
                return;
            }
            hyperlink.hyperlinkStart.remove();
            hyperlink.hyperlinkEnd.remove();
            this.SEModelManager.deleteHyperlink(hyperlinkRun.id);
        });

        if (text) {
            this.deleteContent(selection);

            let run = this.positionManager.getRun(startPosition);
            run = run.split(startPosition.offset - run.getRunStartOffset());
            let hyperlinkTextRun = run.clone();

            hyperlinkTextRun.text = text || url;

            run.before(hyperlinkStart);
            run.before(hyperlinkTextRun);
            run.before(hyperlinkEnd);

            if (run.isEmpty()) {
                run.remove();
            }

            endPosition = new Position(startPosition.id, startPosition.offset + hyperlinkTextRun.length());
            this.selectionManager.setSelectionPosition(endPosition, true);
        } else {
            let startRun = this.positionManager.getRun(startPosition);
            startRun = startRun.split(startPosition.offset - startRun.getRunStartOffset());
            let endRun = this.positionManager.getRun(endPosition);
            endRun = endRun.split(endPosition.offset - endRun.getRunStartOffset());

            startRun.before(hyperlinkStart);
            endRun.before(hyperlinkEnd);

            if (startRun.isEmpty()) {
                startRun.remove();
            }
            if (endRun.isEmpty()) {
                endRun.remove();
            }
        }

        this.SEModelManager.setHyperlink(hyperlinkStart.id, hyperlinkStart, hyperlinkEnd);

    }

    /**
     * Selection에 해당하는 위치에 Hyperlink를 편집합니다.
     * @param {Number} id 
     * @param {String} url 
     * @param {String} text 
     */
    updateHyperlink(id, url, text) {
        const hyperlink = this.SEModelManager.getHyperlink(id);

        if (hyperlink) {
            const hyperlinkStart = hyperlink.hyperlinkStart;

            if (text) {
                const hyperlinkEnd = hyperlink.hyperlinkEnd;
                const hyperlinkSelection= new Selection(this.positionManager);
                let hyperlinkStartPosition = this.positionManager.getRunStartPosition(hyperlinkStart);
                let hyperlinkEndPosition = this.positionManager.getRunEndPosition(hyperlinkEnd);

                hyperlinkSelection.set(hyperlinkStartPosition, hyperlinkEndPosition);

                let textRuns = hyperlinkSelection.getTextRuns();
                let hyperlinkTextRun = textRuns[0].clone();
                textRuns.forEach(run => run.remove());

                hyperlinkTextRun.text = text || url;
                hyperlinkStart.after(hyperlinkTextRun);

                this.selectionManager.setSelectionPosition(this.positionManager.getRunEndPosition(hyperlinkTextRun), true);
            }

            hyperlinkStart.setTarget(url);
        }
    }

    /**
     * Picture SEModel에 Hyperlink 정보를 설정합니다.
     * @param {Selection} selection 
     * @param {String} url 
     */
    setPictureHyperlink(selection, url) {
        const picture = this.SEModelManager.getPicture(selection.anchor.pictureId);
        picture.setHyperlinkTarget(url);
    }

    /**
     * Picture 개체의 width height 값을 변경합니다.
     * @param {Number} picId
     * @param {Number} width 
     * @param {Number} height 
     */
    resizePicture(picId, width, height) {
        const picture = this.SEModelManager.getPicture(picId);
        this.setPictureSize(picture, width, height);
    }

    /**
     * Picture 개체의 위치를 변경합니다.
     * @param {Number} picId
     * @param {Position} position
     */
    movePicture(picId, position) {
        const picture = this.SEModelManager.getPicture(picId);

        let run = this.positionManager.getRun(position);
        run = run.split(position.offset - run.getRunStartOffset());
        run.before(picture);

        if (run.isEmpty()) {
            run.remove();
        }
    }

    /**
     * Picture 개체의 rotation을 변경합니다.
     * @param {Number} picId 
     * @param {String} type 
     */
    rotatePicture(picId, type) {
        const picture = this.SEModelManager.getPicture(picId);
        let rotation = util.get(picture.getProp(), ['shape_prop', 'transform', 'rotation_in_angle']) || 0;
        rotation = util.reviseRotation(rotation);

        switch(type) {
            case 'left':
                rotation -= 90000;
                if (rotation < 0) {
                    rotation = 270000;
                }
            break;
            case 'right':
                rotation += 90000;
                if (rotation > 360000) {
                    rotation = 90000;
                }
            break;
        }
        picture.setRotation(rotation);
    }

    /**
     * 이미지 파일을 서버에 업로드 전에 base64로 인코딩하여 DOM에 미리 삽입합니다.
     * @param {File} imageFile 
     * @returns {jQueryPromise}
     */
    preInsertImage(imageFile) {
        const deferred = $.Deferred();
        const reader = new FileReader();
        reader.onload = (e) => {
            const url = e.target.result;
            const elementId = this.SEModelManager.getNewElementId();
            const relationshipIdx = this.relationshipListManager.createItem(url, 'IMAGE', null, 'base64');
            const picture = Picture.createPicture(elementId, relationshipIdx);

            const position = this.selection.getStartPosition();
            let run = this.positionManager.getRun(position);
            run = run.split(position.offset - run.getRunStartOffset());
            run.before(picture);

            deferred.resolve(elementId);
        };

        reader.onerror = (e) => {
            console.log(e);
            deferred.reject();
        };

        reader.readAsDataURL(imageFile);

        return deferred.promise();
    }

    /**
     * 이미지 파일을 서버에 업로드합니다.
     * @param {File} imageFile 
     * @returns {jQueryPromise}
     */
    uploadImage(imageFile) {
        const deferred = $.Deferred();
        const extension = util.get(imageFile, ['type'], '').split('/')[1];

        const formData = new FormData();
        formData.append('imageFile', imageFile, imageFile.name);
        formData.append('extension', extension);

        $.ajax({
            url: this.imageUploadAPI,
            type: "POST",
            data: formData,
            processData: false,
            contentType: false,
            success: (data) => {
                deferred.resolve(data.uploadPath);
            }
        });

        return deferred.promise();
    }

    /**
     * url 이미지를 삽입합니다.
     * @param {String} url
     * @returns {Picture} picture
     */
    insertUrlImage(url) {
        const elementId = this.SEModelManager.getNewElementId();
        const relationshipIdx = this.relationshipListManager.createItem(url, 'IMAGE', null, 'url');
        const picture = Picture.createPicture(elementId, relationshipIdx);
        const position = this.selection.getStartPosition();
        
        let run = this.positionManager.getRun(position);
        run = run.split(position.offset - run.getRunStartOffset());
        run.before(picture);

        return picture;
    }

    /**
     * EndRunProp 속성을 가진 빈 텍스트런을 세팅합니다.
     * @param {String} action 
     */
    setEndRunProp(action, val) {
        if (!this.endRunProp) {
            const run = this.positionManager.getRun(this.selection.getStartPosition());
            this.endRunProp = TextRun.createTextRun(util.isTextRun(run) ? run.getProp() : null);
        }
        const value = val || this.toggleRunProp(this.endRunProp, action);
        switch(action) {
            case ACTION.FILL:
                this.endRunProp.setFill(value);
                break;
            case ACTION.TEXTFILL:
                this.endRunProp.setTextFill(value);
                break;
            case ACTION.FONTNAME:
                this.endRunProp.setTextFont(value);
                break;
            case ACTION.FONTSIZE:
                this.endRunProp.setFontSize(value);
                break;
            case ACTION.INCREASE_FONTSIZE:
                this.endRunProp.increaseFontSize();
                break;
            case ACTION.DECREASE_FONTSIZE:
                this.endRunProp.decreaseFontSize();
                break;
            case ACTION.BOLD:
                this.endRunProp.setBold(value);
                break;
            case ACTION.ITALIC:
                this.endRunProp.setItalic(value);
                break;
            case ACTION.UNDERLINE:
                this.endRunProp.setUnderline(value);
                break;
            case ACTION.STRIKE:
                this.endRunProp.setStrike(value);
                break;
        }
    }

    /**
     * End Run Prop을 초기화합니다.
     */
    initEndRunProp() {
        this.endRunProp = null;
    }

    /**
     * 인자로받은 run모델의 fill prop을 셋팅해주는 함수입니다.
     * @param {Array} runs 
     * @param {String} hex 
     */
    setFill(runs, hex) {
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.setFill(hex);
            }
        });
    }

    /**
     * 인자로받은 run모델의 text fill prop을 셋팅해주는 함수입니다.
     * @param {Array} runs 
     * @param {String} hex 
     */
    setTextFill(runs, hex) {
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.setTextFill(hex);
            }
        });
    }

    /**
     * 인자로받은 run모델의 font prop을 셋팅해주는 함수입니다.
     * @param {Array} runs 
     * @param {String} font
     */
    setTextFont(runs, value) {
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.setTextFont(value);
            }
        });
    }

    /**
     * 인자로받은 run모델의 font size prop을 셋팅해주는 함수입니다.
     * @param {Array} runs 
     * @param {String} size
     */
    setFontSize(runs, value) {
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.setFontSize(value);
            }
        });
    }

    /**
     * 인자로받은 run모델의 font size prop을 증가해주는 함수입니다.
     * @param {Array} runs
     */
    increaseFontSize(runs) {
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.increaseFontSize();
            }
        });
    }

        /**
     * 인자로받은 run모델의 fill size prop을 감소해주는 함수입니다.
     * @param {Array} runs
     */
    decreaseFontSize(runs) {
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.decreaseFontSize();
            }
        });
    }

    /**
     * 인자로 받은 run모델에 bold속성을 셋팅 혹은 해제합니다.
     * @param {Array} runs
     * @param {Boolean} val
     */
    setBold(runs, val) {
        const value = util.isNil(val) ? this.toggleRunProp(runs[0], 'bold') : val;
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.setBold(value);
            }
        });
    }

    /**
     * 인자로 받은 run모델에 italic속성을 셋팅 혹은 해제합니다.
     * @param {Array} runs
     * @param {Boolean} val
     */
    setItalic(runs, val) {
        const value = util.isNil(val) ? this.toggleRunProp(runs[0], 'italic') : val;
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.setItalic(value);
            }
        });
    }

    /**
     * 인자로 받은 run모델에 underline속성을 셋팅 혹은 해제합니다.
     * @param {Array} runs
     * @param {Boolean} val
     */
    setUnderline(runs, val) {
        const value = util.isNil(val) ? this.toggleRunProp(runs[0], 'underline') : val;
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.setUnderline(value);
            }
        });
    }

    /**
     * 인자로 받은 run모델에 strike속성을 셋팅 혹은 해제합니다.
     * @param {Array} runs
     * @param {Boolean} val
     */
    setStrike(runs, val) {
        const value = util.isNil(val) ? this.toggleRunProp(runs[0], 'strike') : val;
        runs.forEach(run => {
            if (util.isTextRun(run)) {
                run.setStrike(value);
            }
        });
    }

    /**
     * Run Element와 propName을 인자로 받아 prop을 toggle 시켜주는 함수입니다.
     * @param {Object} run Run Element
     * @param {String} propName 
     * @returns {Boolean} true | false
     */
    toggleRunProp(run, propName) {
        let prop = run.getProp();
        let value;
        switch (propName) {
            case 'bold':
                value = util.get(prop, ['bold'], false);
                break;
            case 'italic':
                value = util.get(prop, ['italic'], false);
                break;
            case 'underline':
                value = util.get(prop, ['underline', 'style'], 'none') === 'none' ? false : true;
                break;
            case 'strike':
                value = util.get(prop, ['strike', 'style'], 'none') === 'none' ? false : true;
                break;
        }
        return !value;
    }

    /**
     * paragraph의 가로 맞춤 속성을 설정합니다.
     * @param {Array} paragraphs
     * @param {String} value - 'LEFT', 'RIGHT', 'CENTER', 'JUSTIFY'
     */
    setAlign(paragraphs, value) {
        paragraphs.forEach(paragraph => {paragraph.setAlign(value);});
    }

    /**
     * paragraphs의 줄간격을 설정합니다.
     * @param {Array} paragraphs
     * @param {Number} value - 퍼센트 단위(100, 200, 300...)
     */
    setLineHeight(paragraphs, value) {
        paragraphs.forEach(paragraph => paragraph.setLineHeight(value));
    }
    /*
     * Picture SEModel의 size(width, height)를 셋팅합니다.
     * @param {Picture} picture 
     * @param {Number} width 
     * @param {Number} height 
     */
    setPictureSize(picture, width, height) {
        if (width) {
            const $elContents = $(`#${this.editorId} .se-main .se-contents`);
            width = Math.min(Math.max(width, MIN_PICTURE_SIZE), $elContents.width());
            util.set(picture.getProp(), ['shape_prop', 'transform', 'size', 'width_in_emu'], util.px2emu(width));
        }
        if (height) {
            height = Math.max(height, MIN_PICTURE_SIZE);
            util.set(picture.getProp(), ['shape_prop', 'transform', 'size', 'height_in_emu'], util.px2emu(height));
        }
    }

    /**
     * 표에서 cell의 배경색을 설정합니다.
     * @param {String} hex 
     */
    setCellBackground(hex) {
        this.selection.getCells().forEach(cell => {
            cell.setBackground(hex);
        });
    }

    /**
     * 표에서 cell의 세로정렬을 설정합니다.
     * @param {String} value 
     */
    setCellVertAlign(value) {
        this.selection.getCells().forEach(cell => {
            cell.setVertAlign(value);
        });
    }
}

export default ModelHandler;


