import Element from './Element';
import consts from '../consts/consts';
import util from '../util/util';

/**
 * TextRun
 * @extends Element
 */
class TextRun extends Element {
    /**
     * 빈 Text Run을 생성하여 반환합니다.
     * @returns {TextRun} Text Run
     */
    static createTextRun(prop) {
        return new TextRun({
            text: '',
            prop: prop || {"fonts":{"hint":"eastAsia"}}
        });
    }

    /**
     * Element의 Type을 반환합니다.
     * @returns {String} TextRun
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'TextRun';
    }

    /**
     * 나중의 export를 고려하여, SEModel을 docModel 형태로 만들기 위한 함수입니다.<br/>
     * docModel 형태의 textrun를 만들어서 JSON Object를 반환합니다.
     * @returns {Object} textrun JSON Object
     */
    toPbModel() {
        let textrun = {
            "textrun": {
                "text": this.text,
                "prop": this.prop
            },
            "type": "TEXTRUN"
        };
        return textrun;
    }

    /**
     * index를 인자로 받아서 TextRun Element를 cloned하여 뒤에 붙여주는 함수입니다.</br>
     * TextRun Element의 text를 index로 계산하여 자르고, cloned된 TextRun Element에 나머지 text를 붙여줍니다.</br>
     * 그리고 cloned된 TextRun Element를 TextRun Element(this)뒤에 insert 시켜줍니다.
     * @private
     * @param {Number} index 
     * @returns {Run} Run Element 
     * @example
     * "동해물과"(TextRun).split(2)
     * this.text = '동해'
     * cloned.text = '물과'
     */
    split(index) {
        let cloned = this.clone();
        let text = this.text;
        this.text = text.substring(0, index);
        cloned.text = text.substring(index);
        cloned.insertAfter(this);
        return cloned;
    }

    /**
     * TextRun Element의 properties를 반환합니다.
     * @returns {Object} - TextRun Element Properties
     */
    getProp() {
        return util.get(this, ['prop']) ? util.get(this, ['prop']) : this.prop = {};
    }

    /**
     * TextRun Element의 text 값을 반환합니다.
     * @returns {String} - TextRun Element의 text
     */
    getText() {
        return util.get(this, ['text'], '');
    }

    /**
     * TextRun Element의 text 길이를 반환합니다.
     * @returns {Number} - TextRun Element의 text length
     */
    length() {
        return this.getText().length;
    }

    /**
     * TextRun Element의 text 길이가 0인지를 검사합니다.
     * @returns {Boolean} - text 길이가 0일 경우 true
     */
    isEmpty() {
        return this.length() === 0;
    }

    /**
     * {'r': 255, 'g': 255, 'b': 255} 와 같은 RGB값으로 이뤄진 value를 인자로 받아서</br>
     * 모델의 fill prop을 셋팅해주는 함수입니다.
     * @param {String} - hex value 
     */
    setFill(value) {
        let prop = this.getProp();
        prop.fill = {
            'solid_fill': {
                'color': {
                    'srgb_color': {
                        'value_in_hex': util.hexToDecimal(value)
                    },
                    'type': 'SRGB'
                }
            },
            'type': 'SOLID_FILL'
        };
    }

    /**
     * {'r': 255, 'g': 255, 'b': 255} 와 같은 RGB값으로 이뤄진 value를 인자로 받아서</br>
     * 모델의 textfill prop을 셋팅해주는 함수입니다.
     * @param {String} - hex value 
     */
    setTextFill(value) {
        let prop = this.getProp();
        if (value instanceof Function) {
            value = value(prop[actionName]);
        }

        prop.text_fill = {
            'solid_fill': {
                'color': {
                    'srgb_color': {
                        'value_in_hex': util.hexToDecimal(value)
                    },
                    'type': 'SRGB'
                }
            },
            'type': 'SOLID_FILL'
        };
    }

    /**
     * '나눔고딕', '궁서' 와 같은 fontfamily value를 인자로 받아서</br>
     * 모델의 textfill prop을 셋팅해주는 함수입니다.
     * @param {String} String fontfamily 
     */
    setTextFont(value) {
        let prop = this.getProp();
        prop.fonts = {
            'ascii': value
        };
    }

    /**
     * FONT_SIZE_STEP과 모델의 fontSize prop을 비교하여 모델의 fontsize prop을 셋팅해주는 함수입니다. 
     */
    increaseFontSize() {
        let prop = this.getProp();
        let currentSize = util.get(prop, ['font_size_in_sunit'], consts.DEFAULT_FONT_SIZE);
        let fontSize = consts.FONT_SIZE_STEP.find(size => size > currentSize) || currentSize;
        prop.font_size_in_sunit = fontSize;
    }

    /**
     * FONT_SIZE_STEP과 모델의 fontSize prop을 비교하여 모델의 fontsize prop을 셋팅해주는 함수입니다. 
     */
    decreaseFontSize(value) {
        let prop = this.getProp();
        let currentSize = util.get(prop, ['font_size_in_sunit'], consts.DEFAULT_FONT_SIZE);
        let fontSize = consts.FONT_SIZE_STEP.slice(0).reverse().find(sz => sz < currentSize) || currentSize;
        prop.font_size_in_sunit = fontSize;
    }

    /**
     * sunit단위의 fontsize를 인자로 받아서 모델의 fontsize prop을 셋팅해주는 함수입니다.
     * @param {Number} Number sunit 
     */
    setFontSize(value) {
        let prop = this.getProp();
        prop.font_size_in_sunit = value;
    }

    /**
     * Boolean값을 인자로 받아서 모델의 bold prop을 셋팅해주는 함수입니다.
     * @param {Boolean} value 
     */
    setBold(value) {
        let prop = this.getProp();
        prop.bold = value ? 1 : 0;;
    }

    /**
     * Boolean값을 인자로 받아서</br>
     * 모델의 italic prop을 셋팅해주는 함수입니다.
     * @param {Boolean} value 
     */
    setItalic(value) {
        let prop = this.getProp();
        prop.italic = value ? 1 : 0;
    }

    /**
     * Boolean값을 인자로 받아서 모델의 underline prop을 셋팅해주는 함수입니다.
     * @param {Boolean} value 
     */
    setUnderline(value) {
        let prop = this.getProp();
        prop.underline = {
            'style': value ? 'single' : 'none'
        };
    }

    /**
     * Boolean값을 인자로 받아서 모델의 strike prop을 셋팅해주는 함수입니다.
     * @param {Boolean} value 
     */
    setStrike(value) {
        let prop = this.getProp();
        prop.strike = {
            'style': value ? 'single' : 'none'
        };
    }
}

export default TextRun;


