import ACTION from '../consts/actionMap';
import ControllerInterface from './ControllerInterface';
import consts from '../consts/consts';
import util from '../util/util';

const UI_NAME = consts.UI_NAME;
const LISTS_FORMAT = consts.LISTS_FORMAT;
/**
 * UI 동작을 수행하는 class입니다.<br/>
 * editor에서 UiManager를 초기화 합니다.
 */
class UiController extends ControllerInterface {
    /**
     * UiController 클래스의 생성자입니다.
     * @param {SynapEditor} editor
     */
    constructor(editor) {
        super();
        this.editorId = editor.id;
        this.UiManager = editor.getUIManager();
        this.selectionManager = editor.getSelectionManager();
        this.selectionRenderer = editor.getSelectionRenderer();
        this.elSEContents = document.querySelector(`#${this.editorId} .se-contents`);
        this.elSelections = document.querySelector(`#${this.editorId} .se-selections`);
        this.elTableHandle = document.querySelector(`#${this.editorId} .se-table-handler`);
        this.selection = this.selectionManager.selection;
        this.positionManager = editor.getPositionManager();
        this.listsManager = editor.getListsManager();
    }
}

/**
 * ui 관련 동작을 수행하는 uiController 객체입니다.
 * 각 동작 메소드는 UiController를 상속 받습니다.
 */
const uiController = {
    [ACTION.SHOW_TEXT_BALLOON_POPUP]: class extends UiController {
        execute() {
            const elSelection = this.elSelections.querySelector(':last-child');

            if (elSelection) {
                this.UiManager.showBalloonPopup(elSelection, UI_NAME.TEXT_BALLOON);
            }
        }
    },
    [ACTION.SHOW_PICTURE_BALLOON_POPUP]: class extends UiController {
        execute(picId) {
            this.UiManager.showBalloonPopup(this.elSEContents.querySelector(`img[pic-id="${picId}"]`), UI_NAME.PICTURE_BALLOON);
        }
    },
    [ACTION.SHOW_TABLE_CELL_BALLOON_POPUP]: class extends UiController {
        execute() {
            this.UiManager.showBalloonPopup(this.elTableHandle.querySelector('.se-table-cell-prop'), UI_NAME.TABLE_CELL_BALLOON);
        }
    },

    [ACTION.TOGGLE_LINK_POPUP]: class extends UiController {
        preExecute() {
            this.selectionManager.expendHyperlinkSelection();
            this.selectionRenderer.renderSelection(this.selection);
        }

        execute(event) {
            const linkData = this.selectionManager.getHyperlinkData();
            const linkPopup = this.UiManager.getLinkPopup();
            const isPicture = this.selection.isPictureSelection();

            linkPopup.toggle(event.currentTarget, linkData, isPicture);
        }
    },
    [ACTION.TOGGLE_FONT_COLOR_PICKER]: class extends UiController {
        execute(event) {
            const colorPicker = this.UiManager.getColorPicker();
            colorPicker.toggle(event.currentTarget, ACTION.TEXTFILL);
        }
    },
    [ACTION.TOGGLE_FONT_BACKGROUND_COLOR_PICKER]: class extends UiController {
        execute(event) {
            const colorPicker = this.UiManager.getColorPicker();
            colorPicker.toggle(event.currentTarget, ACTION.FILL);
        }
    },
    [ACTION.TOGGLE_TABLE_POPUP]: class extends UiController {
        execute(event) {
            const tablePopup = this.UiManager.getTablePopup();
            const target = event.currentTarget;
        }
    },
    [ACTION.TOGGLE_IMAGE_POPUP]: class extends UiController {
        execute(event) {
            const imagePopup = this.UiManager.getImagePopup();
            imagePopup.toggle(event.currentTarget);
        }
    },
    [ACTION.HIDE_ALL_POPUP]: class extends UiController {
        execute($targetBtn) {
            const targetBtnName = $targetBtn && $targetBtn.attr('name');
            this.UiManager.hideAllPopup(targetBtnName);
        }
    },
    [ACTION.SHOW_CHARACTER_PICKER]: class extends UiController {
        execute(actionName) {
            this.UiManager.getCharacterPicker().show(actionName);
        }
    },

    [ACTION.SHOW_START_VALUE_DIALOG]: class extends UiController {
        execute() {
            let format, startValue;
            const paragraph = this.positionManager.getElement(this.selection.getStartPosition());

            if (paragraph.isLists()) {
                const id = paragraph.getListsId();
                const level = paragraph.getListsLevel();
                format = this.listsManager.getListsFormat(id, level);
                startValue = this.listsManager.getStartValue(id, level);
            }

            this.UiManager.getStartValueDialog().show(format, startValue);
        }
    },

    [ACTION.SET_START_VALUE_BUTTON_STATUS]: class extends UiController {
        execute(name) {
            let bEnable = false;
            const paragraph = this.positionManager.getElement(this.selection.getStartPosition());

            if (paragraph.isLists()) {
                const id = paragraph.getListsId();
                const level = paragraph.getListsLevel();
                const format = this.listsManager.getListsFormat(id, level);
                bEnable = format !== LISTS_FORMAT.BULLET;
            }

            this.UiManager.buttonMap[name].forEach(dropdown => {
                const targetItem = util.last(dropdown.dropdownItems);
                if (bEnable) {
                    targetItem.enable();
                } else {
                    targetItem.disable();
                }
            });
        }
    }
};

export default uiController;


