/**
 * 모든 UI가 상속받는 UI 클래스입니다.
 * @hideconstructor
 */
class UIInterface {
    constructor() {
        this.$element = null;
    }

    /**
     * UI의 jQuery Element를 가져옵니다.
     * @returns {jQuery} UI의 jQuery Element
     * @example
     * var ui = new UI();
     * var $uiElement = ui.getElement();
     */
    getElement() {}

    /**
     * UI의 jQuery Element를 생성해 가져옵니다.
     * UI를 상속받은 클래스는 createElement() 메소드를 Override 합니다.
     * @private
     * @returns {jQuery} 생성된 UI의 jQuery Element
     */
    __createElement__() {
        // do override this
    }
}

export default UIInterface;


