import ACTION from './consts/actionMap';

/**
 * shortcut Event
 * event 이름을 관리하는 Map 입니다.
 */
const shortcuts = {
    // selection
    'Left': ACTION.LEFT,
    'Right': ACTION.RIGHT,
    'Up': ACTION.UP,
    'Down': ACTION.DOWN,
    'PageUp': ACTION.PAGE_UP,
    'PageDown': ACTION.PAGE_DOWN,
    'Home | Cmd+Left': ACTION.LINE_START,
    'End | Cmd+Right': ACTION.LINE_END,
    'Ctrl+Home | Cmd+Up': ACTION.DOCUMENT_START,
    'Ctrl+End | Cmd+Down': ACTION.DOCUMENT_END,
    'Ctrl+Left | Alt+Left': ACTION.PREVIOUS_WORD,
    'Ctrl+Right | Alt+Right': ACTION.NEXT_WORD,
    'Ctrl+Up | Alt+Up': ACTION.PREVIOUS_PARAGRAPH_START,
    'Ctrl+Down | Alt+Down': ACTION.NEXT_PARAGRAPH_START,

    'Shift+Left': ACTION.EXTEND_LEFT,
    'Shift+Right': ACTION.EXTEND_RIGHT,
    'Shift+Up': ACTION.EXTEND_UP,
    'Shift+Down': ACTION.EXTEND_DOWN,
    'Shift+PageUp': ACTION.EXTEND_PAGE_UP,
    'Shift+PageDown': ACTION.EXTEND_PAGE_DOWN,
    'Shift+Home | Cmd+Shift+Left': ACTION.EXTEND_LINE_START,
    'Shift+End | Cmd+Shift+Right': ACTION.EXTEND_LINE_END,
    'Ctrl+Shift+Home | Cmd+Shift+Up': ACTION.EXTEND_DOCUMENT_START,
    'Ctrl+Shift+End | Cmd+Shift+Down': ACTION.EXTEND_DOCUMENT_END,
    'Ctrl+Shift+Up | Alt+Shift+Up': ACTION.EXTEND_PREVIOUS_PARAGRAPH_START,
    'Ctrl+Shift+Down | Alt+Shift+Down': ACTION.EXTEND_NEXT_PARAGRAPH_START,
    'Ctrl+Shift+Left | Alt+Shift+Left': ACTION.EXTEND_PREVIOUS_WORD,
    'Ctrl+Shift+Right | Alt+Shift+Right': ACTION.EXTEND_NEXT_WORD,

    'Ctrl+A | Cmd+A': ACTION.SELECT_ALL,

    // instantEdit
    'Backspace': ACTION.DELETE_LEFT,
    'Delete': ACTION.DELETE_RIGHT,

    // clipboard
    'Ctrl+C | Cmd+C': ACTION.COPY,
    'Ctrl+X | Cmd+X': ACTION.CUT,
    'Ctrl+V | Cmd+V': ACTION.PASTE,

    // edit
    'Enter': ACTION.ENTER,
    'Shift+Enter': ACTION.INSERT_LINE_BREAK,
    'Tab': ACTION.TAB,
    'Shift+Tab': ACTION.DECREASE_INDENT,
    'Ctrl+B | Cmd+B': ACTION.BOLD,
    'Ctrl+I | Cmd+I': ACTION.ITALIC,
    'Ctrl+U | Cmd+U': ACTION.UNDERLINE,
    'Alt+Shift+5': ACTION.STRIKE,
    'Ctrl+. | Cmd+.': 'subscript',
    'Ctrl+, | Cmd+,': 'superscript',
    'Ctrl+Shift+, | Cmd+Shift+,': ACTION.DECREASE_FONTSIZE,
    'Ctrl+Shift+. | Cmd+Shift+.': ACTION.INCREASE_FONTSIZE,
    'Ctrl+L | Cmd+L': ACTION.ALIGN_LEFT,
    'Ctrl+E | Cmd+E': ACTION.ALIGN_CENTER,
    'Ctrl+R | Cmd+R': ACTION.ALIGN_RIGHT,
    'Ctrl+J | Cmd+J': ACTION.ALIGN_JUSTIFY,
    'Ctrl+] | Cmd+]': ACTION.INCREASE_INDENT,
    'Ctrl+[ | Cmd+[': ACTION.DECREASE_INDENT,
    'Ctrl+Z | Cmd+Z': ACTION.UNDO,
    'Ctrl+Y | Cmd+Y': ACTION.REDO,

    // etc
    'Ctrl+P | Cmd+P': 'print'
};

/**
 * key Event code에 따른 keyName Map입니다.
 */
const keyNames = {
    '8': 'Backspace',
    '9': 'Tab',
    '13': 'Enter',
    '27': 'Esc',
    '32': 'Space',
    '33': 'PageUp',
    '34': 'PageDown',
    '35': 'End',
    '36': 'Home',
    '37': 'Left',
    '38': 'Up',
    '39': 'Right',
    '40': 'Down',
    '45': 'Insert',
    '46': 'Delete',
    '112': 'F1',
    '113': 'F2',
    '114': 'F3',
    '115': 'F4',
    '116': 'F5',
    '117': 'F6',
    '118': 'F7',
    '119': 'F8',
    '120': 'F9',
    '121': 'F10',
    '122': 'F11',
    '123': 'F12',
    '186': ';',
    '59': ';', // 59 for Firefox
    '187': '+',
    '188': ',',
    '189': '-',
    '190': '.',
    '191': '/',
    '192': '`',
    '219': '[',
    '220': '\\',
    '221': ']',
    '222': "'"
};

/**
 * keyCode를 받아 keyName Array에서 해당 Action String을 반환합니다.
 * @private
 * @param {Number} code
 * @returns {String} Action String
 */
function getKeyName(code) {
    // 0-9, A-Z => String.fromCharCode()
    return ((48 <= code && code <= 57) || (65 <= code && code <= 90))
        ? String.fromCharCode(code)
        : keyNames[code];
}

/**
 * OS 환경에 따라 단축키를 다르게 초기화 합니다.
 * @private
 * @returns {Function} 단축키에 해당하는 동작을 반환하는 함수
 */
function initShorcut() {
    let myShoortcuts = {};
    const isMac = navigator.appVersion.indexOf('Mac') > -1;
    Object.keys(shortcuts).forEach(key => {
        let action = shortcuts[key];
        let ks = key.split('|');
        let shortcut = (isMac && ks[1] ? ks[1] : ks[0]).trim();
        myShoortcuts[shortcut] = action;
    });

    /**
     * 키보드 이벤트에서 키의 조합에 따라 수행해야 할 동작을 반환합니다.
     * @param {Event} e KeyboardEvent
     * @return {String} 단축키에 해당하는 동작
     */
    return function shortcut(e) {
        let aShortCut = [], shortcutAction;
        let keyName = getKeyName(e.keyCode);
    
        if (e.ctrlKey) {
            aShortCut.push('Ctrl');
        }
        if (e.metaKey) {
            aShortCut.push('Cmd');
        }
        if (e.altKey) {
            aShortCut.push('Alt');
        }
        if (e.shiftKey) {
            aShortCut.push('Shift');
        }
        if (keyName) {
            aShortCut.push(keyName);
        }
    
        shortcutAction = myShoortcuts[aShortCut.join('+')];
        return shortcutAction || null;
    };
}

export default initShorcut();


