/**
 * 특수기호 데이터를 저장하고 있는 배열 객체입니다.
 * CharacterPicker에서 사용합니다.
 */
const specialCharacter = [{
    category: 'default',
    label: '일반 기호',
    values: ['a', 'b', 'c']
}, {
    category: 'arrow',
    label: '화살표',
    values: ['d', 'e', 'f']
}, {
    category: 'math',
    label: '수학',
    values: ['g', 'h', 'i']
}, {
    category: 'circle',
    label: '원문자',
    values: ['j', 'k', 'l']
}, {
    category: 'number',
    label: '숫자/단위',
    values: ['m', 'n', 'o']
}];

export default specialCharacter;


