import consts from './consts/consts';

const UI_NAME = consts.UI_NAME;

/**
 * 사이냅 에디터의 기본 설정입니다.
 */
const defaultConfiguration = {
    // 툴바
    toolbar: [UI_NAME.PRINT, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.UNDO, UI_NAME.REDO, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.PARAGRAPH_STYLE_TEXT_VERSION, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.FONT_FAMILY_TEXT_VERSION, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.FONT_SIZE_TEXT_VERSION, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.INCREASE_FONT_SIZE, UI_NAME.DECREASE_FONT_SIZE, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.BOLD, UI_NAME.ITALIC, UI_NAME.UNDERLINE, UI_NAME.STRIKE, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.SUPERSCRIPT, UI_NAME.SUBSCRIPT, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.FONT_COLOR, UI_NAME.FONT_BACKGROUND_COLOR, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.ALIGN, UI_NAME.HORIZONTAL_SEPARATOR,
        UI_NAME.CUT, UI_NAME.COPY, UI_NAME.PASTE, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.LINK, UI_NAME.UNLINK, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.INSERT_TABLE, UI_NAME.IMAGE, UI_NAME.BACKGROUND_IMAGE, UI_NAME.VIDEO, UI_NAME.FILE, UI_NAME.BOOKMARK, UI_NAME.HORIZONTAL_LINE,
        UI_NAME.SPECIAL_CHARACTER, UI_NAME.QUOTE, UI_NAME.EMOTICON, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.BULLET_LISTS, UI_NAME.NUMBERED_LISTS, UI_NAME.MULTI_LISTS, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.INDENT, UI_NAME.OUTDENT, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.LINE_HEIGHT, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.FULL_SCREEN, UI_NAME.VERTICAL_SEPARATOR,
        UI_NAME.HELP, UI_NAME.ABOUT
    ],

    // 에디터 높이
    height: 600,

    // 언어팩이 존재하지 않을 때 기본으로 에디터를 표시할 언어를 설정합니다. (기본값 : en, 사용 가능한 값: ko, en, ja, zh...)
    defaultLanguage: 'en',

    // 에디터를 표시할 언어를 직접 설정합니다. ( ko, en, ja, zh...)
    language: 'ko',

    // CUSTOMSTYLE Config
    customStyle: {
        'BigRed': {
            'fontSize': '32000',
            'textFill': {
                r: 255,
                g: 0,
                b: 0
            }
        },
        'SmallBlue': {
            'fontSize': '16000',
            'textFill': {
                r: 0,
                g: 0,
                b: 255
            },
            'bold': 1
        }
    },

    importAPI: `${location.protocol}//${location.hostname}:${location.port}/getSerializedPbData`,
    imageUploadAPI: `${location.protocol}//${location.hostname}:${location.port}/uploadImage`
};

export default defaultConfiguration;


