import Popup from './Popup';
import $ from 'jquery';
import consts from '../../consts/consts';

const UI_NAME = consts.UI_NAME;
const CELL_WIDTH = 19;
const CELL_HEIGHT = 24;

const MIN_ROW_COUNT = 5;
const MIN_COL_COUNT = 5;
const MAX_ROW_COUNT = 10;
const MAX_COL_COUNT = 10;

const POPUP_WIDTH = 50;
const POPUP_HEIGHT = 50;

/**
 * TablePopup 클래스입니다.
 * Popup 클래스를 상속 받습니다.
 * @extends {Popup}
 */
class TablePopup extends Popup {
    /**
     * Creates an instance of TablePopup.
     * @param {String} editorId - 에디터를 식별할 수 있는 아이디 
     */
    constructor(editorId) {
        super(editorId, UI_NAME.INSERT_TABLE, POPUP_WIDTH, POPUP_HEIGHT);

        this.showPosition = undefined; // 테이블팝업이 팝업된 위치. show()에서 세팅됩니다.

        this.bUseHeader = false;
        this.bUseFooter = false;
        this.bUseCloseBtn = false;

        this.selectedRow = 0;
        this.selectedCol = 0;
        this.outerOffset = {
            width: MIN_COL_COUNT * CELL_WIDTH,
            height: MIN_ROW_COUNT * CELL_HEIGHT
        };

        this.createDom();
        this.attachEvent();
    }

    /**
     * TablePopup Dom을 생성합니다.
     * @private
     */
    createDom() {
        super.createDom();
    }

    /**
     * TablePopup의 outerElement를 반환합니다. 
     * @private
     * @returns {jQueryElement}
     */
    getOuterElement() {
        return this.getElement().find('.se-table-popup-outer');
    }

    /**
     * TablePopup의 innerElement를 반환합니다. 
     * @private
     * @returns {jQueryElement}
     */
    getInnerElement() {
        return this.getElement().find('.se-table-popup-inner');
    }

    /**
     * TablePopup의 cellElement를 반환합니다. 
     * @private
     * @returns {jQueryElement}
     */
    getCellElement() {
        return this.getElement().find('.se-table-popup-cell');
    }

    /**
     * TablePopup의 row, col에 해당하는 cellElement를 반환합니다. 
     * @private
     * @param {Number} row - 행 번호
     * @param {Number} col - 열 번호
     * @returns {jQueryElement}
     */
    getCellElementByNum(row, col) {
        return this.getElement().find(`[row=${row}][col=${col}]`);
    }

    /**
     * outerElement의 오프셋을 업데이트 합니다.
     * @private
     * @param {Number} [row=MIN_ROW_COUNT] - 행 번호
     * @param {Number} [col=MIN_COL_COUNT] - 열 번호
     */
    updateOuterOffset(row = MIN_ROW_COUNT, col = MIN_COL_COUNT) {
        const $outer = this.getOuterElement();
        const width = Math.max(MIN_COL_COUNT, col) * CELL_WIDTH;
        const height = Math.max(MIN_ROW_COUNT, row) * CELL_HEIGHT;

        $outer.width(width);
        $outer.height(height);

        this.outerOffset.width = width;
        this.outerOffset.height = height;
    }

    /**
     * innerElement를 새로운 innerElement로 교체합니다. 
     * @private
     * @param {Number} row - 행 번호
     * @param {Number} col - 열 번호
     */
    replaceInnerElement(row, col) {
        const $newInner = this.makeInnerElement(row, col);
        this.getInnerElement().replaceWith($newInner);
    }

    /**
     * 좌측 상단에 표현되는 메시지를 업데이트 합니다.
     * @private
     * @param {Number} row - 행 번호
     * @param {Number} col - 열 번호
     */
    updateMessage(row, col) {
        const $table = this.getElement();
        $table.find('.se-table-popup-row').html(Math.min(row, MAX_ROW_COUNT));
        $table.find('.se-table-popup-col').html(Math.min(col, MAX_COL_COUNT));
    }

    /**
     * 팝업의 Body 영역에 들어갈 jQueryElement를 반환합니다.
     * @param {Number} [row=MIN_ROW_COUNT] 
     * @param {Number} [col=MIN_COL_COUNT] 
     * @returns {jQueryElement}
     */
    getBodyElement(row = MIN_ROW_COUNT, col = MIN_COL_COUNT) {
        const html = `
            <div class='se-popup-body'>
                <div class='se-table-popup-msg'>
                    <span class='se-table-popup-row'>${this.selectedRow}</span>
                    <span>X</span>
                    <span class='se-table-popup-col'>${this.selectedCol}</span>
                </div>
            </div>
        `;

        const $body = $(html);
        const $outer = this.makeOuterElement();
        const $inner = this.makeInnerElement(row, col);

        $outer.append($inner);
        $body.append($outer);

        return $body;
    }

    /**
     * outerElement를 새로 만들어 반환합니다.
     * @private
     * @returns {jQueryElement}
     */
    makeOuterElement() {
        const width = this.outerOffset.width;
        const height = this.outerOffset.height;
        const $outer = $(`<div class='se-table-popup-outer' style='width: ${width}px; height: ${height}px;'></div>`);
        return $outer;
    }

    /**
     * innerElement를 새로 만들어 반환합니다.
     * @private
     * @returns {jQueryElement}
     */
    makeInnerElement(maxRow, maxCol) {
        const width = this.outerOffset.width + 50;
        const height = this.outerOffset.height + 50;
        const $inner = $(`<div class='se-table-popup-inner' style='width: ${width}px; height: ${height}px;'></div>`);

        for (let row = 1; row <= maxRow; row++) {
            const $tableRow = $(`<div class='se-table-layer-row'></div>`);

            for (let col = 1; col <= maxCol; col++) {
                const $tableCell = this.makeCellElement(row, col);
                $tableRow.append($tableCell);
            }

            $inner.append($tableRow);
        }
        $inner.click(this.insertTable.bind(this));
        $inner.mousemove(this.selectCell.bind(this));

        return $inner;
    }

    /**
     * cellElement를 새로 만들어 반환합니다.
     * @private
     * @returns {jQueryElement}
     */
    makeCellElement(row, col) {
        const $tableCell = $(`<div class='se-table-popup-cell' row='${row}' col='${col}'></div>`);
        return $tableCell;
    }

    /**
     * 테이블을 삽입합니다.
     * @private
     * @param {Event} event - jQuery Event 객체
     */
    insertTable(event) {
        const $inner = this.getInnerElement();
        const posX = event.pageX - $inner.offset().left;
        const posY = event.pageY - $inner.offset().top;
        const rowNum = Math.min(parseInt(posY / CELL_HEIGHT) + 1, MAX_ROW_COUNT);
        const colNum = Math.min(parseInt(posX / CELL_WIDTH) + 1, MAX_COL_COUNT);

        this.hide();

        console.log(rowNum, colNum);
    }

    /**
     * 셀을 선택합니다.
     * @private
     * @param {Event} event - jQuery Event 객체
     */
    selectCell(event) {
        const $inner = this.getInnerElement();
        const posX = event.pageX - $inner.offset().left;
        const posY = event.pageY - $inner.offset().top;

        this.selectedRow = parseInt(posY / CELL_HEIGHT) + 1;
        this.selectedCol = parseInt(posX / CELL_WIDTH) + 1;

        if (this.selectedRow > 0 && this.selectedCol > 0) {
            let renderRow = Math.min(Math.max(this.selectedRow, MIN_ROW_COUNT), MAX_ROW_COUNT - 1);
            let renderCol = Math.min(Math.max(this.selectedCol, MIN_COL_COUNT), MAX_COL_COUNT - 1);

            const $rightBottomCell = this.getCellElementByNum(renderRow, renderCol);

            if ($rightBottomCell && $rightBottomCell.offset()) {
                const doAddRow = posY > $rightBottomCell.offset().top - $inner.offset().top;
                const doAddCol = posX > $rightBottomCell.offset().left - $inner.offset().left;

                renderRow = doAddRow ? renderRow + 1 : renderRow;
                renderCol = doAddCol ? renderCol + 1 : renderCol;

                if (renderRow !== this.prevRenderRow || renderCol !== this.prevRenderCol) {
                    this.updateOuterOffset(renderRow, renderCol);
                    this.replaceInnerElement(renderRow, renderCol);

                    this.prevRenderRow = renderRow;
                    this.prevRenderCol = renderCol;
                }

                this.updateMessage(this.selectedRow, this.selectedCol);
                this.getCellElement().removeClass('select');
                
                for (let row = 1; row <= this.selectedRow; row++) {
                    for (let col = 1; col <= this.selectedCol; col++) {
                        const $cell = this.getCellElementByNum(row, col);
                        $cell.addClass('select');
                    }
                }
            }
        }

        const position = {
            left: this.showPosition.left - (this.getSize().width / 2)
        };
        this.setPosition(position);
    }

    /**
     * 테이블 팝업을 보여줍니다.
     * @param {Object} [position] 팝업을 보여줄 위치
     * @param {Boolean} [isBalloonAction=false] 풍선 도구에서 발생한 액션인지 여부
     */
    show(position, isBalloonAction = false) {
        super.show(position, isBalloonAction);

        this.showPosition = {
            top: position.top,
            left: position.left + (this.getSize().width / 2)
        };
    }

}

export default TablePopup;


