import $ from 'jquery';
/**
 * 유틸리티입니다.
 * @static
 * @namespace util
 */

const ZWSP = 8203; // '&#8203 Zero Width Space 빈 텍스트 런을 표현하기 위해 사용합니다.'
const HTMLUtil = {
    /**
     * 인자로 받은 HTML Element가 ElementNode인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isHTMLElementNode : function(el) {
        return !!el && el.nodeType === Node.ELEMENT_NODE;
    },

    /**
     * 인자로 받은 HTML Element가 TextNode인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isTextNode : function(el) {
        return !!el && el.nodeType === Node.TEXT_NODE;
    },

    /**
     * 인자로 받은 HTML Element가 빈 TextNode인지 검사합니다.
     * TextNode가 ZWSP만 가질 경우에도 true입니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isEmptyTextNode : function(el) {
        return this.isTextNode(el) && el.length === 0 || el.textContent.charCodeAt() === ZWSP;
    },

    /**
     * 인자로 받은 HTML Element가 Paragraph Level인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isParagraphHTMLElement : function(el) {
        return this.isHTMLElementNode(el) && (['P', 'LI', 'H1', 'H2', 'H3', 'H4', 'H5', 'H6'].indexOf(el.tagName) > -1);
    },

    /**
     * 인자로 받은 HTML Element가 list인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isListHTMLElement : function(el) {
        return this.isHTMLElementNode(el) && (['UL', 'OL'].indexOf(el.tagName) > -1);
    },

    /**
     * 인자로 받은 HTML Element가 Span인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isSpanHTMLElement : function(el) {
        return this.isHTMLElementNode(el) && el.tagName === 'SPAN';
    },

    /**
     * 인자로 받은 HTML Element가 Span인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isAHTMLElement : function(el) {
        return this.isHTMLElementNode(el) && el.tagName === 'A';
    },

    /**
     * 인자로 받은 HTML Element가 BR인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isBrHTMLElement : function (el) {
        return this.isHTMLElementNode(el) && el.tagName === 'BR';
    },

    /**
     * 인자로 받은 HTML Element가 Image인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isImageHTMLElement : function (el) {
        return this.isHTMLElementNode(el) && el.tagName === 'IMG';
    },

    /**
     * 인자로 받은 HTML Element가 Video인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isVideoHTMLElement : function (el) {
        return this.isHTMLElementNode(el) && el.tagName === 'VIDEO';
    },

    /**
     * 인자로 받은 HTML Element가 개체인지(Image, Video) 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isPictureHTMLElement : function (el) {
        return this.isImageHTMLElement(el) || this.isVideoHTMLElement(el);
    },

    /**
     * 인자로 받은 HTML Element가 Table인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isTableHTMLElement : function (el) {
        return this.isHTMLElementNode(el) && el.tagName === 'TABLE';
    },

    /**
     * 인자로 받은 HTML Element가 Hr인지 검사합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {Boolean}
     */
    isHrHTMLElement : function (el) {
        return this.isHTMLElementNode(el) && el.tagName === 'HR';
    },

    /**
     * HTML의 'para-id' 속성이 있는 부모를 탐색합니다.
     * @param {HTMLElement} el HTML Element
     * @returns {String} elementId
     */
    getHTMLElementId : function(el) {
        let elementId = el.getAttribute('para-id');
        if (!elementId && el.parentElement) {
            return this.getHTMLElementId(el.parentElement);
        }
        return elementId;
    },

    /**
     * 해당 ID의 HTMLElement를 반환합니다.
     * @param {String} id
     * @returns {HTMLElement} HTMLElement
     */
    getHTMLElement : function(editorId, id) {
        return document.getElementById(`${editorId}_${id}`);
    },

    /**
     * Span의 Paragraph Offset을 반환합니다.
     * @param {HTMLElement} span HTML Element
     * @returns {Number} offset
     */
    getHTMLSpanOffset : function(spanElement) {
        let offset = 0;
        let element = spanElement;
        if (this.isAHTMLElement(element.parentElement)) {
            offset += this.getHTMLSpanOffset(element.parentElement);
        }
        
        while (element = element.previousElementSibling) {
            offset += this.getHTMLSpanLength(element);
        }
        return offset;
    },

    /**
     * Span의 Length를 반환합니다.
     * @param {HTMLElement} span HTML Element
     * @returns {Number} length
     */
    getHTMLSpanLength : function (spanElement) {
        let element = spanElement;
        let length;
        switch(true) {
            case this.isEmptyTextNode(element):
                length = 0;
                break;
            case this.isSpanHTMLElement(element):
                length = element.textContent.length;
                break;
            case this.isAHTMLElement(element):
                length = Array.from(element.children).reduce((sum, child) => {
                    return sum + this.getHTMLSpanLength(child);
                }, 0);
                break;
            default:
                length = 1;
                break;
        }

        return length;
    },

    /**
     * x, y 좌표에 해당하는 Paragraph level HTML Element를 반환합니다.
     * @param {HTMLElement} element
     * @param {Number} x
     * @param {Number} y
     * @returns {HTMLElement} Paragarph level HTMLELement
     */
    getParagraphHTMLElementFromPoint : function(element, x, y) {
        if (!element) {
            return null;
        } else if (this.isHrHTMLElement(element)) {
            return element;
        } if (this.isParagraphHTMLElement(element)) {
            const childHTMLElement = element.firstElementChild;
            if(this.isParagraphHTMLElement(childHTMLElement)) {
                return this.getParagraphHTMLElementFromPoint(childHTMLElement, x, y);
            } else {
                return element;
            }
        } else if (this.isSpanHTMLElement(element) ||
                   this.isPictureHTMLElement(element) ||
                   this.isAHTMLElement(element)) {
            return this.getParagraphHTMLElementFromPoint(element.parentElement, x, y);
        } else if (['TBODY', 'TR'].indexOf(element.tagName) > -1) { // table일 경우
            let reviseY = y;
            if (element.tagName === 'TBODY') {
                reviseY = this.getClosestHTMLElementPoint(element, x, y).y;
            }
            let cellHTMLElements = Array.from(element.querySelectorAll('td,th'));
            let cellHTMLElement = cellHTMLElements.find(cell => {
                let {top, bottom, left, right} = cell.getBoundingClientRect();
                return top < reviseY && reviseY <= bottom && left < x && x <= right;
            });

            if (!cellHTMLElement) {
                return $(element).closest('table').get(0);
            }
            return this.getParagraphHTMLElementFromPoint(cellHTMLElement, x, y);
        } else {
            let lastHTMLElement = element.lastElementChild;
            let childHTMLElement = element.firstElementChild;
            while(childHTMLElement !== lastHTMLElement) {
                let nextSibling = childHTMLElement.nextSibling;
                if(y < nextSibling.getBoundingClientRect().top) {
                    break;
                }
                childHTMLElement = nextSibling;
            }
            return this.getParagraphHTMLElementFromPoint(childHTMLElement, x, y);
        }
    },

    /**
     * editor의 se-contents하위에 있는 HTML elementd인지 검사합니다.
     * @param {HTMLElement} element
     * @param {String} id editor id
     * @returns {Boolean}
     */
    isInSEContents : function(element, id) {
        if (!element) {
            return false;
        } else if(typeof element.className === 'string' && element.className.indexOf('se-contents') > -1 && this.isInMyEditor(element, id)) {
            return true;
        } else {
            return this.isInSEContents(element.parentElement, id);
        }
    },

    /**
     * editor의 하위에 있는 HTML elementd인지 검사합니다.
     * @param {HTMLElement} element
     * @param {String} id editor id
     * @returns {Boolean}
     */
    isInMyEditor : function(element, id) {
        if (!element) {
            return false;
        } else if(element.id === id) {
            return true;
        } else {
            return this.isInMyEditor(element.parentElement, id);
        }
    },

    /**
     * element에 유효한 좌표로 변경합니다.
     * @param {HTMLElement} HTMLElement 
     * @param {Number} [x = 0] x좌표
     * @param {Number} [y = 0] y좌표
     * @returns {Object} 넘겨받은 HTML에 포함되는 좌료를 반환합니다. {x:Number, y:Number}
     */
    getClosestHTMLElementPoint: function(HTMLElement, x = 0, y = 0) {
        let {top, bottom, left, right} = HTMLElement.getBoundingClientRect();
        return {
            x: Math.min(Math.max(left + 1, x), right - 1),
            y: Math.min(Math.max(top + 1, y), bottom - 1)
        };
    },

    /**
     * 편집모델의 run level(SPAN, TABLE, IMG등)에 해당하는 HTMLElement를 반환합니다.
     * @param {HTMLElement} HTMLElement 
     * @param {Number} offset
     * @returns {HTMLElement} RunLevel HTMLElement
     */
    getRunLevelHTMLElement: function(element, offset) {
        if (this.isTableHTMLElement(element) || this.isHrHTMLElement(element)) {
            return element;
        }

        let [startOffset, endOffset] = [0, 0];
        let children = Array.from(element.children);
        let child;
        for(child of children) {
            endOffset = startOffset + this.getHTMLSpanLength(child);
            
            // li태그안에 Heading Tag가 렌더링 됨 ex)<li><h1></h1></li>
            if (this.isParagraphHTMLElement(child)) {
                return this.getRunLevelHTMLElement(child, offset);
            }

            if (offset === 0 || (startOffset < offset && offset <= endOffset)) {
                if (this.isAHTMLElement(child)) {
                    return this.getRunLevelHTMLElement(child, offset - startOffset);
                }
                break;
            }
            startOffset = endOffset;
        }
        return child;
    }
};

export default HTMLUtil;


