import consts from '../consts/consts';
import util from './util';

const FILL_TYPE = consts.FILL_TYPE;
const COLOR_TYPE = consts.COLOR_TYPE;
const BORDER_STYLE = consts.BORDER_STYLE;

/**
 * 렌더링에 필요한 Utily
 */
class RenderUtil {
    constructor(themeListManager) {
        this.themeListManager = themeListManager;
    }

    getColorObject(oFill) {
        let oColor;
        let fillType = util.get(oFill, ['type']);
        let themeList = this.themeListManager.getList();

        if (fillType === FILL_TYPE.SOLID) {
            let oSolidFillColor = util.get(oFill, ['solid_fill', 'color']);

            if (oSolidFillColor.type === COLOR_TYPE.SCHEME) {
                let themeColorType = util.get(oSolidFillColor, ['scheme_color', 'theme_color_type']);

                //themeColorType에 background, text가 들어 있는 경우가 있는데, themeList의 값과 key값이 서로 다르다.
                //그래서 background는 lt로 치환하고, text는 dk로 치환한다.
                if (themeColorType.indexOf('background') > -1) {
                    themeColorType = themeColorType.replace('background', 'lt');
                } else if (themeColorType.indexOf('text') > -1) {
                    themeColorType = themeColorType.replace('text', 'dk');
                }

                oColor = util.get(themeList, ['0', 'theme_elements', 'color_scheme', themeColorType]);
            } else {
                oColor = oSolidFillColor;
            }
        }

        return oColor;
    }

    /**
     * color 객체를 받아 hex 단위의 색상 값을 반환합니다.
     * 값이 없으면 undefined를 반환합니다.
     * @param {Object} oColor 
     * @returns {Number | undefined} 
     */
    getColorValue(oColor) {
        let colorType = util.get(oColor, ['type']);

        if (colorType === COLOR_TYPE.SRGB) {
            return util.get(oColor, ['srgb_color', 'value_in_hex']);
        } else if (colorType === COLOR_TYPE.SYSTEM) {
            return util.get(oColor, ['system_color', 'last_color']);
        }
    }

    /**
     * 표의 스타일을 적용하기 위해 TableCell Element를 인자로 받아 css text를 반환합니다.
     * @param {String} direction - 테두리 방향
     * @param {Object} border - TableCell Element의 border 속성
     * @returns {String} - css text
     */
    getBorderCssText(direction, border) {
        if (util.isNil(border)) {
            return '';
        }

        let style = this.getBorderStyle(border);
        let color = this.getBorderColor(border);
        let width = this.getBorderWidth(border);

        return `border-${direction}-style: ${style}; border-${direction}-color: ${color}; border-${direction}-width: ${width}px;`;
    }

    /**
     * 표의 테두리를 렌더링하기 위해 border style을 반환합니다.
     * 'solid', 'dashed', 'dotted' 중 하나가 반환됩니다.
     * @private
     * @param {Object} border - border 속성
     * @returns {String} - border style
     */
    getBorderStyle(border) {
        let style = util.get(border, ['style']);

        if (style === 'dashed' || style === 'dotted' || style === 'outset' || style === 'inset') {
            //Do Nothing
        } else if (style === 'dashSmallGap' || style === 'dotDash' || style === 'dotDotDash') {
            style = BORDER_STYLE.DASHED;
        } else if (style === 'none') {
            style = BORDER_STYLE.NONE;
        } else {
            style = BORDER_STYLE.SOLID;
        }

        return style;
    }

    /**
     * 표의 테두리를 렌더링하기 위해 border width를 px 단위로 반환합니다.
     * @private
     * @param {Object} border - border 속성
     * @returns {Number} - border width
     */
    getBorderWidth(border) {
        let sunit = util.get(border, ['size_in_sunit']);
        return util.sunit2px(sunit);
    }

    /**
     * 표의 테두리를 렌더링하기 위해 border color를 rgb 형태로 반환합니다.
     * @private
     * @param {Object} border - border 속성
     * @returns {String} - border color
     */
    getBorderColor(border) {
        let colorObject = this.getColorObject(util.get(border, ['fill']));
        let colorValue = this.getColorValue(colorObject);
        let rgb;
        if (colorValue) {
            rgb = util.getColorCodeFromDecimal(colorValue);
        }
        return rgb || '#000';
    }
}
export default RenderUtil;


