var vmParser = require('./vmParser');
var vmDescriptor = require('./vmWordDescriptor');

var vmManager = {
    parse: function (byteArr) {
        let serializedBinary = [];

        for (var i = 0; i < byteArr.length; i++) {
            serializedBinary.push(byteArr[i] & 0xFF);
        }

        this.parser = new vmParser(serializedBinary, vmDescriptor);
        this.parser.parse(0, serializedBinary.length);

        return this.parser.jsonPath[0].value;
    }
};

module.exports = vmManager;


