import Element from './Element';
import consts from '../consts/consts';
import util from '../util/util';

/**
 * Paragraph
 * @extends Element
 */
class Paragraph extends Element {
    /**
     * 빈 Paragraph을 생성하여 반환합니다.
     * @param {Obejct} prop
     * @returns {Paragraph} Paragraph
     */
    static createParagraph(prop) {
        return new Paragraph({
            "prop": prop,
            "children":[]
        });
    }

    /**
     * childrenPropety를 반환합니다.
     * @private
     * @returns {String} children
     */
    __getChildrenProperty__() {
        return 'children';
    }

    /**
     * Element의 Type을 반환합니다.
     * @returns {String} Paragraph
     */
    getType() {
        // Type대신 Obejct.consturctor을 사용하면 minify/uglify시 문제가 되어서 만들었습니다.
        return 'Paragraph';
    }

    /**
     * Paragraph Element의 text를 반환합니다.
     * @returns {String}
     */
    getText() {
        return this.getChildren().reduce((text, run) => {
            // image/table등이 있을 경우
            let addText = run.length() > 0 ? ' ':'';
            if (util.isTextRun(run)) {
                addText = run.getText();
            }
            return text + addText;
        }, '');
    }

    /**
     * 나중의 export를 고려하여, SEModel을 docModel 형태로 만들기 위한 함수입니다.<br/>
     * Paragraph Element는 자식노드를 가지고 있기 때문에, 상위 노드인 Element의 toPbModel함수를 호출하여,<br/>
     * 자식노드의 docModel 형태의 JSON Object를 받아서 사용합니다.<br/>
     * 최종적으로 docModel 형태의 paragraph를 만들어서 JSON Object를 반환합니다.
     * @returns {Object} paragraph JSON Object
     */
    toPbModel() {
        let paragraph = {
            "paragraph": super.toPbModel({
                "element_id": this.element_id,
                "prop": this.prop
            }),
            "type": "PARAGRAPH"
        };
        return paragraph;
    }

    /**
     * Paragraph Element의 text 길이를 반환합니다.
     * @returns {Number} length
     */
    length() {
        return this.children.reduce((sum, run) => {
            return sum + run.length();
        }, 0);
    }

    /**
     * Paragraph Element의 properties를 반환합니다.
     * @returns {Object} prop
     */
    getProp() {
        return this.prop;
    }

    /**
     * Paragraph Element의 properties를 얻어와서 numbering_ref_pr prop이 있는지 검사합니다.<br/>
     * numbering_ref_pr properties가 있을경우 True를 반환합니다.
     * @returns {Boolean}
     */
    isLists() {
        let prop = this.getProp();
        return util.has(prop, ['numbering_ref_pr']);
    }

    /**
     * Paragraph Element가 HorizontalLine Paragraph인지 검사하여 맞을 경우 true를 반환합니다.
     * @returns {Boolean}
     */
    isHorizontalLine() {
        return util.isHorizontalLine(this.firstChild());
    }

    /**
     * Paragraph Element가 Table Paragraph인지 검사하여 맞을 경우 true를 반환합니다.
     * @returns {Boolean}
     */
    isTable() {
        return util.isTable(this.firstChild());
    }

    /**
     * Paragraph Element의 numbering_id를 설정합니다.
     * @param {Number} id 
     */
    setListsId(id) {
        let prop = this.getProp();
        util.set(prop, ['numbering_ref_pr', 'numbering_id'], id);
    }

    /**
     * Paragraph Element의 properties를 얻어와서 numbering_id를 반환합니다.<br/>
     * numbering_id이 없을경우 0을 반환합니다.
     * @returns {Number} id
     */
    getListsId() {
        let prop = this.getProp();
        return util.get(prop, ['numbering_ref_pr', 'numbering_id'], 0);
    }

    /**
     * Paragraph Element의 글머리 목록 level을 설정합니다.
     * @param {Number} level 
     */
    setListsLevel(level) {
        let prop = this.getProp();
        util.set(prop, ['numbering_ref_pr', 'level'], level);
    }

    /**
     * Paragraph Element의 properties를 얻어와서 List level을 반환합니다.<br/>
     * List level이 없을경우 -1을 반환합니다.
     * @returns {Number} level
     */
    getListsLevel() {
        let prop = this.getProp();
        return util.get(prop, ['numbering_ref_pr', 'level'], -1);
    }

    /**
     * Paragraph Element의 TextRun모델을 반환합니다.
     * @returns {Array} Array Text Run
     */
    getTextRuns() {
        return this.getChildren().filter(run => util.isTextRun(run));
    }

    /**
     * Paragraph의 첫번째 런을 반환합니다.
     * @return {*}
     */
    getFirstRun() {
        let run = this.firstChild();

        while (!util.isRun(run)) {
            run = run.nextSibling();
        }

        return run;
    }

    /**
     * Paragraph의 마지막 런을 반환합니다.
     * @return {*}
     */
    getLastRun() {
        let run = this.lastChild();

        while (!util.isRun(run)) {
            run = run.previousSibling();
        }

        return run;
    }

    /**
     * 'left', 'right', 'center', 'justify'와 같은 type을 인자로 받아서</br>
     * 모델의 align prop을 셋팅해주는 함수입니다.
     * @param {String} String fontfamily 
     */
    setAlign(type) {
        let prop = this.getProp();
        prop.align = type;
    }

    /**
     * '100', '150' '200'와 같은 percent를 인자로 받아서</br>
     * 모델의 spacing prop을 셋팅해주는 함수입니다.
     * 렌더링을 할 때, sunit 단위로 하고 있기 때문에, percent에 1000을 곱해줍니다.
     * @param {Number} Number percent 
     */
    setLineHeight(percent) {
        let prop = this.getProp();
        prop.spacing = {
            'line_in_percent': percent * 1000,
            'line_rule': 'multiple'
        };
    }

    /**
     * 'p', 'h1', 'h2', 'h3' 등의 문단 스타일을 받아서</br>
     * para의 prop에 SynapEditor만의 문단 속성 'para_style'을 추가합니다.
     * @param {Number} Number percent 
     */
    setParaStyle(type) {
        let prop = this.getProp();
        if(type === 'p') {
            delete prop.para_style;
        } else {
            prop.para_style = type;
        }
    }

    /** 
     * px단위의 value 를 인자르 받아서 모델의 spcaing_in_sunit값을 셋팅해주는 함수 입니다.
     * @param {Number} Number px 
     */
    setLetterSpacing(value) {
        let prop = this.getProp();
        let endRunProp = util.get(prop, ['end_run_prop']);
        endRunProp.spacing_in_sunit = util.px2sunit(value);
    }

    /**
     * Paragraph Model에 Table 모델이 있는지 검사하여 반환합니다.
     * @returns {Boolean}
     */
    hasTable() {
        return this.getChildren().some(run => util.isTable(run));
    }

    /**
     * Paragraph Model에 HorizontalLine 모델이 있는지 검사하여 반환합니다.
     * @returns {Boolean}
     */
    hasHorizontalLine() {
        return this.getChildren().some(run => util.isHorizontalLine(run));
    }

    /**
     * Paragraph이 Model이 비어있는지 검사하여 반환합니다.
     * @returns {Boolean}
     */
    isEmpty() {
        return this.getChildren().every(run => run.length() === 0);
    }

    /**
     * 들여쓰기 레벨을 반환합니다.
     * @returns {Number}
     */
    getIndentLevel() {
        const prop = this.getProp();
        const indent = util.get(prop, ['indent', 'left_in_sunit'], 0);
        const indentStep = consts.INDENT_STEP;
        const level = Math.round(Math.max(indent, 0) / indentStep);
        
        return Math.min(level, 8);
    }

    /**
     * 들여쓰기 값을 설정합니다.
     * @param {Number} sunit 
     */
    setIndent(sunit) {
        const prop = this.getProp();
        util.set(prop, ['indent', 'left_in_sunit'], sunit);
    }
}

export default Paragraph;


