import util from '../util/util';

/**
 * 문서의 외부 리소스들을 관리하는 RelationshipListManager Class입니다.
 * @class RelationshipListManager
 */
class RelationshipListManager {
    /**
     * RelationshipListManager의 인스턴스를 생성합니다.
     */
    constructor() {
        this.list = null;
    }

    /**
     * relationshipList를 설정합니다.
     * @param {Array} list 
     */
    setList(list) {
        this.list = list || [];
    }

    /**
     * relationshipList를 반환합니다.
     * @returns {Array}
     */
    getList() {
        return this.list;
    }

    /**
     * relationship 아이템을 추가합니다.
     * @param {Object} item 
     */
    appendItem(item) {
        this.list.push(item);
    }

    /**
     * relationshipid에 해당하는 아이템을 반환합니다.
     * @param {String} rId 
     * @returns {Object}
     */
    getItem(rId) {
        return this.list.find((item) => {
            return item.id === rId;
        });
    }

    /**
     * relationshipId에 해당하는 이미지 경로를 반환합니다.
     * @param {String} rId 
     * @returns {String}
     */
    getImagePath(rId) {
        let item = this.list.find((item) => {
            return item.id === rId;
        });

        return util.get(item, ['target']);
    }

    /**
     * 새로운 relationship 아이템을 만들어 리스트에 추가하고, 
     * 새로 추가된 relationship 인덱스를 반환합니다.
     * @param {String} url 
     * @param {String} type
     * @param {String} [targetMode='Internal'] 
     * @param {String} method
     * @returns {Number}
     */
    createItem(url, type, targetMode = 'Internal', method) {
        const idx = this.getNewRelationShipIdx();
        const relationshipItem = {
            id: 'rId' + idx,
            target: url,
            target_mode: targetMode,
            type: type
        };

        if (method) {
            relationshipItem['method'] = method;
        }

        this.appendItem(relationshipItem);

        return idx;
    }

    /**
     * 새로운 relactionship 인덱스를 반환합니다.
     * @returns {Number}
     */
    getNewRelationShipIdx() {
        let length = this.list.length;
        return ++length;
    }

    /**
     * RelationShipItem에 uploadPath를 셋팅합니다.
     * @param {Number} rId 
     * @param {String} uploadPath 
     */
    setRelationShipItemPath(relationshipItem, uploadPath) {
        util.set(relationshipItem, ['target'], uploadPath);
    }

    /**
     * RelationShipItem에 method를 셋팅합니다.
     * @param {Object} relationshipItem 
     * @param {String} [method = ''] 
     */
    setRelationShipItemMethod(relationshipItem, method = '') {
        util.set(relationshipItem, ['method'], method);
    }
}

export default RelationshipListManager;


