import $ from 'jquery';
import BodyRenderer from './BodyRenderer';
import ParagraphRenderer from './ParagraphRenderer';
import RunRenderer from './RunRenderer';
import PictureRenderer from './PictureRenderer';
import HorizontalLineRenderer from './HorizontalLineRenderer';
import HyperlinkRenderer from './HyperlinkRenderer';
import TableRenderer from './TableRenderer';
import TableRowRenderer from './TableRowRenderer';
import TableCellRenderer from './TableCellRenderer';
import ListsRenderer from './ListsRenderer';
import util from '../util/util';

/**
 * 편집모델을 렌더링하는 Class입니다.<br/>
 * @example
 * import Renderer from 'file path'
 * const renderer = new Renderer();
 */
class Renderer {
    /**
     * BodyRenderer, ParagraphRenderer, RunRenderer, PictureRenderer를 초기화 하고,
     * 글머리 목록을 렌더링 하기 위해 필요한 인라인 스타일을 생성합니다.
     */
    constructor(editor) {
        this.editor = editor;
        this.editorId = editor.id;
        this.runRenderer = new RunRenderer(this);
        this.pictureRenderer = new PictureRenderer(this);
        this.horizontalLineRenderer = new HorizontalLineRenderer(this);
        this.tableCellRenderer = new TableCellRenderer(this);
        this.tableRowRenderer = new TableRowRenderer(this);
        this.tableRenderer = new TableRenderer(this);
        this.listsRenderer = new ListsRenderer(this);
        this.hyperlinkRenderer = new HyperlinkRenderer(this);
        this.paragraphRenderer = new ParagraphRenderer(this);
        this.bodyRenderer = new BodyRenderer(this);

        this.SEModelManager = editor.getSEModelManager();
        this.modelHandler = editor.getModelHandler();
    }

    /**
     * 상위 에디터 객체를 반환합니다.
     * @returns {Object} - 에디터 객체
     */
    getEditor() {
        return this.editor;
    }

    /**
     * 상위 에디터 객체의 아이디를 반환합니다.
     * @returns {String} - 에디터 객체 아이디
     */
    getEditorId() {
        return util.get(this.editor, ['id']);
    }

    /**
     * 문서 전체를 렌더링합니다.
     */
    renderAll() {
        let bodyModel = this.SEModelManager.getBody();
        this.listsRenderer.generateListsCss();
        $(`#${this.getEditorId()}`).find('.se-contents').html(this.bodyRenderer.getHTML(bodyModel));
    }

    /**
     * paragraph SEModel Id들을 받아 renderParagraph을 호출하여 렌더링합니다. 
     * @param {Array} paragraphIds 
     */
    renderParagraphsByIds(paragraphIds) {
        let paragraphs = paragraphIds.map(id => this.SEModelManager.getParagraph(id));
        this.renderParagraphs(paragraphs);
    }

    /**
     * paragraphs SEModel들을 받아 paragraph을 렌더링합니다.
     * 현재 HTML Element가 있는 Paragraph은 교체합니다.<br>
     * 현재 HTML Element가 없는 Paragraph은 추가합니다.<br>
     * 현재 HTML Element가 SEModel에 없는 Paragraph은 삭제합니다.
     * @param {Array} paragraphs 
     */
    renderParagraphs(paragraphs) {
        const editorId = this.getEditorId();
        
        paragraphs.forEach(paragraph => {
            const paragraphId = paragraph.getId();
            const elementId = `${editorId}_${paragraphId}`;
            const $elParagraph = $('#' + elementId);
            const hyperlinkStart = this.modelHandler.getHyperlinkStart(paragraphId);

            if (hyperlinkStart && !paragraph.contains(hyperlinkStart)) {
                this.paragraphRenderer.setHyperlinkElement(this.hyperlinkRenderer.getHTML(hyperlinkStart));
            }

            if (!this.SEModelManager.getParagraph(paragraphId)) { //모델에 없는 경우 DOM 제거
                $elParagraph.remove();
            } else {
                if ($elParagraph.length === 0) { //DOM에 없는 경우
                    const prevParagraph = paragraph.previousSibling();
                    const prevParagraphId = prevParagraph.getId();
                    const prevElementId = `${editorId}_${prevParagraphId}`;
                    const $prevParagraph = $('#' + prevElementId);

                    if (paragraph.isLists()) {
                        this.__insertListsParagraph__(paragraph, $prevParagraph);
                    } else if (paragraph.isHorizontalLine()) {
                        $prevParagraph.after(this.paragraphRenderer.getHorizonTalLineHTML(paragraph));
                    } else if (paragraph.isTable()) {
                        $prevParagraph.after(this.paragraphRenderer.getTableHTML(paragraph));
                    } else {
                        $prevParagraph.after(this.paragraphRenderer.getHTML(paragraph));
                    }

                    // 새로 엘리먼트가 추가 된 이후에 글머리 목록을 쪼개줘야 한다.
                    if (prevParagraph && prevParagraph.isLists() && prevParagraph.getListsId() !== paragraph.getListsId()) {
                        this.__splitUlElement__(prevParagraph);
                    }

                } else { //DOM에 이미 있는 경우
                    // 엘리먼트 교체 전에 글머리 목록을 미리 쪼개줘야 한다.
                    if (!paragraph.isLists()) {
                        this.__splitUlElement__(paragraph);
                    }

                    if (paragraph.isLists()) {
                        this.__replaceListsParagraph__(paragraph, $elParagraph);
                        this.__mergeUlElement__(paragraph);
                    } else if (paragraph.isHorizontalLine()) {
                        $elParagraph.replaceWith(this.paragraphRenderer.getHorizonTalLineHTML(paragraph));
                    } else if (paragraph.isTable()) {
                        $elParagraph.replaceWith(this.paragraphRenderer.getTableHTML(paragraph));
                    } else {
                        $elParagraph.replaceWith(this.paragraphRenderer.getHTML(paragraph));
                    }
                }
            }

            this.paragraphRenderer.setHyperlinkElement(null);
        });
    }

    /**
     * $targetParagraph 이후에 글머리 문단을 삽입합니다.
     * @private 
     * @param {Paragraph} listsParagraph 
     * @param {jQueryElement} $targetParagraph 
     */
    __insertListsParagraph__(listsParagraph, $targetParagraph) {
        const editorId = this.getEditorId();
        const listsId = listsParagraph.getListsId();

        let $ul = getUlElement(editorId, listsId);
        let listsParagraphs = this.SEModelManager.getParagraphsByListsId(listsId);
        let html = listsParagraphs.map(paragraph => this.paragraphRenderer.getListsHTML(paragraph));

        if ($ul.length === 0) {
            $ul = this.bodyRenderer.createUl(listsId);
            $targetParagraph.after($ul);
        }

        $ul.html(html);
    }

    /**
     * $targetParagraph 글머리 문단으로 교체합니다.
     * @private
     * @param {Paragraph} listsParagraph 
     * @param {jQueryElement} $targetParagraph 
     */
    __replaceListsParagraph__(listsParagraph, $targetParagraph) {
        const editorId = this.getEditorId();
        const listsId = listsParagraph.getListsId();
        const listsHTML = this.paragraphRenderer.getListsHTML(listsParagraph);

        let $ul = getUlElement(editorId, listsId);

        if ($ul.length === 0) { //만약에 ul이 없으면
            $ul = this.bodyRenderer.createUl(listsId);
            $ul.append(listsHTML);
            $targetParagraph.replaceWith($ul);

        } else {
            //문단이 글머리에 속한 문단이 아니었을 경우
            if ($targetParagraph.parent('.se-lists').length === 0) {
                const index = this.SEModelManager.getIndexFromLists(listsParagraph.getListsId(), listsParagraph);

                if (index === 0) {
                    $ul.prepend($targetParagraph);
                } else {
                    $ul.children().eq(index - 1).after($targetParagraph);
                }
            }

            $targetParagraph.replaceWith(listsHTML);
        }
    }

    /**
     * ul tag를 쪼갭니다.
     * 파라미터로 넘어온 문단이 ul에 속한 문단이라면 해당 문단을 기준으로 ul태그를 쪼갭니다.
     * 자세한 설명은 '{@link http://wiki.synap.co.kr/pages/viewpage.action?pageId=32418412|글머리 쪼개는 규칙}'을 참고하세요.
     * @private
     * @param {Paragraph} targetParagraph 
     */
    __splitUlElement__(targetParagraph) {
        const editorId = this.getEditorId();
        const targetParagraphId = targetParagraph.getId();
        const $elTargetParagraph = $(`#${editorId}_${targetParagraphId}`);
        const $ul = $elTargetParagraph.parent('.se-lists');

        if ($ul.length > 0) {
            const children = $ul.children();
            let $beforeElement = $ul;
            let bSplitStart = false;

            for (let i = 0; i < children.length; i++) {
                const $paragraph = $(children.get(i));
                const paragraphId = parseInt($paragraph.attr('para-id'));
                const paragraph = this.SEModelManager.getParagraph(paragraphId);

                if (paragraphId === targetParagraphId) {
                    bSplitStart = true;
                }

                if (bSplitStart) {
                    if (paragraph && paragraph.isLists()) {
                        const listsId = paragraph.getListsId();

                        //listsId에 해당하는 ul이 있는지 확인하여 없으면 새로 만들어 준다.
                        let $newUl = getUlElement(editorId, listsId);
                        if ($newUl.length === 0) {
                            $newUl = this.bodyRenderer.createUl(listsId);
                            $beforeElement.after($newUl);
                            $beforeElement = $newUl;
                        }

                        $beforeElement.append($paragraph);

                    } else {
                        if (i === 0) { //글머리의 첫번째인 경우
                            $beforeElement.before($paragraph);
                        } else {
                            $beforeElement.after($paragraph);
                            $beforeElement = $paragraph;
                        }
                    }
                }
            }
        }

        if ($ul.children().length === 0) {
            $ul.remove();
        }
    }

    /**
     * 해당 문단의 위아래 문단이 글머리 문단이면서, ul 태그를 하나로 합쳐야 한다면 
     * 합치는 작업을 진행합니다.
     * @private
     * @param {Paragraph} paragraph 
     */
    __mergeUlElement__(paragraph) {
        const editorId = this.getEditorId();
        const paragraphId = paragraph.getId();
        const listsId = paragraph.getListsId();
        const $ul = getUlElement(editorId, listsId);
        const $elParagraph = $(`#${editorId}_${paragraphId}`);
        
        this.SEModelManager.getParagraphsByListsId(listsId).forEach(para => {
            const paraId = para.getId();
            const $li = $(`#${editorId}_${paraId}`);
            const $removeUl = $li.parent('.se-lists');
            let bRemove = false;
            
            if ($ul.find($li).length === 0) {
                if ($removeUl.children().length === 1) { //마지막 자식 엘리먼트면 옮긴 다음에 지워준다.
                    bRemove = true;
                }
        
                $ul.append($li);
            }

            if (bRemove) {
                $removeUl.remove();
            }
        });
    }

    /**
     * Picture를 렌더링합니다.
     * @param {Picture} picture 
     */
    renderPicture(picture) {
        if (util.isPicture(picture)) {
            const $elPicture = $(`[pic-id=${picture.getId()}]`);

            if ($elPicture.length > 0) {
                $elPicture.replaceWith(this.pictureRenderer.getHTML(picture));
            }
        }
    }

    /**
     * HTML Element Picture가 랜더링 완료됬는지를 확인합니다.
     * 랜더링이 완료됬다면 모델에 size 값을 셋팅후 랜더링합니다.
     * @param {Picture} picture 
     */
    checkCompleteRenderPicture(picture) {
        const deferred = $.Deferred();
        const elementId = picture.getId();
        let count = 0;

        // TODO setInterval setTimeout 등을 포함한 메소드가 테스트로 돌경우 다른 테스트코드에 영향을 줌
        // setInterval에 관한 테스트 코드 방안이 필요함.
        const intervalId = setInterval(() => {
            const elPicture = document.querySelector(`#${this.editorId} .se-contents img[pic-id='${elementId}']`);
            if (elPicture && elPicture.complete && elPicture.offsetWidth !== 0 && elPicture.offsetHeight !== 0) {
                deferred.resolve(picture, elPicture.offsetWidth, elPicture.offsetHeight);
                clearInterval(intervalId);
            } else if(count === 5) {
                deferred.reject();
                clearInterval(intervalId);
            }
            count++;
        }, 500);

        return deferred.promise();
    }

    /**
     * BodyRenderer 객체를 반환합니다.
     * @returns {BodyRenderer} bodyRenderer
     */
    getBodyRenderer() {
        return this.bodyRenderer;
    }

    /**
     * ParagraphRenderer 객체를 반환합니다.
     * @returns {ParagraphRenderer} paragraphRenderer
     */
    getParagraphRenderer() {
        return this.paragraphRenderer;
    }

    /**
     * RunRenderer 객체를 반환합니다.
     * @returns {RunRenderer} runRenderer
     */
    getRunRenderer() {
        return this.runRenderer;
    }

    /**
     * TableRenderer 객체를 반환합니다.
     * @returns {TableRenderer} tableRenderer
     */
    getTableRenderer() {
        return this.tableRenderer;
    }

    /**
     * TableRowRenderer 객체를 반환합니다.
     * @returns {TableRowRenderer} tableRowRenderer
     */
    getTableRowRenderer() {
        return this.tableRowRenderer;
    }

    /**
     * TableCellRenderer 객체를 반환합니다.
     * @returns {TableCellRenderer} tableCellRenderer
     */
    getTableCellRenderer() {
        return this.tableCellRenderer;
    }

    /**
     * HyperlinkRenderer 객체를 반환합니다.
     * @returns {HyperlinkRenderer} hyperlinkRenderer
     */
    getHyperLinkRenderer() {
        return this.hyperlinkRenderer;
    }

    /**
     * PictureRenderer 객체를 반환합니다.
     * @returns {PictureRenderer} pictureRenderer
     */
    getPictureRenderer() {
        return this.pictureRenderer;
    }

    /**
     * HorizontalLineRenderer 객체를 반환합니다.
     * @returns {HorizontalLineRenderer} horizontalLineRenderer
     */
    getHorizontalLineRenderer() {
        return this.horizontalLineRenderer;
    }

    /**
     * ListsRenderer 객체를 반환합니다.
     * @returns {ListsRenderer} listsRenderer
     */
    getListsRenderer() {
        return this.listsRenderer;
    }
}

export default Renderer;

/**
 * dom에서 ulElement를 가져옵니다.
 * @param {Number} editorId 
 * @param {Number} listsId 
 * @returns {jQueryElement}
 */
function getUlElement(editorId, listsId) {
    return $('#' + editorId).find(`[lists-id=${listsId}]`);
}


