import util from '../../util/util';
import consts from '../../consts/consts';
import CellIndex from '../CellIndex';
import Position from './Position';
import Paragraph from "../Paragraph";

const {POSITION_TYPE} = consts;

/**
 * 편집모델의 selection상태를 관리하는 Class입니다.<br/>
 * selection을 변경하는 API를 제공합니다.
 * @example
 * import Selection form 'file path';
 * const selection = new Selection()
 */
class Selection {
    constructor(positionManager) {
        this.originAnchor = null;
        this.anchor = null;
        this.focus = null;
        this.cellIndex = null;

        this.positionManager = positionManager;
    }

    /**
     * selection 객체를 복제하여 반환합니다.
     * @returns {Selecton} Clone Selection
     */
    clone(){
        let selection = new Selection(this.positionManager);
        selection.set(this.anchor, this.focus);
        return selection;
    }

    /**
     * 인자로 받은 anchor, focus로 Selection을 설정합니다.
     * @param {Position} anchor
     * @param {Position} focus
     */
    set(anchor, focus) {
        let anchorParagraph = this.positionManager.getElement(anchor);
        let focusParagraph = this.positionManager.getElement(focus);
        if (!anchorParagraph || !focusParagraph) { // 유효하지 않은 셀렉션
            return;
        }

        this.originAnchor = this.anchor = anchor.clone();
        this.focus = focus.clone();
        let cca = anchorParagraph.closestCommonAncestorOf(focusParagraph);
        if (util.isTable(cca) || util.isTableRow(cca)) {
            let commonTable = cca.closest(util.isTable);
            this.anchor = this.positionManager.toCellPosition(anchor, commonTable);
            this.focus = this.positionManager.toCellPosition(focus, commonTable);
            this.cellIndex = commonTable.getExtendCellIndex(CellIndex.getCellIndexFromPosition(this.anchor, this.focus));
        } else {
            let iaAnchorPrecedes = this.positionManager.precedes(anchor, focus);
            this.anchor = this.positionManager.toPosition(anchor, cca, iaAnchorPrecedes);
            this.focus = this.positionManager.toPosition(focus, cca, !iaAnchorPrecedes);
            this.cellIndex = null;
        }
    }

    /**
     * selection의 anchor와 focus를 같은 [Position]{@link Position}에 위치시킵니다.
     * @param {Position} position Position instance
     */
    setCaret(position) {
        this.originAnchor = this.anchor = position.clone();
        this.focus = position.clone();
    }

    /**
     * selection의 focus를 변경하여 확장 시킵니다.
     * @param {Position} position 
     */
    extend(position) {
        this.set(this.originAnchor, position);
    }

    /**
     * 문단을 선택한 셀렉션인지 여부를 판단합니다.
     * @returns {Boolean}
     */
    isTextSelection() {
        return [this.anchor, this.focus].every(pos => pos.getType() === POSITION_TYPE.TEXT);
    }

    /**
     * 표를 선택한 셀렉션인지 여부를 판단합니다.
     * @returns {Boolean}
     */
    isCellSelection() {
        return [this.anchor, this.focus].every(pos => pos.getType() === POSITION_TYPE.CELL);
    }

    /**
     * 이미지를 선택한 셀렉션인지 여부를 판단합니다.
     * @returns {Boolean}
     */
    isPictureSelection() {
        return [this.anchor, this.focus].every(pos => pos.getType() === POSITION_TYPE.PICTURE);
    }

    /**
     * 셀렉션이 셀 안쪽에 위치했는지 판단합니다.
     * @returns {Boolean}
     */
    isInCell() {
        let anchorParagraph = this.positionManager.getElement(this.anchor);
        let focusParagraph = this.positionManager.getElement(this.focus);
        if (!anchorParagraph || !focusParagraph) { // 유효하지 않은 셀렉션
            return false;
        }

        let cca = anchorParagraph.closestCommonAncestorOf(focusParagraph);
        return util.isTableCell(cca);
    }

    /**
     * Selection에 포함된 편집모델 Run들을 반환합니다.
     * 옵션 deep이 true일 경우 Table모델 안쪽의 Run들도 모두 가져옵니다.
     * @param {Boolean} [deep = false] table안쪽까지 깊이 탐색할지 여부
     * @returns {Array} Array Run
     */
    getRuns(deep = false) {
        if (this.isCollapsed()) {
            return [];
        } else if (this.isCellSelection()) {
            return this.getParagraphs(deep).reduce((runs, paragraph) => {
                return runs.concat(paragraph.getChildren());
            }, []);
        }

        let runs = [];
        let startRun = this.getStartRun();
        let endRun = this.getEndRun();
        let paragraphs = this.getParagraphs(deep);
        let startPosition = this.getStartPosition();
        let endPosition = this.getEndPosition();

        for (let i = 0; i < paragraphs.length; i++) {
            let paragraph = paragraphs[i];
            let paraId = paragraph.getId();
            let tmpRuns = paragraph.getChildren();

            if (paraId === endPosition.id) {
                let index = tmpRuns.indexOf(endRun);
                tmpRuns = index > -1 ? tmpRuns.slice(0, index + 1) : [];
            }
            if (paraId === startPosition.id) {
                let index = tmpRuns.indexOf(startRun);
                tmpRuns = index > -1 ? tmpRuns.slice(index) : [];
            }
            runs.push(...tmpRuns);
        }

        return runs;
    }

    /**
     * startPosition 정보에 해당하는 편집모델 Run Element를 반환합니다.
     * @returns {Element} Run Element
     */
    getStartRun() {
        if (this.isCollapsed()) {
            return null;
        }

        let position = this.getStartPosition();
        let element = this.positionManager.getElement(position);

        if (position.offset === element.length()) {
            return element.nextSibling().firstChild();
        }

        let endIndex = 0;
        let run = element.getChildren().find(child => {
            endIndex += child.length();
            return (position.offset === 0 || position.offset < endIndex);
        });

        return run;
    }

    /**
     * endPosition 정보에 해당하는 편집모델 Run Element를 반환합니다.
     * @returns {Element} Run Element
     */
    getEndRun() {
        if (this.isCollapsed()) {
            return null;
        }

        let position = this.getEndPosition();
        let element = this.positionManager.getElement(position);

        if (position.offset === 0) {
            return element.previousSibling().lastChild();
        }

        let endIndex = 0;
        let run = element.getChildren().find(child => {
            endIndex += child.length();
            return position.offset <= endIndex;
        });

        return run;
    }

    /**
     * Selection에 포함된 편집모델 Text Run들을 반환합니다.
     * 옵션 deep이 true일 경우 Table모델 안쪽의 Paragraph들도 모두 가져옵니다.
     * @param {Boolean} [deep = false] table안쪽까지 깊이 탐색할지 여부
     * @returns {Array} Array Text Run
     */
    getTextRuns(deep = false) {
        return this.getRuns(deep).filter(run => util.isTextRun(run));
    }

    /**
     * Selection에 포함된 편집모델의 Text를 반환합니다.
     * 옵션 deep이 true일 경우 Table모델 안쪽의 Paragraph들의 text들도 모두 가져옵니다.
     * @param {Booelan} [deep = false] table안쪽까지 깊이 탐색할지 여부
     * @returns {String} Text Run의 Text
     */
    getText(deep = false) {
        const startPosition = this.getStartPosition();
        const endPosition = this.getEndPosition();
        const textRuns = this.getTextRuns(deep);
        const textStartOffset = textRuns.length !== 0 ? startPosition.offset - textRuns[0].getRunStartOffset() : 0;
        const textEndOffset = textRuns.length !== 0 ? endPosition.offset - textRuns[textRuns.length - 1].getRunEndOffset() : 0;
        let text = textRuns.reduce((text, run) => {
            return text += run.text;
        }, '');
        
        return textStartOffset === textEndOffset ? text : text.slice(textStartOffset, textEndOffset);
    }

    /**
     * Selection에 포함된 편집모델 Paragraph들을 반환합니다.<br>
     * 옵션 deep이 true일 경우 Table모델 안쪽의 Paragraph들도 모두 가져옵니다.
     * @param {Boolean} [deep = false] table안쪽까지 깊이 탐색할지 여부
     * @returns {Array} paragraphs
     */
    getParagraphs(deep = false) {
        let paragraphs = [];
        let startParagraph, endParagraph;
        let startPosition = this.getStartPosition();
        let endPosition = this.getEndPosition();

        if (this.isCellSelection()) {
            paragraphs = this.getCells().reduce((paras, cell) => {
                return paras.concat(cell.getChildren());
            }, []);
        } else {
            startParagraph = this.positionManager.getElement(startPosition);
            endParagraph = this.positionManager.getElement(endPosition);
            let cca = startParagraph.closestCommonAncestorOf(endParagraph);
            paragraphs = cca.getChildren().slice(startParagraph.index(), endParagraph.index() + 1);
        }
        
        if (deep) {
            let deepParagraphs = [];
            for(let paragraph of paragraphs) {
                if(paragraph.isTable()) {
                    if ((startParagraph === paragraph && startPosition.offset > 0) ||
                        (endParagraph === paragraph && endPosition.offset < 1)) {
                        continue;
                    }
                    let table = paragraph.firstChild();
                    deepParagraphs.push(...table.getParagraphs());
                }
                deepParagraphs.push(paragraph);
            }
            return deepParagraphs;
        } else {
            return paragraphs;
        }
    }

    /**
     * Selection에 포함된 편집모델 Paragraph들의 id를 반환합니다.<br>
     * 옵션 deep이 true일 경우 Table모델 안쪽의 Paragraph들도 모두 가져옵니다.
     * @param {Boolean} [deep = false] table안쪽까지 깊이 탐색할지 여부
     * @returns {Array} ids
     */
    getParagraphIds(deep = false) {
        let paras = this.getParagraphs(deep);
        let ids = [];
        paras.forEach(para => ids.push(para.getId()));
        return ids;
    }

    /**
     * 셀렉션에 포함된 Element를 반환합니다. <br/>
     * collapse 상태는 아무것도 반환하지 않으며, 현재 table일 경우 표 전체를 반환합니다. <br/>
     * paragraph은 셀렉션의 offset을 이용하여 run을 쪼개어 반환합니다. 
     * @returns {Array} Elements
     */
    getSelectedElements() {
        if (this.isCollapsed()) {
            return;
        } else if (this.isCellSelection()) { //현재는 무조건 표 전체를 반환함.
            const tableId = this.anchor.tableId;
            return [this.getTable(tableId)];
        } else if (this.isPictureSelection()) { // TODO body의 getElement를 사용하도록 바꿔야함.
            const picture =  this.positionManager.getElement(new Position(this.anchor.pictureId)).clone(true);
            delete picture.element_id;
            return [picture];
        }

        let endPosition = this.getEndPosition();
        let startPosition = this.getStartPosition();
        let endRun = this.positionManager.getRun(endPosition);
        let startRun = this.positionManager.getRun(startPosition);

        return this.getParagraphs(true).map(paragraph => {
            let clone = paragraph.clone(true);
            if (clone.getId() === endPosition.id) {
                let endIndex = endRun.index();
                util.splitRun(clone.nthChild(endIndex), endPosition.offset - endRun.getRunStartOffset());
                clone.removeChild(endIndex + 1, clone.lastChild().index() + 1);
            }
            if (clone.getId() === startPosition.id) {
                let startIndex = startRun.index();
                let [run, splitedRun] = util.splitRun(clone.nthChild(startIndex), startPosition.offset - startRun.getRunStartOffset());
                startIndex += (!splitedRun ? 0 : 1);
                clone.removeChild(0, Math.max(startIndex, 0));
            }
            delete clone.element_id;
            return clone;
        });
    }


    /**
     * elements Table, Picture 등의 SEModel만을 가지고 있을 때, elements를 Paragraph SEModel을 만들어 자식으로 붙이고 반환합니다.
     * @returns {Array} Paragraphs
     */
    getCopiedParagraphs() {
        let elements = this.getSelectedElements();
        if (!elements) { // 붙여넣기할 모델이 없을경우 빈배열을 줌.
            return [];
        }
        let firstElement = elements[0];
        const type = firstElement.getType(); // elements는 Table, Picture등의 객체를 제외하곤 Paragraph에 담겨서 가져오기때문에 첫번째만 검사.
        switch (type) {
            case 'Table':
            case 'Picture':
                const paragraph = Paragraph.createParagraph();
                paragraph.append(firstElement);
                elements[0] = paragraph;
                break;
        }
        return elements;
    }

    /**
     * 영역을 포함하고있는 Table을 반환합니다.</br>
     * CellSelection상태일때 사용합니다.
     * @returns {Table}
     */
    getTable() {
        let cells = this.getCells();
        if (cells.length > 0) {
            return cells[0].getTable();
        } else {
            return null;
        }
    }

    /**
     * 영역에 포함된 셀들을 반환합니다.</br>
     * cellSelection상태일때 사용합니다.
     * @param {Boolean} [containMergedCell = false]
     * @returns {Array} [cell, cell, ...] cell Array
     */
    getCells(containMergedCell = false) {
        if(!this.isCellSelection()) {
            let cells = [];
            let anchorParagraph = this.positionManager.getElement(this.anchor);
            let focusParagraph = this.positionManager.getElement(this.focus);
            if (!anchorParagraph || !focusParagraph) { // 유효하지 않은 셀렉션
                return cells;
            }
    
            let cca = anchorParagraph.closestCommonAncestorOf(focusParagraph);
            let cell = cca.closest(util.isTableCell);
            if (cell) {
                cells.push(cell);
            }
            return cells;
        }

        const table = this.positionManager.getElement(new Position(this.anchor.tableId));
        return table.getCells(this.cellIndex, containMergedCell);
    }

    /**
     * cell selection에서 좌상단 cell을 반환합니다.
     * @returns {TableCell}
     */
    getLeftTopCell() {
        const table = this.positionManager.getElement(new Position(this.anchor.tableId));
        return table.getCell(this.cellIndex.getStartCellIndex());
    }

    /**
     * cell selection에서 우하단 cell을 반환합니다.
     * @returns {TableCell}
     */
    getRightBottomCell() {
        const table = this.positionManager.getElement(new Position(this.anchor.tableId));
        return table.getCell(this.cellIndex.getEndCellIndex());
    }

    /**
     * cell selection에서 focus cell을 반환합니다.
     * @returns {TableCell}
     */
    getFocusCell() {
        const table = this.positionManager.getElement(new Position(this.focus.tableId));
        return table.getCell(this.focus.getCellIndex());
    }

    /**
     * Selection의 anchor, focus가 같은지 검사합니다.<br>
     * Caret이 깜빡이는 상태입니다.
     * @returns {Boolean} collapsed 상태
     */
    isCollapsed() {
        return this.isTextSelection() && this.anchor.isEqual(this.focus);
    }

    /**
     * Selection에서 시작 Position 정보를 반환합니다.
     * @returns {Position} Start Position
     */
    getStartPosition() {
        return this.positionManager.precedes(this.anchor, this.focus) ? this.anchor.clone() : this.focus.clone();
    }

    /**
     * Selection에서 끝 Position 정보를 반환합니다.
     * @returns {Position} End Position
     */
    getEndPosition() {
        return this.positionManager.follows(this.focus, this.anchor) ? this.focus.clone() : this.anchor.clone();
    }

    /**
     * 현재 Selection에 Position이 포함되는지를 검사합니다.
     * @param {Position} position
     * @returns {Booelan}
     */
    isContain(position) {
        const startPosition = this.getStartPosition();
        const endPosition = this.getEndPosition();

        return (position.isEqual(startPosition) || position.isEqual(endPosition)) ||
        (this.positionManager.follows(position, startPosition) && this.positionManager.precedes(position ,endPosition));
    }
}

export default Selection;


